<!-- 百科管理 -->
<template>
	<div class="right-content encyclopedia">
		<el-tabs v-model="activeName" class="content"  @tab-click="changeTab">
			<el-tab-pane label="百科管理" name="first">
				<div class="flex" style="flex-wrap: wrap; justify-content: space-between;">
					<div>
						<!-- 批量删除 -->
						<el-button @click="deleteEncyclopedias()" type="primary" size="medium" plain class="deletes">批量删除</el-button>
						<!-- 置顶、审核、推荐 -->
						<el-dropdown trigger="click" @command="changeStatus">
						  	<el-button type="primary" size="medium" class="change-status">
						    	更改状态<i class="el-icon-arrow-down el-icon--right"></i>
						  	</el-button>
						  	<el-dropdown-menu slot="dropdown">
						    	<el-dropdown-item command="status, 1" type="primary" size="medium" plain class="status">审核</el-dropdown-item>
								<el-dropdown-item command="top, 1" type="primary" size="medium" plain class="status">置顶</el-dropdown-item>
								<el-dropdown-item command="recommended, 1" type="primary" size="medium" plain class="status">推荐</el-dropdown-item>

								<el-dropdown-item command="status, 0" type="primary" size="medium" plain class="status">取消审核</el-dropdown-item>
								<el-dropdown-item command="top, 0" type="primary" size="medium" plain class="status">取消置顶</el-dropdown-item>
								<el-dropdown-item command="recommended, 0" type="primary" size="medium" plain class="status">取消推荐</el-dropdown-item>
						  </el-dropdown-menu>
						</el-dropdown>
					</div>
					<div class="search">
						<span style="margin-left: 15px;">分类：</span>
					 	<el-select v-model="selectedOptions" placeholder="请选择" clearable>
    						<el-option v-for="item in encyclopediaCategoryList" :key="item.id" :label="item.name" :value="item.id"></el-option>
  						</el-select>
						<span style="margin-left: 15px;">日期：</span>
						<el-date-picker v-model="date" type="daterange" align="center" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
						<span style="margin-left: 15px;">关键词：</span>
						<el-input v-model="keyword" placeholder="请输入内容" clearable></el-input>
						<el-button @click="searchEncyclopedia()" type="primary" size="medium" icon="el-icon-search" class="search-btn">搜索</el-button>
					</div>
				</div>

				<el-table :data="encyclopediaList" style="width: 100%" :border='true' v-loading="loading" @selection-change="selectionChange">
					<el-table-column type="selection" width="40"></el-table-column>
					<el-table-column label="ID" prop="id" width="80"></el-table-column>
				 	<el-table-column label="标题">
				 		<template slot-scope="scope">
	    					<a :href="url + 'encyclopedia/' + encyclopediaList[scope.$index].id" target="_blank" :title="encyclopediaList[scope.$index].en_title">{{encyclopediaList[scope.$index].en_title}}</a>
	    				</template>
				 	</el-table-column>
					<el-table-column label="分类" prop="category_name" width="130"></el-table-column>
				 	<el-table-column label="作者" prop="author" width="80"></el-table-column>
				 	<el-table-column label="发布时间" prop="published_time" width="170"></el-table-column>
				 	<el-table-column label="状态" prop="post_status" width="200">
				 		<template slot-scope="scope">
				 			<span>{{encyclopediaList[scope.$index].status == 1 ? '已审核' : '未审核'}}</span>
				 			<span style="margin: 0 15px;">{{encyclopediaList[scope.$index].is_top == 1 ? '已置顶' : '未置顶'}}</span>
				 			<span>{{encyclopediaList[scope.$index].recommended == 1 ? '已推荐' : '未推荐'}}</span>
				 		</template>
				 	</el-table-column>
				 	<el-table-column label="操作" width="100">
		  				<template slot-scope="scope">
							<el-button type="text" size="small" @click="editEncyclopedia(scope.row)">编辑</el-button>
	    					<el-button type="text" size="small" @click="deleteEncyclopedia(scope.row)">删除</el-button>
		  				</template>
					</el-table-column>
				</el-table>

				<el-pagination layout="prev, pager, next" :total="total" class="page" :page-size="pageSize" @current-change="currentChange" :current-page.sync="currentPage" v-if="total != 0"></el-pagination>
			</el-tab-pane>

			<el-tab-pane label="添加百科" name="second">
				<encyclopediaEditAndAdd v-if="add" v-on:change="activeChange($event)"></encyclopediaEditAndAdd>
			</el-tab-pane>

			<el-tab-pane label="编辑百科" name="third" v-if="edit">
				<encyclopediaEditAndAdd v-on:change="activeChange($event)"></encyclopediaEditAndAdd>
			</el-tab-pane>
		</el-tabs>
	</div>
</template>

<script>
	import apis from '../../apis'
	import encyclopediaEditAndAdd from '@/views/encyclopedia/encyclopedia-EditAndAdd'

	export default {
		inject: ['reload', 'reloadTab'],
		components: {
			encyclopediaEditAndAdd
		},
		data() {
			return {
				activeName: 'first',
				url: 'http://www.cdddkqyy.com/',
				loading: true,
				encyclopediaList: [],
				encyclopediaCategoryList: [],
				encyclopediaArr: [],
				selectedOptions: '',
				date: '',
				keyword: '',
				total: 0,
				pageSize: 0,
				page: 1,
				add: false,
				edit: false,
				search: false,
				currentPage: 1,
			}
		},
		methods: {
			//改变头部选项卡
			changeTab(tab, event) {
				tab.index == 0 ? (this.add = false) | (this.edit = false) : '';
				tab.index == 1 ? (this.edit = false) | (this.$root.propList = this.encyclopediaCategoryList) | (this.add = true) | (this.$root.propType = 'add') : '';
			},
			// page改变
			currentChange(page) {
				this.loading = true;
				var startTime, endTime, keyword, select;
				if(this.search) {
					this.selectedOptions != '' ? select = this.selectedOptions : '';
					this.date != '' ? (startTime = this.date[0]) | (endTime = this.date[1]) : '';
					this.keyword != '' ? keyword = this.keyword : '';
				}
            	this.page = page;
            	apis.encyclopediaList({page: this.page, start_time: startTime, end_time: endTime, keyword: keyword, category: select, token: this.$root.token}, function(data) {
					data ? this.loading = false : '';					
					if(data.code == 0) {
						this.$alert(data.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				            	location.reload();
				          	}
				        });
					}
	            	else {
	            		this.encyclopediaList = data.data;
	            		this.total = data.total;
						this.pageSize = data.per_page;
	            	}
				}.bind(this))
			},
		    // 接收子组件 传回来的值 改变头部选项卡
		    activeChange(name) {
		    	this.activeName = name;
		    	this.add = false;
		    	this.edit = false;
		    },
		    // 删除
		    deleteEncyclopedia(row) {
		    	apis.encyclopediaDelete({id: row.id, token: this.$root.token}, function(bck) {
		    		this.$alert(bck.info, {
		    			confirmButtonText: '确定',
			          	callback: action => {
			            	if(bck.info == '您还没有登录！！') {
			            		location.reload();
			            	}
			            	else if(bck.code == 1) {
			            		this.reload();
			            	}
			          	}
		    		})
		    	}.bind(this))
		    },
		    // 多选操作
	      	selectionChange(val) {
		        this.encyclopediaArr = val;
	      	},
	      	// 批量删除
	      	deleteEncyclopedias() {
	      		if(this.encyclopediaArr.length != 0) {
	      			var ids = [];
		      		for(var i = 0; i < this.encyclopediaArr.length; i ++) {
		      			ids.push(this.encyclopediaArr[i].id);
		      		}
		      		apis.encyclopediaDelete({ids: ids, token: this.$root.token}, function(bck) {
		      			this.$options.methods.alertMsg(this, bck.info, bck.code);
		      		}.bind(this))
	      		}
	      		else {
	      			this.$alert('请勾选要操作的文章');
	      		}
	      	},
	      	// 操作审核，置顶，推荐
	      	changeStatus(status) {
	      		var text = status.split(',');
	      		var statusType = '', topType = '', recommendedType = '', ids = [], self = this;

	      		text[0] == 'status' ? statusType = text[1] : text[0] == 'top' ? topType = text[1] : text[0] == 'recommended' ? recommendedType = text[1] : '';
	      		
	      		if(this.encyclopediaArr.length != 0) {
		      		for(var i = 0; i < this.encyclopediaArr.length; i ++) {
		      			ids.push(this.encyclopediaArr[i].id);
		      		}
	      			apis.encyclopediaStatus({ids: ids, status: statusType, top: topType, recommended: recommendedType, token: self.$root.token}, function(bck) {
	      				self.$options.methods.alertMsg(self, bck.info, bck.code);
	      			})
	      		}
	      		else {
	      			this.$alert('请勾选要操作的文章');
	      		}
	      	},
		    // 搜索
		    searchEncyclopedia() {
	      		this.date === null ? this.date = '' : '';
	      		this.loading = true;
	      		apis.encyclopediaList({category: this.selectedOptions, start_time: this.date[0], end_time: this.date[1], keyword: this.keyword, token: this.$root.token}, function(data) {
	      			data ? this.loading = false : '';
	      			if(data.code == 0) {
						this.$alert(data.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				            	location.reload();
				          	}
				        });
					}
					else {
						this.encyclopediaList = data.data;
						this.total = data.total;
						this.pageSize = data.per_page;
			    		if(this.date !== null || this.keyword != '' || this.selectedOptions != '') {
							this.search = true;
						}
						else {
							this.search = false;
						}
						this.currentPage = 1;
					}
	      		}.bind(this))
		    },
		    // 编辑
		    editEncyclopedia(row) {
		    	this.edit = true;
		    	this.activeName = 'third';
		    	this.$root.propType = 'edit';
				this.$root.propList = this.encyclopediaCategoryList;
				this.$root.editEncyclopedia = row;
		    },
	      	// 消息提示框，公共部分
	      	alertMsg: function(_this, info, code) {
		    	_this.$alert(info, {
	         	 	confirmButtonText: '确定',
		          	callback: action => {
		            	if(code != 0) {
							var startTime, endTime, keyword, select;
							if(_this.search) {
								_this.selectedOptions != '' ? select = _this.selectedOptions : '';
								_this.date != '' ? (startTime = _this.date[0]) | (endTime = _this.date[1]) : '';
								_this.keyword != '' ? keyword = _this.keyword : '';
							}
		            		// 操作成功后重新请求列表
		            		apis.encyclopediaList({page: _this.page, start_time: startTime, end_time: endTime, keyword: keyword, category: select, token: _this.$root.token}, function(data) {
								_this.encyclopediaList = data.data;
								_this.total = data.total;
								_this.pageSize = data.per_page;
							}.bind(_this))
		            	}
		            	else if(info == '您还没有登录！！') {
		            		location.reload();
		            	}
		          	}
		        });
		    }
		},
		created() {
			apis.encyclopediaList({token: this.$root.token}, function(data) {
				data ? this.loading = false : '';

				if(data.code == 0) {
					this.$alert(data.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	location.reload();
			          	}
			        });
				}
				else {
					this.encyclopediaList = data.data;
					this.total = data.total;
					this.pageSize = data.per_page;
				}
			}.bind(this))

			apis.encyclopediaCategoryList({token: this.$root.token}, function(data) {
				this.encyclopediaCategoryList = data;
			}.bind(this))
		}
	}
</script>

<style lang="less">
	.encyclopedia {
		.search {
			margin-bottom: 15px;
			.el-input {
				width: auto;
			}
			.search-btn {
				margin-left: 15px;
			}
		}
		.search {
			font-size: 15px;
		}
		.change-status {
			margin-left: 10px;
		}
		.page {
			text-align: center;
			margin-top: 15px;
		}
		a {
			white-space: nowrap;
		    color: #409EFF;
		    text-decoration: none;
		}
	}
</style>


