<!-- 问答管理 -->
<template>
	<div class="right-content question">
		<el-tabs v-model="activeName" @tab-click="changeTab" class="content">
			<el-tab-pane label="问答管理" name="first">
				<div class="flex" style="flex-wrap: wrap; justify-content: space-between;">
					<div>
						<!-- 批量删除 -->
						<el-button @click="deleteQuestions()" type="primary" size="medium" plain class="deletes">批量删除</el-button>
						<!-- 置顶、审核、推荐 -->
						<el-dropdown trigger="click" @command="changeStatus">
						  	<el-button type="primary" size="medium" class="change-status">
						    	更改状态<i class="el-icon-arrow-down el-icon--right"></i>
						  	</el-button>
						  	<el-dropdown-menu slot="dropdown">
						    	<el-dropdown-item command="status, 1" type="primary" size="medium" plain class="status">审核</el-dropdown-item>
								<el-dropdown-item command="top, 1" type="primary" size="medium" plain class="status">置顶</el-dropdown-item>
								<el-dropdown-item command="recommended, 1" type="primary" size="medium" plain class="status">推荐</el-dropdown-item>

								<el-dropdown-item command="status, 0" type="primary" size="medium" plain class="status">取消审核</el-dropdown-item>
								<el-dropdown-item command="top, 0" type="primary" size="medium" plain class="status">取消置顶</el-dropdown-item>
								<el-dropdown-item command="recommended, 0" type="primary" size="medium" plain class="status">取消推荐</el-dropdown-item>
						  </el-dropdown-menu>
						</el-dropdown>
					</div>
					<div class="search">
						<span style="margin-left: 15px;">分类：</span>
						<el-cascader expand-trigger="click" :options="questionCategoryList" v-model="selectedOptions" change-on-select clearable></el-cascader>
						<span style="margin-left: 15px;">日期：</span>
						<el-date-picker v-model="date" type="daterange" align="center" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
						<span style="margin-left: 15px;">关键词：</span>
						<el-input v-model="keyword" placeholder="请输入内容" clearable></el-input>
						<el-button @click="searchQuestion()" type="primary" size="medium" icon="el-icon-search" class="search-btn">搜索</el-button>
					</div>
				</div>

				<el-table :data="questionList" style="width: 100%" :border='true' v-loading="loading" @selection-change="selectionChange">
					<el-table-column type="selection" width="40"></el-table-column>
					<el-table-column label="标题">
						<template slot-scope="scope">
	    					<a :href="url + 'qa/' + questionList[scope.$index].id" target="_blank">{{questionList[scope.$index].question_title}}</a>
	    				</template>
					</el-table-column>
					<el-table-column label="发布时间" prop="published_time" width="170"></el-table-column>
					<el-table-column label="分类" prop="category_name" width="150"></el-table-column>
					<el-table-column label="提问者" prop="author" width="100"></el-table-column>
					<el-table-column label="状态" width="200">
						<template slot-scope="scope">
				 			<span>{{questionList[scope.$index].status == 1 ? '已审核' : '未审核'}}</span>
				 			<span style="margin: 0 15px;">{{questionList[scope.$index].is_top == 1 ? '已置顶' : '未置顶'}}</span>
				 			<span>{{questionList[scope.$index].recommended == 1 ? '已推荐' : '未推荐'}}</span>
				 		</template>
					</el-table-column>
					<el-table-column label="操作" width="170">
	      				<template slot-scope="scope">
	        				<el-button type="text" size="small" @click="answerQuestion(scope.row)">医生解答</el-button>
	        				<el-button type="text" size="small" @click="editQuestion(scope.row)">编辑</el-button>
	    					<el-button type="text" size="small" @click="deleteQuestion(scope.row)">删除</el-button>
	      				</template>
	    			</el-table-column>
				</el-table>
				<el-pagination layout="prev, pager, next" :total="total" class="page" :page-size="pageSize" @current-change="currentChange" :current-page.sync="currentPage" v-if="total != 0"></el-pagination>
			</el-tab-pane>

			<el-tab-pane label="添加问题" name="second">
				<div class="add-question">
		    		<span class="title">日期</span>
		    		<el-date-picker v-model="publishDate" type="datetime" placeholder="选择发布日期时间" align="center"></el-date-picker>
					<span class="required">*</span>
		    	</div>
				<div class="add-question">
					<span class="title">栏目</span>
					<el-cascader expand-trigger="click" :options="questionCategoryList" v-model="selectedOptions2" change-on-select></el-cascader>
		    		<span class="required">*</span>
				</div>
				<div class="add-question">
					<span class="title">问题</span>
					<el-input v-model="addQuestionTitle" placeholder="请输入问题标题"></el-input>
		    		<span class="required">*</span>
				</div>
				<div class="add-question">
					<span class="title" style="vertical-align: top;">描述</span>
					<el-input v-model="addQuestionExcerpt" placeholder="请输入问题描述" type="textarea" :autosize="{ minRows: 2}"></el-input>
		    		<span class="required">*</span>
				</div>
				<div class="add-question">
					<span class="title" style="vertical-align: top;">帮助</span>
					<el-input v-model="addQuestionHelp" type="textarea" :autosize="{ minRows: 2}"></el-input>
				</div>
				<div class="add-question">
					<span class="title" style="vertical-align: top;">性别</span>
				 	<el-radio v-model="addQuestionerSex" label="1">男</el-radio>
  					<el-radio v-model="addQuestionerSex" label="2">女</el-radio>
				</div>
				<div class="add-question">
					<span class="title">年龄</span>
				 	<el-input v-model="addQuestionerAge" placeholder="请输入年龄"></el-input>
				</div>
				<div class="add-question">
		    		<el-button type="primary" @click="addQuestion()">添加</el-button>
					<el-button @click="goBack()">返回</el-button>
		    	</div>
			</el-tab-pane>
			
			<!-- 解答问题 -->
			<el-tab-pane label="医生解答" name="third" v-if="answer">
				<answer v-on:change="activeChange($event)"></answer>
			</el-tab-pane>
		</el-tabs>

		<!-- 问题编辑 -->
		<el-dialog title="编辑问题" :visible.sync="dialogEditVisible" width="40%">
			<div class="edit-question">
				<span class="title">栏目</span>
				<el-cascader expand-trigger="click" :options="questionCategoryList" v-model="selectedOptions2" change-on-select></el-cascader>
			</div>
		  	<div class="edit-question">
				<span class="title">问题</span>
				<el-input v-model="editQuestionTitle" placeholder="请输入问题标题"></el-input>
				<span class="required">*</span>
			</div>
			<div class="edit-question">
				<span class="title" style="vertical-align: top;">描述</span>
				<el-input v-model="editQuestionExcerpt" placeholder="请输入问题描述" type="textarea" :autosize="{ minRows: 2}"></el-input>
			</div>
			<div class="edit-question">
				<span class="title" style="vertical-align: top;">帮助</span>
				<el-input v-model="editQuestionHelp" type="textarea" :autosize="{ minRows: 2}"></el-input>
			</div>
			<div class="edit-question">
				<span class="title" style="vertical-align: top;">性别</span>
			 	<el-radio v-model="editQuestionerSex" label="1">男</el-radio>
				<el-radio v-model="editQuestionerSex" label="2">女</el-radio>
			</div>
			<div class="edit-question">
				<span class="title">年龄</span>
			 	<el-input v-model="editQuestionerAge" placeholder="请输入年龄"></el-input>
			</div>
		  	<span slot="footer" class="dialog-footer">
		    	<el-button @click="dialogEditVisible = false">取 消</el-button>
		    	<el-button type="primary" @click="editSubmit()">确 定</el-button>
		  	</span>
		</el-dialog>
	</div>
</template>

<script>
	import apis from '../../apis'
	import E from 'wangeditor'
	import answer from '@/views/answer/answer'

	export default {
		inject: ['reload', 'reloadTab'],
		components: {
			answer
		},
		data() {
			return {
				activeName: 'first',
				loading: true,
				questionList: [],
				questionCategoryList: [],
				url: 'http://www.cdddkqyy.com/',
				selectedOptions: [],
				selectedOptions2: [],
				total: 0,
				pageSize: 0,
				page: 1,
				date: '',
				keyword: '',
				questionArr: [],
				addQuestionTitle: '',
				addQuestionExcerpt: '',
				addQuestionHelp: '',
				addQuestionerAge: '',
				addQuestionerSex: '1',
				publishDate: '',
				dialogEditVisible: false,
				editQuestionTitle: '',
				editQuestionExcerpt: '',
				editQuestionHelp: '',
				editQuestionerSex: '',
				editQuestionerAge: '',
				questionId: Number,
				answer: false,
				search: false,
				currentPage: 1,
			}
		},
		methods: {
			//改变头部选项卡
			changeTab(tab, event) {
				if(tab.index == 1) {
					this.addQuestionTitle = '';
					this.addQuestionExcerpt = '';
					this.addQuestionHelp = '';
					this.addQuestionerAge = '';
					this.selectedOptions2 = [];
					this.publishDate = new Date();
				}
				tab.index !=2 ? this.answer = false : '';
			},
		    // 多选操作
	      	selectionChange(val) {
		        this.questionArr = val;
	      	},
	      	// 批量删除
	      	deleteQuestions() {
	      		if(this.questionArr.length != 0) {
	      			var ids = [];
		      		for(var i = 0; i < this.questionArr.length; i ++) {
		      			ids.push(this.questionArr[i].id);
		      		}
		      		apis.questionDelete({ids: ids, token: this.$root.token}, function(bck) {
		      			this.$options.methods.alertMsg(this, bck.info, bck.code, 'ctr');
		      		}.bind(this))
	      		}
	      		else {
	      			this.$alert('请勾选要操作的文章');
	      		}
	      	},
	      	// 删除
	      	deleteQuestion(row) {
	      		apis.questionDelete({id: row.id, token: this.$root.token}, function(bck) {
	      			this.$options.methods.alertMsg(this, bck.info, bck.code, 'ctr');
	      		}.bind(this))
	      	},
	      	// 操作审核，置顶，推荐
	      	changeStatus(status) {
	      		var text = status.split(',');
	      		var statusType = '', topType = '', recommendedType = '', ids = [], self = this;

	      		text[0] == 'status' ? statusType = text[1] : text[0] == 'top' ? topType = text[1] : text[0] == 'recommended' ? recommendedType = text[1] : '';
	      		
	      		if(this.questionArr.length != 0) {
		      		for(var i = 0; i < this.questionArr.length; i ++) {
		      			ids.push(this.questionArr[i].id);
		      		}
	      			apis.questionStatus({ids: ids, status: statusType, top: topType, recommended: recommendedType, token: this.$root.token}, function(bck) {
	      				self.$options.methods.alertMsg(self, bck.info, bck.code, 'ctr');
	      			})
	      		}
	      		else {
	      			this.$alert('请勾选要操作的文章');
	      		}
	      	},
	      	// 搜索
	      	searchQuestion() {
	      		var select = '';
	      		this.selectedOptions != '' ? select = this.selectedOptions[this.selectedOptions.length - 1] : '';
	      		this.date === null ? this.date = '' : '';
	      		this.loading = true;
	      		apis.questionList({category: select, start_time: this.date[0], end_time: this.date[1], keyword: this.keyword, token: this.$root.token}, function(data) {
	      			data ? this.loading = false : '';
	      			if(data.code == 0) {
						this.$alert(data.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				            	location.reload();
				          	}
				        });
					}
					else {
						this.questionList = data.data;
						this.total = data.total;
						this.pageSize = data.per_page;
			    		if(this.date !== null || this.keyword != '' || this.selectedOptions != '') {
							this.search = true;
						}
						else {
							this.search = false;
						}
						this.currentPage = 1;
					}
	      		}.bind(this))
	      	},
            // page改变
            currentChange(page) {
            	this.loading = true;
            	var keyword, startTime, endTime, select;
            	if(this.search) {
            		this.selectedOptions != '' ? select = this.selectedOptions[this.selectedOptions.length - 1] : '';
	            	this.keyword != '' ? keyword = this.keyword : '';
	            	this.date != '' ? (startTime = this.date[0]) | (endTime = this.date[1]) : '';
            	}
            	this.page = page;
            	apis.questionList({category: select, page: page, start_time: startTime, end_time: endTime, keyword: keyword, token: this.$root.token}, function(data) {
					data ? this.loading = false : '';
					if(data.code == 0) {
						this.$alert(data.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				            	location.reload();
				          	}
				        });
					}
					else {
						this.questionList = data.data;
					}
				}.bind(this))
            },
            // 添加
            addQuestion() {
            	apis.questionAdd({question_title: this.addQuestionTitle, category_id: this.selectedOptions2[this.selectedOptions2.length - 1], question_excerpt: this.addQuestionExcerpt, question_help: this.addQuestionHelp, questioner_gender: this.addQuestionerSex, questioner_age: this.addQuestionerAge, published_time: this.publishDate, token: this.$root.token}, function(bck) {
            		this.$options.methods.alertMsg(this, bck.info, bck.code);
            	}.bind(this))
            },
            // 返回
            goBack() {
            	this.activeName = 'first';
            },
            // 编辑
            editQuestion(row) {
            	this.dialogEditVisible = true;
            	this.editQuestionTitle = row.question_title;
				this.editQuestionExcerpt = row.question_excerpt;
				this.editQuestionHelp = row.question_help;
				this.editQuestionerSex = String(row.questioner_gender);
				this.editQuestionerAge = row.questioner_age;
				this.questionId = row.id;
            },
            // 提交编辑
            editSubmit() {
            	apis.questionEdit({id: this.questionId, question_title: this.editQuestionTitle, category_id: this.selectedOptions2[this.selectedOptions2.length - 1], question_excerpt: this.editQuestionExcerpt, question_help: this.editQuestionHelp, questioner_gender: this.editQuestionerSex, questioner_age: this.editQuestionerAge, token: this.$root.token}, function(bck) {
            		this.$options.methods.alertMsg(this, bck.info, bck.code);
            	}.bind(this))
            },
            // 解答疑问
            answerQuestion(row) {
            	this.answer = true;
            	this.activeName = 'third';

            	this.$root.question = row;
            },
		    // 接收子组件 传回来的值 改变头部选项卡
		    activeChange(name) {
		    	this.activeName = name;
		    	this.answer = false;
		    },
	      	// 消息提示框，公共部分
	      	alertMsg: function(_this, info, code, ctr) {
		    	if(ctr) {
		    		_this.$alert(info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	if(code != 0) {
				            	var keyword, startTime, endTime, select;
				            	if(_this.search) {
				            		_this.selectedOptions != '' ? select = _this.selectedOptions[_this.selectedOptions.length - 1] : '';
					            	_this.keyword != '' ? keyword = _this.keyword : '';
					            	_this.date != '' ? (startTime = _this.date[0]) | (endTime = _this.date[1]) : '';
				            	}
			            		// 操作成功后重新请求列表
			            		apis.questionList({category: select, page: _this.page, start_time: startTime, end_time: endTime, keyword: keyword, token: _this.$root.token}, function(data) {
									_this.questionList = data.data;
									_this.total = data.total;
									_this.pageSize = data.per_page;
								}.bind(_this))
			            	}
			            	else if(info == '您还没有登录！！') {
			            		location.reload();
			            	}
			          	}
			        });
		    	}
		    	else {
		    		_this.$alert(info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			          		if(info == '您还没有登录！！') {
			            		location.reload();
			          			_this.$router.push('/');
							}
							else if(code == 1) {
								_this.reload();
							}
			          	}
			        });
		    	}
		    },
		},
		created() {
			apis.questionList({token: this.$root.token}, function(data) {
				data ? this.loading = false : '';

				if(data.code == 0) {
					this.$alert(data.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	location.reload();
			          	}
			        });
				}
				else {
					this.questionList = data.data;
					this.total = data.total;
					this.pageSize = data.per_page;
				}
			}.bind(this))

			apis.questionCategoryListTree({token: this.$root.token}, function(data) {
				this.questionCategoryList = data;
			}.bind(this))

		},
	}
</script>

<style lang="less">
	.question {
		a {
			white-space: nowrap;
		    color: #409EFF;
		    text-decoration: none;
		}
		.change-list-order {
			margin-bottom: 15px;
		}
		.change-status {
			margin-left: 10px;
		}
		.search {
			margin-bottom: 15px;
			font-size: 15px;
			.el-input {
				width: auto;
			}
			.search-btn {
				margin-left: 15px;
			}
		}
		.page {
			text-align: center;
			margin-top: 15px;
		}
		.add-question, .edit-question {
		    font-size: 15px;
		    padding-left: 5%;
		    margin-top: 20px;
		    .title {
		    	margin-right: 15px;
		    }
		    .el-input, .el-textarea {
		    	width: 217px;
		    }
		}
	}
</style>


