<!-- url美化 -->
<template>
	<div class="right-content url-route">
		<el-tabs v-model="activeName" class="content" @tab-click="changeTab">

		    <el-tab-pane label="URL美化" name="first" class="backstage-menu">
				<el-table :data="urlList" style="width: 100%" :border='true' v-loading="loading">
	    			<el-table-column label="ID" prop="id" width="80"></el-table-column>
	    			<el-table-column label="原始网址" prop="full_url"></el-table-column>
				 	<el-table-column label="显示网址" prop="url" width="200"></el-table-column>
	    			<el-table-column label="状态" width="80">
	    				<template slot-scope="scope">
	    					<div>{{urlList[scope.$index].status == 1 ? '已启用' : '已禁用'}}</div>
	    				</template>
	    			</el-table-column>
				 	<el-table-column label="操作" width="130">
	      				<template slot-scope="scope">
	        				<el-button type="text" size="small" @click="edit(scope.row)">修改</el-button>
	    					<el-button type="text" size="small" @click="changeStatus(scope.row)">{{urlList[scope.$index].status == 1 ? '禁用' : '启用'}}</el-button>
	    					<el-button type="text" size="small" @click="deleteUrl(scope.row)">删除</el-button>
	      				</template>
	    			</el-table-column>
	  			</el-table>
		    </el-tab-pane>
			
		    <el-tab-pane label="添加URL规则" name="second">
				<div class="add-url-rule">
					<span class="title">原始网址</span>
					<el-input v-model="fullUrl" placeholder="请输入美化前网址"></el-input>
		    		<span class="required">*</span>
				</div>
				<div class="add-url-rule">
					<span class="title">显示网址</span>
					<el-input v-model="url" placeholder="请输入美化后网址"></el-input>
		    		<span class="required">*</span>
				</div>
				<div class="add-url-rule">
					<span class="title">是否启用</span>
					<el-select v-model="urlStatu" placeholder="请选择">
		    			<el-option v-for="item in status" :key="item.value" :label="item.label" :value="item.value"></el-option>
		  			</el-select>
		    		<span class="required">*</span>
				</div>
				<div class="add-url-rule">
		    		<el-button type="primary" @click="addUrlRule()">添加</el-button>
		    		<el-button @click="goBack()">返回</el-button>
		    	</div>
		    </el-tab-pane>
  		</el-tabs>
		
		<!-- 修改url规则 -->
  		<el-dialog title="修改URL规则" :visible.sync="dialogVisible" width="30%">
		  	<div class="edit-url-rule">
		  		<span class="title">原始网址</span>
		  		<el-input v-model="editFullUrl"></el-input>
		    	<span class="required">*</span>
		    </div>
			<div class="edit-url-rule">
				<span class="title">显示网址</span>
				<el-input v-model="editUrl"></el-input>
	    		<span class="required">*</span>
    		</div>
			<div class="edit-url-rule">
				<span class="title" style="margin-right: 11px;">是否启用</span>
				<el-select v-model="editUrlStatu" placeholder="请选择">
	    			<el-option v-for="item in status" :key="item.value" :label="item.label" :value="item.value"></el-option>
	  			</el-select>
			</div>
		  	<span slot="footer" class="dialog-footer">
		    	<el-button @click="dialogVisible = false">取 消</el-button>
		    	<el-button type="primary" @click="editSubmit()">确 定</el-button>
		  	</span>
		</el-dialog>
	</div>
</template>

<script>
	import apis from '../../apis'

	export default {
		inject: ['reload'],
		data() {
			return {
				activeName: 'first',
				urlList: [],
				loading: true,
        		status: [{value: 1, label: '启用'},{value: 0, label: '禁用'}],
        		urlStatu: '',
        		fullUrl: '',
        		url: '',
        		dialogVisible: false,
        		editFullUrl: '',
        		editUrl: '',
        		editId: '',
        		editUrlStatu: ''
			}
		},
		methods: {
			//改变头部选项卡
	      	changeTab(tab, event) {
	        	if(tab.name == "first") {
	        		this.fullUrl = '';
					this.url = '';
	        	}
	      	},
			// 启用 or 禁用
			changeStatus(row) {
				if(row.status == 0) {
					apis.urlOpen({id: row.id, token: this.$root.token}, function(bck) {
						this.$options.methods.alert(this, bck.code, bck.info);
					}.bind(this))
				}
				else if(row.status == 1) {
					apis.urlBan({id: row.id, token: this.$root.token}, function(bck) {
						this.$options.methods.alert(this, bck.code, bck.info);
					}.bind(this))
				}
			},
			// 添加url规则
			addUrlRule() {
				apis.urlAdd({full_url: this.fullUrl, url: this.url, status: this.urlStatu, token: this.$root.token}, function(bck) {
					this.$options.methods.alert(this, bck.code, bck.info);
				}.bind(this))
			},
			// 删除url规则
			deleteUrl(row) {
				apis.urlDelete({id: row.id, token: this.$root.token}, function(bck) {
			        this.$options.methods.alert(this, bck.code, bck.info);
				}.bind(this))
			},
			// 修改url规则
			edit(row) {
				this.dialogVisible = true;
				this.editFullUrl = row.full_url;
				this.editUrl = row.url;
				this.editUrlStatu = row.status;
				this.editId = row.id;
			},
			// 修改提交
			editSubmit() {
				apis.urlEdit({id: this.editId, full_url: this.editFullUrl, url: this.editUrl, status: this.editUrlStatu, token: this.$root.token}, function(bck) {
					bck.info == '修改失败' ? (bck.code = 1) | (bck.info = '修改成功') : '';
					this.$options.methods.alert(this, bck.code, bck.info);
				}.bind(this))
			},
			// 返回
			goBack() {
				this.activeName = "first";
				this.fullUrl = '';
				this.url = '';
			},
			alert: function(_this, code, info) {
				_this.$alert(info, {
	         	 	confirmButtonText: '确定',
		          	callback: action => {
		            	if(code == 1) {
		            		_this.reload();
		            	}
		            	else if(info == '您还没有登录！！') {
		            		location.reload();
		            	}
		          	}
		        });
			}
		},
		created() {
			apis.urlRoute({token: this.$root.token}, function(data) {
				data ? this.loading = false : '';
				if(data.code == 0) {
					this.$alert(data.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	location.reload();
			          	}
			        });
				}
				else {
					this.urlList = data;
				}
			}.bind(this))
		}
	}
</script>

<style lang="less">
	.url-route {
		.el-input {
			width: 217px;
		}
		.add-url-rule, .edit-url-rule {
		    font-size: 15px;
		    padding-left: 5%;
		    margin-top: 20px;
		    .title {
		    	margin-right: 15px;
		    }
		}
		.edit-url-rule {
			margin-top: 0;
		}
		.edit-url-rule:not(:last-child) {
			margin-bottom: 20px;
		}
	}
</style>


