+<!-- 网页导航菜单 -->
<template>
	<div class="right-content">
  		<el-tabs v-model="activeName" @tab-click="changeTab" class="content web-nav">

		    <el-tab-pane label="网页导航" name="first">

		    	<!-- 导航列表选择框 -->
				<el-select v-model="navListId" @change="webNavSelect" class="web-nav-select">
				    <el-option v-for="item in navClassList" :key="item.id" :label="item.name" :value="item.id"></el-option>
				</el-select>

				<!-- 修改按钮 -->
				<el-button @click="changeListOrder()" type="primary" plain class="change-list-order">修改显示顺序</el-button>

				<el-table :data="webNavList" style="width: 100%" :border='true' v-loading="loading">
					<el-table-column label="显示顺序" width="100">
	    				<template slot-scope="scope">
	    					<el-input v-model="listOrder[scope.$index]" type="number"></el-input>
	    				</template>
	    			</el-table-column>
	    			<el-table-column label="ID" prop="id" width="50"></el-table-column>
      				<el-table-column label="导航名称">
      					<template slot-scope="scope">
      						<div v-if="webNavList[scope.$index]._level == 1">{{webNavList[scope.$index].label}}</div>
      						<div v-else-if="webNavList[scope.$index]._level == 2"><span style="margin: 0 15px 0 30px;">├─</span>{{webNavList[scope.$index].label}}</div>
      						<div v-else-if="webNavList[scope.$index]._level == 3"><span style="margin: 0 15px 0 60px;">├─</span>{{webNavList[scope.$index].label}}</div>
      						<div v-else-if="webNavList[scope.$index]._level == 4"><span style="margin: 0 15px 0 90px;">├─</span>{{webNavList[scope.$index].label}}</div>
      					</template>
      				</el-table-column>
      				<el-table-column label="导航链接" prop="href" width="270"></el-table-column>
      				<el-table-column label="显示状态" width="80">
	    				<template slot-scope="scope">
	    					<div>{{webNavList[scope.$index].status == 1 ? '显示' : '隐藏'}}</div>
	    				</template>
	    			</el-table-column>
	    			<el-table-column label="操作" width="200">
	      				<template slot-scope="scope">
	        				<el-button type="text" size="small" @click="addChildrenNav(scope.row)">添加子导航</el-button>
	        				<el-button type="text" size="small" @click="goToEdit(scope.row)">编辑</el-button>
	    					<el-button type="text" size="small" @click="deleteNav(scope.row)">删除</el-button>
	      				</template>
	    			</el-table-column>

    			</el-table>
		    </el-tab-pane>
			
		    <el-tab-pane label="添加导航" name="second">
		    	<!-- 添加与编辑共用组件 -->
		    	<!-- 利用v-if机制，每次打开添加菜单选项卡都会重新渲染，达到reset效果 -->
				<webNavEditAndAdd v-if="navListAdd" v-on:change = "activeChange($event)"></webNavEditAndAdd>
		    </el-tab-pane>

		    <el-tab-pane label="编辑导航" name="third" v-if="navListEdit">
		    	<!-- 添加与编辑共用组件 -->
				<webNavEditAndAdd v-on:change = "activeChange($event)"></webNavEditAndAdd>
		    </el-tab-pane>
			
			<!-- 网页导航弹窗 -->
		  	<el-dialog title="添加子导航" :visible.sync="dialogWebNavVisible" class="navDialog" width="40%" @close='closeDialog'>
		  		<div style="padding-left: 10%;">
		  			<p><span class="title">导航上级：</span><b>{{webNavParent}}</b></p>
					<p>
						<span class="title">导航名称</span>
						<el-input placeholder="请输入导航名字" v-model="navName" clearable></el-input>
		    			<span class="required">*</span>
					</p>
					<p>
						<span class="title">导航链接</span>
						<el-input placeholder="如果是一级菜单不用填" v-model="navHref" clearable></el-input>
					</p>
					<p>
						<span class="title">导航状态</span>
						<el-select v-model="navStatu" placeholder="请选择">
			    			<el-option v-for="item in status" :key="item.value" :label="item.label" :value="item.value"></el-option>
			  			</el-select>
		    			<span class="required">*</span>
					</p>
		  		</div>
				<span slot="footer" class="dialog-footer">
			    	<el-button @click="closeDialog()">取 消</el-button>
				    <el-button type="primary" @click="submitNavChildrenAdd()">确 定</el-button>
				</span>
			</el-dialog>
			
			<el-tab-pane label="导航分类" name="fourth">
				<el-table :data="navClassList" style="width: 100%" :border='true'>
	    			<el-table-column label="ID" prop="id" width="50"></el-table-column>
      				<el-table-column label="名称" prop="name"></el-table-column>
      				<el-table-column label="备注" prop="remark" width="250"></el-table-column>
	    			<el-table-column label="操作" width="130">
	      				<template slot-scope="scope">
	        				<el-button type="text" size="small" @click="goToEditNavClass(scope.row)">编辑</el-button>
	    					<el-button type="text" size="small" @click="deleteNavClass(scope.row)">删除</el-button>
	      				</template>
	    			</el-table-column>
    			</el-table>
		    </el-tab-pane>

		    <el-tab-pane label="添加分类" name="firth">
				<div class="nav-class-name">
					<span class="title">分类名称</span>
					<el-input v-model="addNavClassName" placeholder="请输入分类名称"></el-input>
		    		<span class="required">*</span>
				</div>
				<div class="nav-class-remark">
					<span style="vertical-align: top;" class="title">分类备注</span>
					<el-input type="textarea" :rows="3" v-model="addNavClassRemark" clearable></el-input>
				</div>
				<div class="nav-class-btn">
		    		<el-button type="primary" @click="submitNavAdd()">添加</el-button>
		    		<el-button @click="goBack()">返回</el-button>
		    	</div>
		    </el-tab-pane>

		    <!-- 导航分类弹窗 -->
		  	<el-dialog title="编辑导航分类" :visible.sync="dialogNavClassVisible" class="navDialog" width="40%">
		  		<div style="padding-left: 10%;">
		  			<p>
		  				<span class="title">分类名称</span>
		  				<el-input v-model="editNavClassName" clearable></el-input>
		    			<span class="required">*</span>
		  			</p>
					<p>
						<span style="vertical-align: top;" class="title">分类备注</span>
						<el-input type="textarea" :rows="3" v-model="editNavClassRemark" clearable></el-input>
					</p>
		  		</div>
		  		<span slot="footer" class="dialog-footer">
			    	<el-button @click="closeNavClassDialog()">取 消</el-button>
				    <el-button type="primary" @click="submitNavClassEdit()">确 定</el-button>
				</span>
			</el-dialog>
  		</el-tabs>
  	</div>
</template>

<script>
	import apis from '../../apis'
	import webNavEditAndAdd from '@/views/web-nav/web-nav-EditAndAdd'

	export default {
		inject: ['reload'],
		components: {
			webNavEditAndAdd
		},
		data() {
			return {
				activeName: 'first',
				navClassList: [],
				navListId: '',
				webNavList: [],
        		listOrder: [],
        		navId: [],
        		loading: true,
        		navListAdd: false,
        		navListEdit: false,
        		dialogWebNavVisible: false,
        		dialogNavClassVisible: false,
        		webNavParent: '',
        		navName: '',
        		navHref: '',
        		navStatu: '',
        		navParentId: '',
                status: [{value: 1, label: '显示'},{value: 0, label: '隐藏'}],
                updateNavListOrderArr: [],
                navClassId: '',
                editNavClassName: '',
                editNavClassRemark: '',
                cloneListOrder: [],
                originalListOrderArr: [],
                addNavClassName: '',
                addNavClassRemark: ''
			}
		},
		methods: {
			//改变头部选项卡
			changeTab(tab, event) {
				this.$root.navClassList = this.navClassList;
				this.navListAdd = true;

				tab.index == '1' ? (this.$root.propMenuType = 'add') | (this.$root.navClassList = this.navClassList) | (this.$root.catId = this.navListId) : this.navListAdd = false;
				tab.index != '3' ? (this.addNavClassName = '') | (this.addNavClassRemark = '') : '';
				tab.index != '4' ? this.navListEdit = false : '';
			},
			// 导航选择
			webNavSelect() {
				this.loading = true;
				apis.navClassInx({id: this.navListId, token: this.$root.token}, function(data) {
					if(data.code == 0) {
						this.$alert(data.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				            	location.reload();
				          	}
				        });
					}
					else {
						this.webNavList = data;
						data ? this.loading = false : '';
						this.listOrder=[];
						this.navId=[];
					for(var i = 0; i < data.length; i ++) {
						this.navId.push(data[i].id);
	    				this.listOrder.push(data[i].list_order);
	    		}
						
					}
				}.bind(this))
			},
			// 接收子组件web-nav-edit传回来的值 改变头部选项卡
		    activeChange(name) {
		    	this.activeName = name[0];
		    	this.navListAdd = false;
		    	this.navListEdit = false;
		    	// 如果值为1（修改或添加成功）重新渲染表格
		    	name[1] == 1 ? this.reload() : '';
		    },
		    // 删除菜单
		    deleteNav(row) {
		    	this.$confirm('确认要删除【'+ row.label +'】吗？', '确认信息', {
		          	distinguishCancelAndClose: true,
		          	confirmButtonText: '确定',
		          	cancelButtonText: '取消'
		        }).then(() => {
		            // 点确定后
		            apis.webNavDelete({id: row.id, token: this.$root.token}, function(bck) {
			    		// 调用methods的方法
		    			this.$options.methods.alertMsg(this, bck.info, bck.code);
			    	}.bind(this))
	          	}).catch(action => {});
		    },
		    // 添加子导航
		    addChildrenNav(row) {
		    	this.dialogWebNavVisible = true;
		    	this.webNavParent = row.label;
		    	this.navParentId = row.id;
		    },
		    // 关闭弹窗并reset
		    closeDialog(){
		      	this.navName = '';
        		this.navHref = '';
        		this.navStatu = '';
        		this.navHref = '';
        		this.dialogWebNavVisible = false;
		    },
		    // 提交子菜单添加
		    submitNavChildrenAdd() {
		    	this.navName == '' ? this.$alert('请输入导航名称', {confirmButtonText: '确定'}) : this.navStatu === '' ? this.$alert('请选择菜单状态', {confirmButtonText: '确定'}) : this.canSubmit = true;
                this.navHref == '' ? this.navHref = '/' : '';

		    	if(this.canSubmit) {
		    		apis.webNavAdd({cat_id: this.navListId, parent_id: this.navParentId, href: this.navHref, name: this.navName, status: this.navStatu, target: '', token: this.$root.token}, function(bck) {
			    		// 调用methods的方法
			    		this.$options.methods.alertMsg(this, bck.info, bck.code);
			    	}.bind(this))
		    	}
		    },
		    // 跳转编辑页
		    goToEdit(row) {
				this.$root.navClassList = this.navClassList;
		    	this.$root.propMenuType = 'edit';
		    	this.$root.navEdit = row;
		    	this.navListEdit = true;
		    	this.activeName = 'third';
		    },
		    // 修改显示顺序
		    changeListOrder() {
	    		for(var i = 0; i < this.navId.length; i++) {
					this.updateNavListOrderArr.push({id: JSON.parse(JSON.stringify(this.navId))[i], list_order: JSON.parse(JSON.stringify(this.listOrder))[i]})
					this.originalListOrderArr.push({id: JSON.parse(JSON.stringify(this.navId))[i], list_order: JSON.parse(JSON.stringify(this.cloneListOrder))[i]})
				}
				
				apis.webNavListOrder({list_order: this.updateNavListOrderArr, original_list_order: this.originalListOrderArr, token: this.$root.token}, function(bck) {
					// 调用methods的方法
					this.$options.methods.alertMsg(this, bck.info, bck.code);
				}.bind(this))
		    },
		    // 编辑导航分类
		    goToEditNavClass(row) {
		    	this.dialogNavClassVisible = true;
		    	this.navClassId = row.id;
		    	this.editNavClassName = row.name;
		    	this.editNavClassRemark = row.remark;
		    },
		    // 删除导航分类
		    deleteNavClass(row) {
		    	apis.navClassDelete({id: row.id, token: this.$root.token}, function(bck) {
		    		if(bck.code == 1) {
		    			this.$alert(bck.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				            	// 发起请求更新数据，重新渲染
				            	apis.navClass({token: this.$root.token}, function(data) {this.navClassList = data;}.bind(this));
				          	}
				        });
		    		}
		    		else {
		    			this.$alert(bck.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				          		if(bck.info == '您还没有登录！！'){
				          			location.reload();
				          		}
				          	}
				        });
		    		}
		    	}.bind(this))
		    },
		    // 关闭导航分类的编辑弹窗
		    closeNavClassDialog() {
		    	this.dialogNavClassVisible = false;
		    },
		    // 提交导航分类的编辑
		    submitNavClassEdit() {
		    	apis.navClassEdit({id: this.navClassId, name: this.editNavClassName, remark: this.editNavClassRemark, token: this.$root.token}, function(bck) {
		    		if(bck.code == 1) {
		    			this.$alert('修改成功', {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				            	this.dialogNavClassVisible = false;
				            	// 发起请求更新数据，重新渲染
				            	apis.navClass({token: this.$root.token}, function(data) {this.navClassList = data;}.bind(this));
				          	}
				        });
		    		}
		    		else {
		    			if(bck.info == '您还没有登录！！') {
		    				this.$alert(bck.info, {
				         	 	confirmButtonText: '确定',
					          	callback: action => {
				          			location.reload();
					          	}
					        });
		    			}
		    			else {
		    				this.$alert('修改成功', {confirmButtonText: '确定'});
		    			}
		    		}
		    	}.bind(this))
		    },
		    // 提交添加内容
		    submitNavAdd() {
		    	apis.navClassAdd({name: this.addNavClassName, remark: this.addNavClassRemark, token: this.$root.token}, function(bck) {
		    		if(bck.code == 1) {
		    			this.$alert(bck.info, {
                            confirmButtonText: '确定',
                            callback: action => {
                                this.activeName = 'fourth';
				            	// 发起请求更新数据，重新渲染
				            	apis.navClass({token: this.$root.token}, function(data) {
				            		this.navClassList = data;}.bind(this));
                            }
                        });
		    		}
		    		else {
		    			this.$alert(bck.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				          		if(bck.info == '您还没有登录！！'){
				          			location.reload();
				          		}
				          	}
				        });
		    		}
		    	}.bind(this))
		    },
		    // 返回
            goBack() {
                this.addNavClassName = '';
                this.addNavClassRemark = '';
                this.activeName = 'fourth';
            },
		    alertMsg: function(_this, msg, code) {
		    	_this.$alert(msg, {
	         	 	confirmButtonText: '确定',
		          	callback: action => {
		            	if(msg == '您还没有登录！！'){
		          			location.reload();
		          		}
		          		else if(code == 1) {
		          			_this.reload();
		          		}
		          	}
		        });
		    }
		},
		created() {
			apis.navClass({token: this.$root.token}, function(data) {
				data ? this.loading = false : '';
				if(data.code == 0) {
					this.$alert(data.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	location.reload();
			          	}
			        });
				}
				else {
					this.navClassList = data;
					this.navListId = data[0].id;
				}
			}.bind(this))

			// 默认显示第一个导航分类里的网页导航列表
			apis.navClassInx({id: 1, token: this.$root.token}, function(data) {
				this.webNavList = data;

				// 把导航列表的id和list-order添加进数组里，用作修改排序
				for(var i = 0; i < data.length; i ++) {
	    			this.navId.push(data[i].id);
	    			this.listOrder.push(data[i].list_order);
	    		}
	    		Object.assign(this.cloneListOrder, this.listOrder);

			}.bind(this))

		}
	}
</script>

<style lang="less">
		.web-nav {
			.change-list-order {
				margin-left: 15px;
			}
			.web-nav-select {
			    margin-bottom: 15px;
			}
			.el-table__expanded-cell {
				padding: 0;
			}
			.web-nav-children {
				.el-table__header-wrapper {
					display: none;
				}
			}
			.navDialog {
				.el-input {
					width: auto;
				}
				.el-textarea {
					width: 217px;
				}
			}
			.nav-class-name, .nav-class-remark {
        		font-size: 15px;
		        padding-left: 5%;
		        margin-top: 20px;
				.el-input {
					width: auto;
				}
				.el-textarea {
					width: 202px;
				}
			}
			.nav-class-btn {
				margin-top: 20px;
        		font-size: 15px;
		        padding-left: 5%;
		        margin-top: 20px;
			}
			.title {
				margin-right: 15px;
			}
		}
</style>


