<!-- 预约（小程序） -->
<template>
	<div class="right-content register-littleProgram">
		<div class="content">
			<div class="search">
				<el-button class="export-data" @click="exportExcel()">导出数据</el-button>
				<span style="margin-left: 15px;">日期：</span>
				<el-date-picker v-model="date" type="daterange" align="center" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
				<span style="margin-left: 15px;">关键词：</span>
				<el-input v-model="keyword" placeholder="请输入内容" clearable></el-input>
				<el-button @click="searchArticle()" type="primary" size="medium" icon="el-icon-search" class="search-btn">搜索</el-button>
			</div>

			<el-table :data="registerList" style="width: 100%" :border='true' v-loading="loading" id="out-table">
				<el-table-column label="ID" prop="id" width="60"></el-table-column>
				<el-table-column label="姓名" prop="appointer" width="100"></el-table-column>
				<el-table-column label="电话" prop="phone"></el-table-column>
				<el-table-column label="预约科室" prop="department"></el-table-column>
				<el-table-column label="预约医生" prop="doctor"></el-table-column>
				<el-table-column label="预约时间" width="210">
					<template slot-scope="scope">
						<div>{{registerList[scope.$index].appointment + '（' + registerList[scope.$index].time + '）'}}</div>
					</template>
				</el-table-column>
				<el-table-column label="备注" prop="remark" width="200"></el-table-column>
				<el-table-column label="操作" width="100">
      				<template slot-scope="scope">
    					<el-button type="text" size="small" @click="deleteData(scope.row)">删除</el-button>
      				</template>
    			</el-table-column>
			</el-table>

			<el-pagination layout="prev, pager, next" :total="total" class="page" :page-size="pageSize" @current-change="currentChange"></el-pagination>
		</div>
	</div>
</template>

<script>
	import apis from '../../apis'
 	import FileSaver from 'file-saver'
    import XLSX from 'xlsx'

	export default {
		inject: ['reload', 'reloadTab'],
		data() {
			return {
				loading: true,
				registerList: [],
				date: '',
				keyword: '',
				total: 0,
				pageSize: 0,
				page: 1,
			}
		},
		methods: {
			// 删除
			deleteData(row) {
				apis.registerDelete({id: row.id, token: this.$root.token}, function(bck) {
					this.$alert(bck.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	if(bck.code != 0) {
			            		this.reload();
			            	}
			            	else if(bck.info == '您还没有登录！！') {
			            		location.reload();
			            	}
			          	}
			        });
				}.bind(this))
			},
            // page改变
            currentChange(page) {
            	this.loading = true;
            	this.page = page;
            	apis.registerList({page: page, token: this.$root.token}, function(data) {
					data ? this.loading = false : '';
					if(data.code == 0) {
						this.$alert(data.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				            	location.reload();
				          	}
				        });
					}
					else {
						this.registerList = data.data;
						this.total = data.total;
						this.pageSize = data.per_page;
					}
				}.bind(this))
            },
	      	// 搜索
	      	searchArticle() {
	      		this.date === null ? this.date = '' : '';
	      		this.loading = true;
	      		apis.registerList({start_time: this.date[0], end_time: this.date[1], keyword: this.keyword, token: this.$root.token}, function(data) {
	      			data ? this.loading = false : '';
	      			if(data.code == 0) {
						this.$alert(data.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				            	location.reload();
				          	}
				        });
					}
					else {
						this.registerList = data.data;
						this.total = data.total;
						this.pageSize = data.per_page;
					}
	      		}.bind(this))
	      	},
		    // 导出
		    exportExcel() {
	         	if(this.registerList.length == 0) {
	         		this.$alert('没有数据');
	         		return;
	         	}
	         	var wb = XLSX.utils.table_to_book(document.querySelector('#out-table'))
		        
	         	var wbout = XLSX.write(wb, { bookType: 'xlsx', bookSST: true, type: 'array' })
	         	try {
	             	FileSaver.saveAs(new Blob([wbout], { type: 'application/octet-stream' }), 'data.xlsx')
	         	} 
	         	catch(e) { if (typeof console !== 'undefined') console.log(e, wbout) }
	         	return wbout
		    },
		},
		created() {
			apis.registerList({token: this.$root.token}, function(data) {
				data ? this.loading = false : '';

				if(data.code == 0) {
					this.$alert(data.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	location.reload();
			          	}
			        });
				}
				else {
					this.registerList = data.data;
					this.total = data.total;
					this.pageSize = data.per_page;
				}
			}.bind(this))
		}
	}
</script>

<style lang="less">
	.register-littleProgram {
		.search {
			margin: 15px 0;
			font-size: 15px;
			.el-input {
				width: auto;
			}
			.search-btn {
				margin-left: 15px;
			}
		}
		.page {
			text-align: center;
			margin-top: 15px;
		}
	}
</style>


