<!-- 后台菜单 -->
<template>
  	<div class="right-content">
  		<el-tabs v-model="activeName" @tab-click="changeTab" class="content">

		    <el-tab-pane label="后台菜单" name="first" class="backstage-menu">

				<!-- 修改按钮 -->
				<el-button @click="changeListOrder()" type="primary" plain class="change-list-order">修改排序</el-button>

		    	<!-- 主菜单列表 -->
				<div class="el-table--border" style="margin-top: 15px;">
					<table class="el-table table" style="border-collapse: collapse;" v-loading="loading">
						<thead>
							<tr>
								<th class="is-leaf" style="width: 50px;"><div class="cell"></div></th>
								<th class="is-leaf" style="width: 100px;"><div class="cell">显示顺序</div></th>
								<th class="is-leaf" style="width: 80px;"><div class="cell">ID</div></th>
								<th class="is-leaf"><div class="cell">菜单名称</div></th>
								<th class="is-leaf" style="width: 350px;"><div class="cell">应用名</div></th>
								<th class="is-leaf" style="width: 80px;"><div class="cell">显示状态</div></th>
								<th class="is-leaf" style="width: 200px;"><div class="cell">操作</div></th>
							</tr>
						</thead>
						<tbody>
							<tr v-show="ctrList[index].show" v-for="(item, index) in tabList" :class="item._level != 1 ? 'unshow' : ''">
								<td>
									<div class="cell">
										<div class="el-table__expand-icon" :class="ctrList[index].openList ? 'el-table__expand-icon--expanded' : ''" @click="ctrMenuList(item, index)">
											<i class="el-icon el-icon-arrow-right"></i>
										</div>
									</div>
								</td>
								<td>
									<div class="cell">
										<el-input v-model="listOrder[index]" type="number"></el-input>
									</div>
								</td>
								<td>
									<div class="cell">{{item.id}}</div>
								</td>
								<td style="user-select: none;" @click="ctrMenuList(item, index)">
									<div class="cell">
										<div v-if="item._level == 1">{{item.label}}</div>

			      						<div v-else-if="item._level == 2">
			      							<div v-if="tabList[index + 1]">
			      								<div v-if="tabList[index + 1]._level == 3">
			      									<span style="margin: 0 15px 0 30px;">├─</span>{{item.label}}
			      								</div>

			      								<div v-else="">
				      								<span style="margin: 0 15px 0 30px;">{{tabList[index + 1]._level == item._level ? '├─' : '└─'}}</span>{{item.label}}
				      							</div>
			      							</div>
			      							<div v-else="">
			      								<span style="margin: 0 15px 0 30px;">└─</span>{{item.label}}
			      							</div>
			      						</div>

			      						<div v-else-if="item._level == 3">
			      							<div v-if="tabList[index + 1]">
			      								<div v-if="tabList[index + 1]._level == 4">
				      								<span style="margin: 0 15px 0 60px;">├─</span>{{item.label}}
				      							</div>

				      							<div v-else="">
				      								<span style="margin: 0 15px 0 60px;">{{tabList[index + 1]._level == item._level ? '├─' : '└─'}}</span>{{item.label}}
				      							</div>
			      							</div>
			      							<div v-else="">
			      								<span style="margin: 0 15px 0 60px;">└─</span>{{item.label}}
			      							</div>
			      						</div>

			      						<div v-else-if="item._level == 4">
			      							<div v-if="tabList[index + 1]">
			      								<span style="margin: 0 15px 0 90px;">{{tabList[index + 1]._level == item._level ? '├─' : '└─'}}</span>{{item.label}}
			      							</div>
			      							<div v-else="">
			      								<span style="margin: 0 15px 0 60px;">└─</span>{{item.label}}
			      							</div>
			      						</div>
									</div>
								</td>
								<td>
									<div class="cell">{{item.app}} / {{item.controller}} / {{item.action}}</div>
								</td>
								<td>
									<div class="cell">{{item.status == 1 ? '显示' : '隐藏'}}</div>
								</td>
								<td>
									<div class="cell">
										<el-button type="text" size="small" @click="addChildrenMenu(item)">添加子菜单</el-button>
				        				<el-button type="text" size="small" @click="openEdit(item)">编辑</el-button>
				    					<el-button type="text" size="small" @click="deleteMenu(item, index)">删除</el-button>
									</div>
								</td>
							</tr>
						</tbody>
					</table>
				</div>
		    </el-tab-pane>
			
		    <el-tab-pane label="添加菜单" name="second">
		    	<!-- 菜单添加与编辑的共用组件 -->
		    	<!-- 利用v-if机制，每次打开添加菜单选项卡都会重新渲染，达到reset效果 -->
				<backstageMenuEditAndAdd v-if="openAddMenu" v-on:change = "activeChange($event)"></backstageMenuEditAndAdd>
		    </el-tab-pane>

			<el-tab-pane label="编辑菜单" name="third" v-if="editMenu != ''">
				<!-- 菜单添加与编辑的共用组件 -->
				<backstageMenuEditAndAdd v-on:change = "activeChange($event)"></backstageMenuEditAndAdd>
		    </el-tab-pane>

		    <!-- 弹窗 -->
			<el-dialog title="添加子菜单" :visible.sync="dialogTableVisible" class="backstage-menu-dialog" width="40%" @close='closeDialog'>
		  		<div style="padding-left: 10%;">
		  			<p><span class="title">菜单上级：</span><b>{{menuParent}}</b></p>
					<p>
						<span class="title">菜单名称</span>
						<el-input placeholder="请输入菜单名字" v-model="menuName" clearable></el-input>
		    			<span class="required">*</span>
					</p>
					<p>
						<span class="title">应用名称</span>
    					<el-input placeholder="请输入英文" v-model="menuAppName" clearable></el-input>
		    			<span class="required">*</span>
					</p>
					<p>
						<span class="title">控制器名</span>
    					<el-input placeholder="请输入英文" v-model="menuCtrName" clearable></el-input>
		    			<span class="required">*</span>
					</p>
					<p>
						<span class="title">操作名称</span>
    					<el-input placeholder="请输入英文" v-model="menuActionName" clearable></el-input>
		    			<span class="required">*</span>
					</p>
					<p>
						<span class="title">额外参数</span>
    					<el-input placeholder="请输入英文" v-model="menuParamName" clearable></el-input>
    					<span style="margin-left: 15px;">例：id=3&p=3</span>
					</p>
					<p>
			    		<span class="title">菜单图标</span>
			    		<el-input placeholder="请输入类名" v-model="menuIcon" clearable></el-input>
			    		<span style="margin-left: 15px;">请参照&nbsp;<a href="https://www.thinkcmf.com/font/icons" target="_blank">font-awesome图标库</a></span>
			    	</p>
			    	<p>
			    		<span class="title">组件名称</span>
			    		<el-input placeholder="请输入英文" v-model="menuURL" clearable></el-input>
			    		<span class="required">*</span>
			    		<span style="margin-left: 5px;">对应Vue-router里path，作跳转用</span>
			    	</p>
					<p>
						<span class="title">导航状态</span>
						<el-select v-model="menuStatu" placeholder="请选择">
			    			<el-option v-for="item in status" :key="item.value" :label="item.label" :value="item.value"></el-option>
			  			</el-select>
		    			<span class="required">*</span>
					</p>
		  		</div>
		  		<span slot="footer" class="dialog-footer">
			    	<el-button @click="closeDialog()">取消</el-button>
				    <el-button type="primary" @click="submitMenuChildrenAdd()">确定</el-button>
				</span>
			</el-dialog>
  		</el-tabs>
  	</div>
</template>

<script>
	import apis from '../../apis'
	import backstageMenuEditAndAdd from '@/views/backstage-menu/backstage-menu-EditAndAdd'

	export default {
		inject: ['reload', 'reloadTab'],
		components: {
			backstageMenuEditAndAdd
		},
		data() {
			return {
 				activeName: 'first',
 				tabList: [],
 				dialogTableVisible: false,
 				formLabelWidth: '120px',
 				editMenu: [],
        		propTabMenu: [],
        		isAppend: false,
        		openAddMenu: false,
        		loading: true,
        		listOrder: [],
        		listOrderParentId: [],
                status: [{value: 1, label: '显示'},{value: 0, label: '隐藏'}],
        		menuListOrderArr: [],
        		menuParent: '',
        		menuName: '',
        		menuStatu: '',
        		menuAppName : '',
				menuCtrName : '',
				menuActionName : '',
				menuParamName : '',
				menuIcon : '',
				menuURL : '',
				canSubmit: false,
				menuParentId: '',
				cloneListOrder: [],
				originalListOrderArr: [],
				ctrList: [],
				leaveOneOpen: true,
				record: ['', ''],
			}
		},
		methods: {
			//改变头部选项卡
	      	changeTab(tab, event) {
	        	// 清空编辑栏的内容
	        	tab.index != '2' ? this.editMenu = [] : '';

	        	if(tab.index == '1') {
					this.openAddMenu = true;
					this.$root.propMenuType = 'add';
					this.$root.propTabMenu = this.propTabMenu;
	        	}
	        	else {
	        		this.openAddMenu = false;
	        	}
	      	},
	      	// 展开 & 折叠列表
	      	ctrMenuList(row, index) {
	      		var self = this;
	      		// 展开父id对应的子id层
	      		for(var i = 0; i < self.ctrList.length; i ++) {
	      			if(self.ctrList[i].parent_id == row.id) {
	      				self.ctrList[i].show == false ? self.ctrList[i].show = true : self.ctrList[i].show = false;
	      			}
	      		}
	      		// openList 控制表头开关的箭头
      			self.ctrList[index].openList == false ? self.ctrList[index].openList = true : self.ctrList[index].openList = false;
      			
	      		// 如果是第一层
	      		if(row._level == 1) {
	      			// 判断是否点击已打开的对应层
	      			if(self.record[0] == row.id) {
	      				// 如果已经打开过，再点第一层，把所有层都关闭
		      			if(self.leaveOneOpen) {
							for(var k = 0; k < self.ctrList.length; k ++) {
								self.ctrList[k].path[1] == row.id ? (self.ctrList[k].show = false) | (self.ctrList[k].openList = false) : '';
							}
			      			self.leaveOneOpen = false;
			      		}
			      		else {
			      			self.leaveOneOpen = true;
			      		}
	      			}
	      			// 如果不是则关闭其他的层
	      			else {
	      				for(var k = 0; k < self.ctrList.length; k ++) {
							self.ctrList[k].path[1] == self.record[0] ? (self.ctrList[k].show = false) | (self.ctrList[k].openList = false) : '';
						}
						self.leaveOneOpen = true;
	      				self.record[0] = row.id;
	      				self.record[1] != '' ? self.ctrList[self.record[1]].openList = false : '';
	      			}
	      			self.record[1] = index;
		      	}
		      	// else if(row._level == 2) {
		      	// 	if(self.ctrList[index + 1].show) {
		      	// 		for(var j = 0; j < self.ctrList.length; j ++) {
		      	// 			row.id == self.ctrList[j].parent_id ? self.ctrList[j].show = false : '';
		      	// 		}
		      	// 	}
		      	// }
	      	},
		    // 打开编辑栏
		    openEdit(row) {
		    	this.dialogTableVisible = false;
		        this.editMenu.push(row);
		        this.activeName = 'third';
		        this.$root.propEditMenu = this.editMenu;
		        this.$root.propMenuType = 'edit';
		        this.$root.propTabMenu = this.propTabMenu;
//		        this.menuParent = row.label;
//	      		this.menuParentId = row.id;
		    },
		    // 接收子组件backstage-menu-edit传回来的值 改变头部选项卡
		    activeChange(name) {
		    	this.activeName = name;
		    	this.editMenu = [];
		    	this.openAddMenu = false;
		    },
		    // 删除菜单
		    deleteMenu(row, inx) {
		    	var self = this;
	    		self.$confirm('确认要删除【'+ row.label +'】吗？', '确认信息', {
		          	distinguishCancelAndClose: true,
		          	confirmButtonText: '确定',
		          	cancelButtonText: '取消'
		        }).then(() => {
		            // 点确定后
		            apis.menuDelete({id: row.id, token: this.$root.token}, function(bck) {
		    			self.$alert(bck.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				          		if(bck.info == '您还没有登录！！'){
				          			location.reload();
				          		}
				          		else {
				          			self.loading = true;
				          			apis.getLeftTapList({token: this.$root.token}, function(data) {
							    		data ? self.loading = false : '';
						    			 self.tabList = data;
						    			data.code == 1 ? self.tabList.splice(inx, 1) : '';
							    		// 把父id和list-order添加进数组里，用作排序修改
							    		for(var i = 0; i < data.length; i ++) {
							    			self.listOrderParentId.push(data[i].id);
							    			self.listOrder.push(data[i].list_order);
							    		}
							    		Object.assign(self.cloneListOrder, self.listOrder);
	    								this.reload();
				            			this.reloadTab();
							    	}.bind(this))
				          		}
				          	}
				        });
			    	}.bind(this))
	          	}).catch(action => {})
		    	
		    },
		    // 修改菜单显示顺序
		    changeListOrder() {
		    	// 将更改前和更改后发给后台做数据对比修改显示顺序
				for(var i = 0; i < this.listOrderParentId.length; i++) {
					this.menuListOrderArr.push({id: JSON.parse(JSON.stringify(this.listOrderParentId))[i], list_order: JSON.parse(JSON.stringify(this.listOrder))[i]});
					this.originalListOrderArr.push({id: JSON.parse(JSON.stringify(this.listOrderParentId))[i], list_order: JSON.parse(JSON.stringify(this.cloneListOrder))[i]});
				}
				
		    	apis.menuListOrders({list_order: this.menuListOrderArr, original_list_order: this.originalListOrderArr, token: this.$root.token}, function(bck) {
		    		bck.info == '排序失败' ? bck.info = '排序成功' : '';
					this.$alert(bck.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			          		if(bck.info == '您还没有登录！！') {
			            		location.reload();
			          			this.$router.push('/');
							}
							else if(bck.code == 1) {
								this.reload();
								this.reloadTab();
							}
			          	}
			        });
		    	}.bind(this))
		    },
	      	// 添加子菜单
	      	addChildrenMenu(row) {
	      		this.menuParent = row.label;
	      		this.menuParentId = row.id;
		        this.dialogTableVisible = true;
		        this.menuStatu = row.status;
		    },
		    // 关闭弹窗并reset
		    closeDialog(){
        		this.menuName = '';
        		this.menuStatu =  '';
        		this.menuAppName = '';
				this.menuCtrName = '';
				this.menuActionName = '';
				this.menuParamName = '';
				this.menuIcon = '';
				this.menuURL = '';
        		this.dialogTableVisible = false;
		    },
		    // 提交子菜单添加
		    submitMenuChildrenAdd() {
		    	var self = this;
		    	this.canSubmit = false;

		    	this.menuName == '' ? alertMsg('请输入菜单名字') : this.menuURL == '' ? alertMsg('请填写页面路径') : this.menuStatu === '' ? alertMsg('请选择菜单状态') : this.canSubmit = true;

		    	if(this.canSubmit) {
		    		apis.menuAdd({parent_id: this.menuParentId, name: this.menuName, app: this.menuAppName, controller: this.menuCtrName, action: this.menuActionName, param: this.menuParamName, icon: this.menuIcon, url: this.menuURL, status: this.menuStatu, token: this.$root.token}, function(bck) {
						this.$alert(bck.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				          		if(bck.info == '您还没有登录！！'){
				          			location.reload();
				          			this.$router.push('/');
				          		}
				          		else if(bck.code == 1) {
				          			this.reload();
                                	this.reloadTab();
				          		}
				          	}
				        });
			    	}.bind(this))
		    	}
		    	function alertMsg(msg) {
			    	self.$alert(msg, {confirmButtonText: '确定'});
			    }
		    },
	    },
	    created() {
	    	// 发起请求获取菜单列表
	    	apis.getLeftTapList({token: this.$root.token}, function(data) {
	    		data ? this.loading = false : '';
	    		if(data.code == 0) {
					this.$alert(data.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			          		this.$router.push('/');
			            	location.reload();
			          	}
			        });
				}
	    		else {
	    			this.tabList = data;
		    		
		    		for(var i = 0; i < data.length; i ++) {
		    			// 把父id和list-order添加进数组里，用作排序修改
		    			this.listOrderParentId.push(data[i].id);
		    			this.listOrder.push(data[i].list_order);

		    			// 控制菜单列表折叠
		    			data[i]._level == 1 ? this.ctrList.push({'show': true, 'id': data[i].id, 'path': '', 'parent_id': '', openList: false}) : this.ctrList.push({'show': false, 'id': data[i].id, 'path': data[i].path, 'parent_id': data[i].parent_id, openList: false});
		    		}
		    		Object.assign(this.cloneListOrder, this.listOrder);
		    	}
	    	}.bind(this))

	    	apis.menuListTree({token: this.$root.token}, function(data) {
	    		this.propTabMenu = data;
	    		this.propTabMenu.unshift({'value': 0, 'label': '作为一级菜单'});
	    	}.bind(this))
	    	

	    	
	    }
	}
</script>

<style lang="less">
	.backstage-menu {
	  	.demo-table-expand label {
	    	width: 90px;
	    	color: #99a9bf;
	  	}
	  	.demo-table-expand .el-form-item {
	    	margin-right: 0;
	    	margin-bottom: 0;
	    	width: 50%;
	  	}
	  	.change-list-order {
	  		margin-bottom: 15px;
	  	}
	  	.table {
	  		.cell {
		    	.el-input {
		    		width: 100%;
		    	}
		    }
		    .el-table::before {
			    display: none;
			}
			table tbody>tr:nth-child(even) {
				background: #fafafa;
			}
			tr:hover {
				background-color: #f5f7fa;
			}
			.unshow {

			}
	  	}
	}
	.backstage-menu-dialog {
		.el-input {
			width: auto;
		}
		.title {
			margin-right: 15px;
		}
	}
</style>


