<!-- 表单管理 -->
<template>
	<div class="right-content form">
		<el-tabs v-model="activeName" @tab-click="changeTab" class="content">
			<el-tab-pane label="表单管理" name="first">
				
				<div class="search">
					<span style="margin-left: 15px;">表单名称：</span>
					<el-input v-model="keyword" placeholder="请输入内容" clearable></el-input>
					<span style="margin-left: 15px;">创建日期：</span>
					<el-date-picker v-model="date" type="daterange" align="center" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
					<el-button @click="searchForm()" type="primary" size="medium" icon="el-icon-search" class="search-btn">搜索</el-button>
				</div>

				<el-table :data="formList" :cell-style="iscenter" style="width: 100%" :border='true' v-loading="loading" class="fromList">
	    			<el-table-column label="ID" prop="id" width="80"></el-table-column>
	    			<el-table-column label="表单名称" prop="form_name"></el-table-column>
	    			<el-table-column label="收集数据条数" prop="data_count" width="120"></el-table-column>
	    			<el-table-column label="最近数据提交时间" prop="last_post_time" :formatter="formatDate" width="180"></el-table-column>
	    			<el-table-column label="创建时间" prop="create_time" width="180"></el-table-column>
				 	<el-table-column label="操作" width="170">
	      				<template slot-scope="scope">
	        				<el-button type="text" size="small" @click="checkData(scope.row)">查看数据</el-button>
	        				<el-button type="text" size="small" @click="editForm(scope.row)">编辑</el-button>
	    					<el-button type="text" size="small" @click="deleteForm(scope.row)">删除</el-button>
	      				</template>
	    			</el-table-column>
				</el-table>

				<el-pagination layout="prev, pager, next" :total="total" class="page" :page-size="pageSize" @current-change="currentChange" :current-page.sync="currentPage" v-if="total != 0"></el-pagination>
			</el-tab-pane>

			<el-tab-pane label="添加表单" name="second">
				<formEditAndAdd v-on:change="activeChange($event)" v-if="add"></formEditAndAdd>
			</el-tab-pane>

			<el-tab-pane label="编辑表单" name="third" v-if="edit">
				<formEditAndAdd v-on:change="activeChange($event)"></formEditAndAdd>
			</el-tab-pane>

			<el-tab-pane label="数据查看" name="fourth" v-if="check">
				<el-button class="export-data" @click="exportExcel()">导出数据</el-button>
				<el-table :data="formDataList" style="width: 100%" :border='true' v-loading="loading" id="out-table">
	    			<el-table-column label="姓名" width="130">
	    				<template slot-scope="scope">
	        				<div>{{formDataList[scope.$index].data.name}}</div>
	      				</template>
	    			</el-table-column>
	    			<el-table-column label="电话" width="130">
	    				<template slot-scope="scope">
	        				<div>{{formDataList[scope.$index].data.phone}}</div>
	      				</template>
	    			</el-table-column>
	    			<el-table-column label="症状">
	    				<template slot-scope="scope">
	        				<div>{{formDataList[scope.$index].data.symptom}}</div>
	      				</template>
	    			</el-table-column>
	    			<el-table-column label="页面" prop="page"></el-table-column>
	    			<el-table-column label="IP地址" prop="ip" width="150"></el-table-column>
	    			<el-table-column label="提交时间" prop="create_time" width="160"></el-table-column>
				</el-table>
			</el-tab-pane>
		</el-tabs>
	</div>
</template>

<script>
	import apis from '../../apis'
	import formEditAndAdd from '@/views/form/form-EditAndAdd'
 	import FileSaver from 'file-saver'
    import XLSX from 'xlsx'

	export default {
		inject: ['reload', 'reloadTab'],
		components: {
			formEditAndAdd
		},
		data() {
			return {
				loading: true,
				activeName: 'first',
				formList: [],
				keyword: '',
				date: '',
				page: 1,
				total: 0,
				pageSize: 0,
				add: false,
				edit: false,
				check: false,
				formDataList: [],
				currentPage: 1,
				search: false,
			}
		},
		methods: {
//			某列表格居中
            iscenter({row, column, rowIndex, columnIndex}){
            	if(columnIndex===2){
            		return 'text-align: center'
            	}else{
            		return 'text-align: left'
            	}
            },
            // 时间戳转化
             formatDate(row, column) {
             	let lasttime=row.last_post_time;
             	if(lasttime){
             		let date = new Date(parseInt(row.last_post_time) * 1000);
                let Y = date.getFullYear() + '-';
                let M = date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) + '-' : date.getMonth() + 1 + '-';
                let D = date.getDate() < 10 ? '0' + date.getDate() + ' ' : date.getDate() + ' ';
                let h = date.getHours() < 10 ? '0' + date.getHours() + ':' : date.getHours() + ':';
                let m = date.getMinutes()  < 10 ? '0' + date.getMinutes() + ':' : date.getMinutes() + ':';
                let s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
                return Y + M + D + h + m + s;
             	}else{
             		return ''
             	}
                
            },
			// 切换头部导航栏
			changeTab(tab, event) {
				tab.index == 1 ? (this.add = true) | (this.$root.propType = 'add') : (this.add = false) | (this.edit = false);
				tab.index != 2 ? (this.edit = false) | (this.check = false) | (this.formDataList = []) : '';
			},
            // page改变
            currentChange(page) {
            	this.loading = true;
            	var startTime, endTime, keyword;
            	if(this.search) {
            		this.date != '' ? (startTime = this.date[0]) | (endTime = this.date[1]) : '';
					this.keyword != '' ? keyword = this.keyword : '';
            	}
            	this.page = page;
            	apis.formList({start_time: startTime, end_time: endTime, keyword: keyword, page: page, token: this.$root.token}, function(data) {
					data ? this.loading = false : '';
					if(data.code == 0) {
						this.$alert(data.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				            	location.reload();
				          	}
				        });
					}
					else {
						this.formList = data.data;
						this.total = data.total;
						this.pageSize = data.per_page;
					}
				}.bind(this))
            },
			// 删除
			deleteForm(row) {
				apis.formDelete({id: row.id, token: this.$root.token}, function(bck) {
					this.$options.methods.alertMsg(this, bck.info, bck.code);
				}.bind(this))
			},
			// 编辑
			editForm(row) {
				this.activeName = 'third';
				this.edit = true;
				this.$root.editForm = row;
				this.$root.propType = 'edit';
			},
		    // 接收子组件 传回来的值 改变头部选项卡
		    activeChange(name) {
		    	this.activeName = name;
		    	this.add = false;
		    	this.edit = false;
		    },
		    // 查看数据
		    checkData(row) {
		    	this.activeName = 'fourth';
		    	this.check = true;
		    	this.loading = true;

		    	apis.formDataList({form_id: row.id, token: this.$root.token}, function(data) {
		    		data ? this.loading = false : '';
		    		this.formDataList = data;
		    	}.bind(this))
		    },
		    // 导出
		    exportExcel() {
	         	if(this.formDataList.length == 0) {
	         		this.$alert('没有数据');
	         		return;
	         	}
	         	var wb = XLSX.utils.table_to_book(document.querySelector('#out-table'))
		        
	         	var wbout = XLSX.write(wb, { bookType: 'xlsx', bookSST: true, type: 'array' })
	         	try {
	             	FileSaver.saveAs(new Blob([wbout], { type: 'application/octet-stream' }), 'data.xlsx')
	         	} 
	         	catch(e) { if (typeof console !== 'undefined') console.log(e, wbout) }
	         	return wbout
		    },
	      	// 搜索
	      	searchForm() {
	      		this.date === null ? this.date = '' : '';
	      		this.loading = true;
	      		apis.formList({start_time: this.date[0], end_time: this.date[1], keyword: this.keyword, token: this.$root.token}, function(data) {
					data ? this.loading = false : '';

					if(data.code == 0) {
						this.$alert(data.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				            	location.reload();
				          	}
				        });
					}
					else {
						this.formList = data.data;
						this.total = data.total;
						this.pageSize = data.per_page;
						if(this.date !== null || this.keyword != '') {
							this.search = true;
						}
						else {
							this.search = false;
						}
						this.currentPage = 1;
					}
				}.bind(this))
	      	},
			// 公共消息提示部分
            alertMsg: function(_this, info, code) {
            	_this.$alert(info, {
	         	 	confirmButtonText: '确定',
		          	callback: action => {
		            	if(code != 0) {
		            		_this.reload();
		            	}
		            	else if(info == '您还没有登录！！') {
		            		location.reload();
		            	}
		          	}
		        });
            }
		},
		created() {
			apis.formList({token: this.$root.token}, function(data) {
				data ? this.loading = false : '';

				if(data.code == 0) {
					this.$alert(data.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	location.reload();
			          	}
			        });
				}
				else {
					this.formList = data.data;
					this.total = data.total;
					this.pageSize = data.per_page;
				}
			}.bind(this))
		}
	}
</script>

<style lang="less">
	.form {
		.delete-forms, .export-data, .search {
			margin-bottom: 15px;
		}
		.search {
			font-size: 15px;
		    text-align: right;
			.el-input {
				width: auto;
			}
			.search-btn {
				margin-left: 15px;
			}
		}
		.page {
			text-align: center;
			margin-top: 15px;
		}
	}
</style>


