<!-- 后台添加与编辑 -->
<template>
	<div class="edit-menu">
		<div>
			<span class="title">菜单上级</span>
			<el-cascader expand-trigger="click" :options="tabMenu" v-model="selectedOptions" change-on-select></el-cascader>
			<span class="required">*</span>
    	</div>
    	<div class="menu-name">
    		<span class="title">{{propType == 'edit' ? '修改名称' : '菜单名称'}}</span>
    		<el-input placeholder="请输入菜单名字" v-model="menuName" clearable></el-input>
    		<span class="required">*</span>
    	</div>
    	<div class="menu-app">
    		<span class="title">应用名称</span>
    		<el-input placeholder="请输入英文" v-model="menuAppName" clearable></el-input>
    		<span class="required">*</span>
    	</div>
    	<div class="menu-app">
    		<span class="title">控制器名</span>
    		<el-input placeholder="请输入英文" v-model="menuCtrName" clearable></el-input>
    		<span class="required">*</span>
    	</div>
    	<div class="menu-app">
    		<span class="title">操作名称</span>
    		<el-input placeholder="请输入英文" v-model="menuActionName" clearable></el-input>
    		<span class="required">*</span>
    	</div>
    	<div class="menu-app">
    		<span class="title">额外参数</span>
    		<el-input placeholder="请输入英文" v-model="menuParamName" clearable></el-input>
    		<span style="margin-left: 15px;">例：id=3&p=3</span>
    	</div>
    	<div class="menu-icon">
    		<span class="title">{{propType == 'edit' ? '修改图标' : '菜单图标'}}</span>
    		<el-input placeholder="请输入类名" v-model="menuIcon" clearable></el-input>
    		<span>请参照&nbsp;<a href="http://fontawesome.dashgame.com/" target="_blank">font-awesome图标库</a></span>
    	</div>
    	<div class="menu-app">
    		<span class="title">组件名称</span>
    		<el-input placeholder="请输入英文" v-model="menuURL" clearable></el-input>
    		<span class="required">*</span>
    		<span style="margin-left: 5px;">对应Vue-router里path，这里用作路由跳转用</span>
    	</div>
    	<div class="menu-statu">
    		<span class="title">{{propType == 'edit' ? '修改状态' : '菜单状态'}}</span>
    		<el-select v-model="menuStatu" value-key="value" placeholder="请选择">
    			<el-option v-for="item in status"  :key="item.value" :label="item.label" :value="item.value"></el-option>
  			</el-select>
    		<span class="required">*</span>
    	</div>
    	<div class="menu-btn">
    		<el-button type="primary" @click="submitMenu()">{{propType == 'edit' ? '保存' : '添加'}}</el-button>
    		<el-button @click="goBack()">返回</el-button>
    	</div>
	</div>
</template>

<script>
	import apis from '../../apis'

	export default {
		inject: ['reload', 'reloadTab'],
		data() {
			return {
				tabMenu: [],
 				editMenu: [],
        		selectedOptions: [],
        		menuName: '',
        		menuAppName: '',
        		menuCtrName: '',
        		menuActionName: '',
        		menuParamName: '',
        		menuURL: '',
        		menuIcon: '',
        		menuStatu:"1",
        		status: [{value: 1, label: '显示'},{value: 0, label: '隐藏'}],
        		propType: '',
        		canSubmit: false
			}
		},
		methods: {
		    // 返回上一页
		    goBack() {
		    	this.$emit("change", "first");
		    },
		    // 提交
		    submitMenu() {
		    	var self = this;
		    	this.canSubmit = false;
               if(status==0){
               	
               }
		    	this.selectedOptions == '' ? alertMsg('请选择上级菜单') : this.menuURL == '' ? alertMsg('请填写页面路径') : this.menuStatu === '' ? alertMsg('请选择菜单状态') : this.canSubmit = true;

	    		if(this.propType == 'add') {
		    		if(this.canSubmit) {
		    			apis.menuAdd({parent_id: this.selectedOptions[this.selectedOptions.length-1], name: this.menuName, app: this.menuAppName, controller: this.menuCtrName, action: this.menuActionName, param: this.menuParamName, icon: this.menuIcon, url: this.menuURL, status: this.menuStatu, token: this.$root.token}, function(bck) {
			    			this.$alert(bck.info, {
				         	 	confirmButtonText: '确定',
					          	callback: action => {
					            	if(bck.info == '您还没有登录！！') {
					            		location.reload();
					            	}
					            	else if(bck.code == 1) {
					            		this.reload();
				            			this.reloadTab();
					            	}
					          	}
					        });
				    	}.bind(this))
		    		}
		    	}
		    	else if(this.propType == 'edit') {
		    		if(this.canSubmit) {
		    			apis.menuEdit({id: this.editMenu[0].id, parent_id: this.selectedOptions[this.selectedOptions.length-1], name: this.menuName, app: this.menuAppName, controller: this.menuCtrName, action: this.menuActionName, param: this.menuParamName, icon: this.menuIcon, url: this.menuURL, status: this.menuStatu, token: this.$root.token}, function(bck) {
				    		this.$alert(bck.info, {
				         	 	confirmButtonText: '确定',
					          	callback: action => {
					            	if(bck.info == '您还没有登录！！') {
					            		location.reload();
					            	}
					            	else if(bck.code == 1) {
					            		this.reload();
				            			this.reloadTab();
					            	}
					          	}
					        });
				    	}.bind(this))
		    		}
		    	}
			    function alertMsg(msg) {
			    	self.$alert(msg, {confirmButtonText: '确定'});
			    }
		    },
		},
		created() {
			this.tabMenu = JSON.parse(JSON.stringify(this.$root.propTabMenu));
			this.propType = this.$root.propMenuType;
			if(this.propType == 'edit') {
				this.editMenu = JSON.parse(JSON.stringify(this.$root.propEditMenu));
				var path =JSON.parse(JSON.stringify(this.editMenu[0].path));             
                var paths=path.split(',');
                for(var s=0; s<paths.length;s++){
                	this.selectedOptions.push(Number(paths[s]));
                }
				this.menuName = this.editMenu[0].label;
				this.menuAppName = this.editMenu[0].app;
				this.menuCtrName = this.editMenu[0].controller;
				this.menuActionName = this.editMenu[0].action;
				this.menuURL = this.editMenu[0].url;
				this.menuStatu = this.editMenu[0].status;
				this.menuIcon = this.editMenu[0].icon;
			}
			else {
				this.selectedOptions = [this.tabMenu[0].value];
				this.menuStatu=Number(this.menuStatu)
			}

			// 删掉第四层菜单 菜单上级只展示三层
			for(var i = 0; i < this.tabMenu.length; i++) {
				if(this.tabMenu[i].children) {

					for(var k = 0; k < this.tabMenu[i].children.length; k++) {
						if(this.tabMenu[i].children[k].children) {

							for(var l = 0; l < this.tabMenu[i].children[k].children.length; l++) {
								delete this.tabMenu[i].children[k].children[l].children;
							}
						}
					}
				}
			}
		}
	}
</script>
<style lang="less">
	.edit-menu {
		font-size: 15px;
		padding-left: 5%;
		margin-top: 20px;
		.menu-name .el-input, .menu-icon .el-input, .menu-leave .el-input, .menu-app .el-input {
			width: auto;
		}
		.menu-name, .menu-icon, .menu-leave, .menu-statu, .menu-app {
			margin-top: 10px;
		}
		.menu-icon .el-input {
			margin-right: 15px;
		}
		.menu-icon {
			a {
				color: #409eff;
			}
		}
		.menu-statu {
			.el-cascader-menu {
			    height: 85px;
			}
		}
		.menu-btn {
			margin-top: 20px;
		}
		.title {
			margin-right: 15px;
		}
	}
</style>


