<!-- 科室管理 -->
<template>
	<div class="right-content department">
		<el-tabs v-model="activeName" @tab-click="changeTab" class="content">
			<el-tab-pane label="科室管理" name="first">
				<!-- 修改按钮 -->
				<el-button @click="changeListOrder()" type="primary" plain class="change-list-order">修改顺序</el-button>

				<el-table :data="departmentList" style="width: 100%" :border='true' v-loading="loading">
					<el-table-column label="显示顺序" width="100">
	    				<template slot-scope="scope">
	    					<el-input v-model="departmentListOrderArr[scope.$index]" type="number"></el-input>
	    				</template>
	    			</el-table-column>
					<el-table-column label="ID" prop="id" width="80"></el-table-column>
					<el-table-column label="科室名称">
						<template slot-scope="scope">
      						<div v-if="departmentList[scope.$index]._level == 1">{{departmentList[scope.$index].name}}</div>
      						<div v-else-if="departmentList[scope.$index]._level == 2"><span style="margin: 0 15px 0 30px;">├─</span>{{departmentList[scope.$index].name}}</div>
      						<div v-else-if="departmentList[scope.$index]._level == 3"><span style="margin: 0 15px 0 60px;">├─</span>{{departmentList[scope.$index].name}}</div>
      						<div v-else-if="departmentList[scope.$index]._level == 4"><span style="margin: 0 15px 0 90px;">├─</span>{{departmentList[scope.$index].name}}</div>
      					</template>
					</el-table-column>
				 	<el-table-column label="状态" width="80">
				 		<template slot-scope="scope">
				 			<el-button type="text" size="small" @click="changeStatus(scope.row)">{{departmentList[scope.$index].status == 1 ? '显示' : '隐藏'}}</el-button>
				 		</template>
				 	</el-table-column>
				 	<el-table-column label="操作" width="100">
	      				<template slot-scope="scope">
	        				<el-button type="text" size="small" @click="editDepartment(scope.row)">编辑</el-button>
	    					<el-button type="text" size="small" @click="deleteDepartment(scope.row)">删除</el-button>
	      				</template>
	    			</el-table-column>
				</el-table>
			</el-tab-pane>
			
			<el-tab-pane label="添加科室" name="second">
				<div class="add-department">
					<span class="title">科室上级</span>
					<el-cascader :options="departmentList2" v-model="selectedOptions" change-on-select></el-cascader>
		    		<span class="required">*</span>
				</div>
				<div class="add-department">
					<span class="title">科室名称</span>
					<el-input v-model="addDepartmentName" placeholder="请输入名称"></el-input>
		    		<span class="required">*</span>
				</div>
				<div class="add-department">
					<span class="title" style="vertical-align: top;">科室描述</span>
					<el-input v-model="addDepartmentRemark" type="textarea" :autosize="{ minRows: 2, maxRows: 4}"></el-input>
				</div>
				<div class="add-department">
		    		<span class="title">SEO标题</span>
		    		<el-input placeholder="请输入SEO标题" v-model="addSeoTitle" clearable></el-input>
		    	</div>
		    	<div class="add-department">
		    		<span class="title">SEO关键词</span>
		    		<el-input placeholder="请输入关键词" v-model="addSeoKeyword" clearable></el-input>
		    	</div>
		    	<div class="add-department">
		    		<span class="title" style="vertical-align: top;">SEO描述</span>
		    		<el-input v-model="addSeoDescription" clearable type="textarea" :autosize="{ minRows: 2, maxRows: 4}"></el-input>
		    	</div>
		    	<div class="add-department">
		    		<span class="title">列表模板</span>
					<el-select v-model="listTemplate" placeholder="请选择">
	    				<el-option v-for="item in listTPL" :key="item.id" :label="item.name" :value="item.id"></el-option>
	  				</el-select>
		    	</div>
		    	<div class="add-department">
		    		<span class="title">文章模板</span>
					<el-select v-model="articleTemplate" placeholder="请选择">
	    				<el-option v-for="item in listTPL" :key="item.id" :label="item.name" :value="item.id"></el-option>
	  				</el-select>
		    	</div>
				<div class="add-department">
		    		<el-button type="primary" @click="addDepartment()">添加</el-button>
		    		<el-button @click="goBack()">返回</el-button>
		    	</div>
			</el-tab-pane>
		</el-tabs>

		<!-- 编辑弹窗 -->
	  	<el-dialog title="编辑科室" :visible.sync="dialogDepartmentVisible" width="40%">
  			<p class="edit-department">
  				<span class="title">科室上级</span>
  				<el-cascader :options="departmentList2" v-model="selectedOptions" change-on-select></el-cascader>
    			<span class="required">*</span>
  			</p>
			<p class="edit-department">
				<span class="title">科室名称</span>
				<el-input v-model="editDepartmentName" placeholder="请输入名称"></el-input>
	    		<span class="required">*</span>
			</p>
			<p class="edit-department">
				<span class="title" style="vertical-align: top;">科室描述</span>
				<el-input v-model="editDepartmentRemark" type="textarea" :autosize="{ minRows: 2, maxRows: 4}"></el-input>
			</p>
			<p class="edit-department">
				<span class="title">SEO标题</span>
	    		<el-input placeholder="请输入SEO标题" v-model="editSeoTitle" clearable></el-input>
			</p>
			<p class="edit-department">
				<span class="title">SEO关键词</span>
	    		<el-input placeholder="请输入关键词" v-model="editSeoKeyword" clearable></el-input>
			</p>
			<p class="edit-department">
				<span class="title" style="vertical-align: top;">SEO描述</span>
	    		<el-input v-model="editSeoDescription" clearable type="textarea" :autosize="{ minRows: 2, maxRows: 4}"></el-input>
			</p>
			<p class="edit-department">
	    		<span class="title">列表模板</span>
				<el-select v-model="listTemplate" placeholder="请选择">
    				<el-option v-for="item in listTPL" :key="item.id" :label="item.name" :value="item.id"></el-option>
  				</el-select>
	    	</p>
	    	<p class="edit-department">
	    		<span class="title">文章模板</span>
				<el-select v-model="articleTemplate" placeholder="请选择">
    				<el-option v-for="item in listTPL" :key="item.id" :label="item.name" :value="item.id"></el-option>
  				</el-select>
	    	</p>
			<span slot="footer" class="dialog-footer">
		    	<el-button @click="dialogDepartmentVisible = false">取 消</el-button>
			    <el-button type="primary" @click="submitDepartmentEdit()">确 定</el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	import apis from '../../apis'

	export default {
		inject: ['reload', 'reloadTab'],
		data() {
			return {
				activeName: 'first',
				loading: true,
				dialogDepartmentVisible: false,
				departmentList: [],
				departmentList2: [],
				addDepartmentName: '',
				addDepartmentRemark: '',
				addSeoTitle: '',
				addSeoKeyword: '',
				addSeoDescription: '',
				selectedOptions: [],
				idListArr: [],
				departmentListOrderArr: [],
				cloneListOrder: [],
				originalListOrderArr: [],
				updateListOrderArr: [],
				departmentId: 0,
				editDepartmentName: '',
				editDepartmentRemark: '',
				editSeoTitle: '',
				editSeoKeyword: '',
				editSeoDescription: '',
				listTemplate: 0,
				articleTemplate: 0,
				listTPL: [],
			}
		},
		methods: {
			changeTab(tab) {
				if(tab.index == 1) {
					this.addDepartmentName = '';
					this.addDepartmentRemark = '';
					this.addSeoTitle = '';
					this.addSeoKeyword = '';
					this.addSeoDescription = '';
					this.selectedOptions = [0];
					this.listTemplate = 0;
					this.articleTemplate = 0;
				}
			},
			// 添加
			addDepartment() {
				apis.departmentAdd({name: this.addDepartmentName, parent_id: this.selectedOptions[this.selectedOptions.length - 1], description: this.addDepartmentRemark, seo_title: this.addSeoTitle, seo_keyword: this.addSeoKeyword, seo_description: this.addSeoDescription, list_tpl: this.listTemplate, one_tpl: this.articleTemplate, token: this.$root.token}, function(bck) {
					this.$options.methods.alertMsg(this, bck.info, bck.code);
				}.bind(this))
			},
			// 返回
			goBack() {
				this.addDepartmentName = '';
				this.addDepartmentRemark = '';
				this.addSeoTitle = '';
				this.addSeoKeyword = '';
				this.addSeoDescription = '';
				this.selectedOptions = [0];
				this.activeName = 'first';
				this.listTemplate = 0;
				this.articleTemplate = 0;
			},
			// 删除
			deleteDepartment(row) {
				apis.departmentDelete({id: row.id, token: this.$root.token}, function(bck) {
					this.$options.methods.alertMsg(this, bck.info, bck.code);
				}.bind(this))
			},
			// 排序
			changeListOrder() {
		    	var id = this.idListArr;

		    	// 将更改前和更改后发给后台做数据对比修改显示顺序
				for(var i = 0; i < id.length; i++) {
					this.updateListOrderArr.push({id: JSON.parse(JSON.stringify(id))[i], list_order: JSON.parse(JSON.stringify(this.departmentListOrderArr))[i]});
					this.originalListOrderArr.push({id: JSON.parse(JSON.stringify(id))[i], list_order: JSON.parse(JSON.stringify(this.cloneListOrder))[i]});
				}
				apis.departmentListOrder({list_order: this.updateListOrderArr, original_list_order: this.originalListOrderArr, token: this.$root.token}, function(bck) {
            		bck.info == '排序失败' ? bck.info = '排序成功' : '';
					this.$options.methods.alertMsg(this, bck.info, bck.code);
            	}.bind(this))
			},
			// 编辑
			editDepartment(row) {
				this.departmentId = row.id;
				this.editDepartmentName = row.name;
				this.editDepartmentRemark = row.description;
				this.editSeoTitle = row.seo_title;
				this.editSeoKeyword = row.seo_keywords;
				this.editSeoDescription = row.seo_description;
      			this.listTemplate = row.list_tpl;
				this.articleTemplate = row.one_tpl;
				this.dialogDepartmentVisible = true;
                  if(row.parent_id==0){
                    	this.selectedOptions=[0];
                    }else{
                    	this.selectedOptions=[];
                    	let path=row.path.split(',');
                    	path.pop();
                    	for(var s=0; s<path.length;s++){ 			
                	 	this.selectedOptions.push(Number(path[s]));
                	}
                    }
			},
			// 保存编辑
			submitDepartmentEdit() {
				apis.departmentEdit({id: this.departmentId, name: this.editDepartmentName, parent_id: this.selectedOptions[this.selectedOptions.length - 1], description: this.editDepartmentRemark, seo_title: this.editSeoTitle, seo_keywords: this.editSeoKeyword, seo_description: this.editSeoDescription, list_tpl: this.listTemplate, one_tpl: this.articleTemplate, token: this.$root.token}, function(bck) {
					this.$options.methods.alertMsg(this, bck.info, bck.code);
				}.bind(this))
			},
			// 公共消息提示部分
			alertMsg: function(_this, info, code) {
            	_this.$alert(info, {
	         	 	confirmButtonText: '确定',
		          	callback: action => {
		            	if(code == 1) {
		            		_this.reload();
		            	}
		            	else if(code==0) {
		            		
		            	}else if(info == '您还没有登录！！'){
		            		location.reload();
		            	}
		          	}
		        });
            },
	      	// 显示 & 隐藏
	      	changeStatus(row) {
	      		var statu;
	      		row.status == 1 ? statu = 0 : statu = 1;
	      		apis.departmentStatus({id: row.id, status: statu, token: this.$root.token}, function(bck) {
	      			bck.code == 1 ? row.status = statu : '';
	      			if(bck.info == '您还没有登录！！') {
	      				this.$alert(bck.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				          		location.reload();
				          	}
				        });
	      			}
	      		}.bind(this))
	      	}
		},
		created() {
			apis.departmentList({token: this.$root.token}, function(data) {
				data ? this.loading = false : '';
				if(data.code == 0) {
					this.$alert(data.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	location.reload();
			          	}
			        });
				}
				else {
					this.departmentList = data;
					// 把导航列表的id和list-order添加进数组里，用作修改排序
					for(var i = 0; i < data.length; i ++) {
		    			this.idListArr.push(data[i].id);
		    			this.departmentListOrderArr.push(data[i].list_order);
		    		}
		    		Object.assign(this.cloneListOrder, this.departmentListOrderArr);
				}
			}.bind(this))

			apis.departmentListTree({token: this.$root.token}, function(data) {
				this.departmentList2 = data;
				this.departmentList2.unshift({'value': 0, 'label': '作为一级菜单'});
			}.bind(this))

			apis.themeList({token: this.$root.token}, function(data) {
				this.listTPL = data;
				this.listTPL.unshift({'id': 0, 'name': '默认'});
			}.bind(this))
		}
	}
</script>

<style lang="less">
	.department {
		.change-list-order {
			margin-bottom: 15px;
		}
		.add-department, .edit-department {
			font-size: 15px;
		    padding-left: 5%;
		    margin-top: 20px;
	     	.title {
		    	margin-right: 15px;
		    	width: 80px;
		    	text-align: right;
		    	display: inline-block;
		    }
			.el-input, .el-textarea {
				width: 217px;
			}
		}
	}
</style>


