<!-- 问卷添加与编辑 -->
<template>
	<div class="investigation-edit">
		<div class="add-investigation">
			<span class="title">问卷名称</span>
			<el-input v-model="investigationName" placeholder="请输入名称"></el-input>
		  	<span class="required">*</span>
		</div>
		<div class="add-investigation" style="width: 50%; margin-top: 30px;">
			<span class="title">设置问卷内容</span>
			<el-button @click="addQuestion()">增加问题</el-button>
			
			<div class="el-table--border" style="margin-top: 15px;" v-for="(item, index) in content" :key="index">
				<table class="el-table" style="border-collapse: collapse;">
					<thead>
						<tr>
							<th class="is-leaf"><div class="cell">问卷内容</div></th>
							<th class="is-leaf" style="width: 150px;"><div class="cell">问卷类型</div></th>
							<th class="is-leaf"><div class="cell">是否必填</div></th>
							<th class="is-leaf"><div class="cell">操作</div></th>
						</tr>
					</thead>
					<tbody>
						<tr>
							<!-- 问题标题 -->
							<td>
								<div class="cell"><el-input v-model="item.name" placeholder="标题"></el-input></div>
							</td>
							<td>
								<div class="cell">
									<el-select v-model="item.type" placeholder="请选择">
									    <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
									    </el-option>
								  	</el-select>
								</div>
							</td>
							<td>
								<div class="cell">
									<el-checkbox v-model="item.is_must"></el-checkbox>
								</div>
							</td>
							<td>
								<div class="cell">
									<el-button icon="el-icon-delete" @click="deleteQuestionLine(index)"></el-button>
								</div>
							</td>
						</tr>
						<tr v-for="(item2, index2) in item.children">
							<!-- 选项内容 -->
							<td>
								<div class="cell" style="margin-left: 30px;">
									<el-input v-model="item2.name" placeholder="选项名称"></el-input>
								</div>
							</td>
							<td><div class="cell">{{content[index].type == 1 ? '单项选项' : '多项选项'}}</div></td>
							<td>
								<div class="cell"></div>
							</td>
							<td>
								<div class="cell">
									<el-button icon="el-icon-delete" @click="deleteOptionLine(index, index2)"></el-button>
								</div>
							</td>
						</tr>
						<tr>
							<td>
								<div class="cell" style="text-align: left; margin-left: 60px;">
									<el-button icon="el-icon-plus" @click="addOptionLine(index)">增加选项</el-button>
								</div>
							</td>
							<td><div class="cell"></div></td>
							<td><div class="cell"></div></td>
							<td><div class="cell"></div></td>
						</tr>
					</tbody>
				</table>
			</div>
		</div>
		<div class="add-investigation">
			<el-button type="primary" @click="addOrEdit()">添加</el-button>
			<el-button @click="goBack()">返回</el-button>
		</div>
	</div>
</template>

<script>
	import apis from '../../apis'

	export default {
		inject: ['reload', 'reloadTab'],
		data() {
			return {
				investigationName: '',
				content: [{'name': '', 'children': [{'name': ''}], 'is_must': true, 'type': 1}],
				options: [{label: '单选', value: 1}, {label: '多选', value: 2}],
				investigationId: Number,
			}
		},
		methods: {
			// 增加问题
			addQuestion() {
				this.content.push({'name': '', 'children': [{'name': ''}], 'is_must': true, 'type': 1});
			},
			// 删除问题
			deleteQuestionLine(inx) {
            	this.content.splice(inx, 1);
			},
			// 增加选项
			addOptionLine(inx) {
				this.content[inx].children.push({'name': ''});
			},
			// 删除选项
			deleteOptionLine(inx, inx2) {
				this.content[inx].children.splice(inx2, 1);
			},
			// 返回父组件
			goBack() {
				this.$emit("change", "first");
			},
			// 添加 Or 编辑
			addOrEdit() {
				for(var i = 0; i < this.content.length; i++) {
					if(this.content[i].name == '') {
						this.$alert('问题标题不能为空');
						return;
					}
					for(var k = 0; k < this.content[i].children.length; k++) {
						if(this.content[i].children[k].name == '') {
							this.$alert('选项内容不能为空');
							return;
						}
					}
				}
				if(this.$root.propType == 'add') {
					apis.investigationAdd({questionnaire_title: this.investigationName, content: this.content, token: this.$root.token}, function(bck) {
						alertMessage(this, bck.info, bck.code);
					}.bind(this))
				}
				else {
					apis.investigationEdit({id: this.investigationId, questionnaire_title: this.investigationName, content: this.content, token: this.$root.token}, function(bck) {
						alertMessage(this, bck.info, bck.code);
					}.bind(this))
				}
				function alertMessage(_this, info, code) {
					_this.$alert(info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	if(code != 0) {
			            		_this.reload();
			            	}
			            	else if(info == '您还没有登录！！') {
			            		location.reload();
			            	}
			          	}
			        });
				}
			}
		},
		created() {
			// 父组件点击编辑后进入组件，把得到的数据渲染
			if(this.$root.propType == 'add') {

			}
			else {
				var questionnaire = JSON.parse(JSON.stringify(this.$root.editInvestigation));
				this.content = [];
				this.investigationName = questionnaire.questionnaire_title;
				this.investigationId = questionnaire.id;
				for(var i = 0; i < questionnaire.content.length; i++) {
					this.content.push({'name': questionnaire.content[i].name, 'children': questionnaire.content[i].children, 'is_must': questionnaire.content[i].is_must, 'type': Number(questionnaire.content[i].type)});
				}
			}
		}
	}
</script>

<style lang="less">
	.investigation-edit {
		.add-investigation {
		    font-size: 15px;
		    padding-left: 5%;
		    margin-top: 20px;
		    .title {
		    	margin-right: 15px;
		    }
		    .el-input {
		    	width: auto;
		    }
		    .cell {
		    	text-align: center;
		    	.el-input {
		    		width: 100%;
		    	}
		    }
		    .el-table::before {
			    display: none;
			}
			table tbody>tr:nth-child(even) {
				background: #fafafa;
			}
		}
	}
</style>


