<template>
  <div id="app">
  
    <transition name="el-fade-in-linear" v-show="isLogin == 1">
      <div v-if="isLogin == 1">
        <header class="index-header flex">
          <div class="logo flex">
            <div @click="junpToIndex()"><img src="./assets/logo.png"></div>
          </div>
          <div class="user">
            <el-dropdown trigger="click" @command="handleCommand">
              <span class="el-dropdown-link user-name">
                欢迎，{{name}}<i class="el-icon-arrow-down el-icon--right"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                <!-- <el-dropdown-item><i class="fa fa fa-user" style="margin-right: 10px;"></i>个人信息</el-dropdown-item> -->
                <el-dropdown-item command="logout"><i class="fa fa-sign-out" style="margin-right: 10px;"></i>登出</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
        </header>
        <div class="header-block"></div>
        <div>
          <tabs v-if="reloadTabs"></tabs>
          <router-view v-if="reloadContent"/>
        </div>
      </div>
    </transition>
    
      <!-- 登录 -->
      <div class="login-bcg" v-if="isLogin == 0">
        <div class="login">
          <h2 class="text-center" style="margin-top:0; color: #fff;">德道口腔网站管理后台</h2>
          <el-input placeholder="用户名" v-model="userName"></el-input>
          <el-input placeholder="密码" type="password" v-model="password"></el-input>
          <div class="text-center code" v-if="reloadCode">
            <img :src="img" @click="getCode()" alt="获取验证码失败">
          </div>
          <el-input placeholder="请输入验证码" v-model="verificationCode" @keyup.enter.native="login('keyup')"></el-input>
          <el-select v-model="value" placeholder="请选择" @change="changeSelect">
            <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
          </el-select>
          <el-button type="primary" @click="login()">登录</el-button>
        </div>
      </div>
  </div>
</template>

<script>
  import apis from './apis'
  import tabs from '@/views/tabs/tabs'

export default {
  name: 'App',
  components: {
    tabs
  },
  provide() {
    return {
      reload: this.reload,
      reloadTab: this.reloadTab
    }
  },
  data() {
    return {
      reloadContent: true,
      reloadTabs: true,
      reloadCode: true,
      isLogin: 0,
      userName: '',
      userId: '',
      password: '',
      verificationCode: '',
      canLogin: false,
      url: 'http://admin.dedaokq.com/',
      img: '',
      clickEnter: true,
      name: '',
      options: [{label: '成都', value: '1'}, {label: '深圳', value: '2'}],
      value: '1',
    }
  },
  methods: {
    // 重加载组件
    reload() {
      this.reloadContent = false;
      this.$nextTick(function() {
        this.reloadContent = true;
      })
    },
    // 重加载tab
    reloadTab() {
      this.reloadTabs = false;
      this.$nextTick(function() {
        this.reloadTabs = true;
      })
    },
    // 跳回首页
    junpToIndex() {
      this.$router.push({path:'/'});
    },
    // 获取验证码
    getCode() {
      this.img = this.url + 'admin/Login/captcha?' + Math.random();
      this.verificationCode = '';
    },
    // 登录
    login(type) {
      if(type == 'keyup') {
        if(!this.clickEnter) {
          this.clickEnter = true;
          return;
        }
        this.clickEnter = false;
      }
      var self = this;
      this.userName == '' ? alertMsg('请输入用户名') : this.password == '' ? alertMsg('请输入密码') : this.verificationCode == '' ? alertMsg('请输入验证码') : this.canLogin = true;

      if(this.canLogin) {
        apis.login({username: this.userName, password: this.password, captcha: this.verificationCode, db: this.value}, function(bck) {
          if(bck.code == 0) {
            this.img = this.url + 'admin/Login/captcha?' + Math.random();
            this.verificationCode = '';
            this.canLogin = false;
            alertMsg(bck.info);
          }
          else {
            this.isLogin = 1;
            this.name = this.userName;
            this.userId = bck.user_id;
            var userinfo = {'name': this.name, 'id': this.userId, 'db_name': bck.db_name, 'homeUrl': bck.website};
            localStorage.setItem('userInfo', JSON.stringify(userinfo));
            this.userName = '';
            this.password = '';
            this.verificationCode = '';
            this.clickEnter = true;
            this.$root.token = bck.token;
          }
        }.bind(this))
      }
      function alertMsg(text) {
        self.$alert(text, {confirmButtonText: '确定'});
      }
    },
    // 登出
    handleCommand(command) {
      if(command == 'logout') {
        apis.logout({id: this.userId}, function(bck) {
          if(bck.code == 1) {
            this.isLogin = 0;
            this.canLogin = false;
            this.img = this.url + 'admin/Login/captcha?' + Math.random();
            localStorage.removeItem('userInfo');
            this.$router.push({path:'/'});
          }
          else {
            this.$alert(bck.info);
          }
        }.bind(this))
      }
    },
    // 选项改变
    changeSelect(value) {
      this.value = value;
    }
  },
  created() {
    var info;
    if(localStorage.getItem('userInfo')) {
      info = JSON.parse(localStorage.getItem('userInfo'));
      this.name = info.name;
      this.userId = info.id;

      // 检查登录
      apis.checkLogin({username: this.name}, function(bck) {
        if(bck.code == 1) {
          this.isLogin = 1;
          this.$root.token = bck.token;
        }
        else {
          this.isLogin = 0;
        }
      }.bind(this))
    }
    
    this.img = this.url + 'admin/Login/captcha?' + Math.random();
  }
}
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
  }
  body, html, ul {
    margin: 0;
    padding: 0;
  }
  ul {
    list-style: none;
  }
  i {
    display: inline-block;
    font-family: normal;
  }
  img {
    vertical-align: middle;
  }
  input::-webkit-outer-spin-button, input::-webkit-inner-spin-button {
    -webkit-appearance: none !important;
  }
  input[type="number"] {
    -moz-appearance:textfield;
  }
  .flex {
    display: flex;
  }
  .index-header {
    background: #c11920;
    color: #fff;
    justify-content: space-between;
    line-height: 65px;
    height: 65px;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 2035;
  }
  .logo {
    padding-left: 2%;
    width: 35%;
  }
  .logo>div {
    cursor: pointer;
    font-size: 18px;
  }
  .logo>div img {
    margin-right: 15px;
  }
  .user {
    cursor: pointer;
    margin-right: 3%;
  }
  .index-header .user-name {
    color: #fff;
    display: block;
    user-select: none;
  }
  .header-block {
    padding-top: 65px;
    width: 100%;
  }
  .right-content {
    padding-left: 210px;
  }
  .right-content .content {
    padding: 5px 25px;
  }
  .required {
    margin-left: 10px;
    color: red;
  }
  .text-center {
    text-align: center;
  }
  .login {
    margin: 12% auto;
    overflow: hidden;
    width: 530px;
    text-align: center;
    border-radius: 20px;
    padding: 1% 0;
    z-index: 1;
    position: relative;
  }
  .login .el-input, .login .el-button {
    width: 320px;
  }
  .login .el-input, .login .code, .login .el-button {
    margin-bottom: 15px;
  }
  .login .code {
    height: 62px;
  }
  .login .code img {
    cursor: pointer;
  }
  .login-bcg {
    background: linear-gradient(90deg, #496eaa, #944fa8, #a8804f, #496eaa);
    background-size: 1400% 300%;
    top: 10px;
    left: 10px;
    animation: bcg 15s ease infinite;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    width: 100%;
  }
  @keyframes bcg {
    0% {background-position: 0% 0%;}
    50% {background-position: 50% 100%;}
    100% {background-position: 100% 0%;}
  }
  .status {
    text-align: center;
  }
</style>



