<!-- 回收站 -->
<template>
	<div class="right-content recycle">
		<el-tabs v-model="activeName" class="content">
			<el-tab-pane label="回收站" name="first">
				<div class="search">
					<span style="margin-left: 15px;">关键词：</span>
					<el-input v-model="keyword" placeholder="请输入内容" clearable></el-input>
					<span style="margin-left: 15px;">日期：</span>
					<el-date-picker v-model="date" type="daterange" align="center" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
					<el-button @click="searchRecycData()" type="primary" size="medium" icon="el-icon-search" class="search-btn">搜索</el-button>
				</div>

				<el-table :data="recycleList" style="width: 100%" :border='true' v-loading="loading">
					<el-table-column label="ID" prop="id" width="80"></el-table-column>
				 	<el-table-column label="标题" prop="title"></el-table-column>
					<el-table-column label="分类" prop="category" width="130"></el-table-column>
				 	<el-table-column label="删除人" prop="author" width="80"></el-table-column>
				 	<el-table-column label="删除时间" prop="delete_time" width="170"></el-table-column>
				 	<el-table-column label="操作" width="100">
		  				<template slot-scope="scope">
							<el-button type="text" size="small" @click="reductionRecycData(scope.row)">还原</el-button>
	    					<el-button type="text" size="small" @click="deleteRecycleData(scope.row)">删除</el-button>
		  				</template>
					</el-table-column>
				</el-table>

				<el-pagination layout="prev, pager, next" :total="total" class="page" :page-size="pageSize" @current-change="currentChange" :current-page.sync="currentPage" v-if="total != 0"></el-pagination>
			</el-tab-pane>
		</el-tabs>
	</div>
</template>

<script>
	import apis from '../../apis'

	export default {
		inject: ['reload', 'reloadTab'],
		data() {
			return {
				activeName: 'first',
				loading: true,
				recycleList: [],
				keyword: '',
				date: '',
				total: 0,
				pageSize: 0,
				page: 1,
				search: false,
				currentPage: 1,
			}
		},
		methods: {
			// 删除
			deleteRecycleData(row) {
				var self = this;
				self.$confirm('确认要删除【'+ row.title +'】吗？', '确认信息', {
		          	distinguishCancelAndClose: true,
		          	confirmButtonText: '确定',
		          	cancelButtonText: '取消'
		        }).then(() => {
		        	// 确认后执行
					apis.recycDataDelete({id: row.id, token: this.$root.token}, function(bck) {
						self.$options.methods.alertMsg(self, bck.info, bck.code);
					}.bind(this))
				}).catch(action => {})
			},
			// 还原
			reductionRecycData(row) {
				apis.recycDataReduction({id: row.id, token: this.$root.token}, function(bck) {
					this.$options.methods.alertMsg(this, bck.info, bck.code);
				}.bind(this))
			},
			// 翻页
			currentChange(page) {
				this.loading = true;
				var startTime, endTime, keyword;
            	if(this.search) {
            		this.date != '' ? (startTime = this.date[0]) | (endTime = this.date[1]) : '';
            		this.keyword != '' ? keyword = this.keyword : '';
            	}
            	this.page = page;
            	apis.recycleList({start_time: startTime, end_time: endTime, keyword: keyword, page: this.page, token: this.$root.token}, function(data) {
					data ? this.loading = false : '';					
					if(data.code == 0) {
						this.$alert(data.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				            	location.reload();
				          	}
				        });
					}
	            	else {
	            		this.recycleList = data.data;
	            		this.total = data.total;
						this.pageSize = data.per_page;
	            	}
				}.bind(this))
			},
			// 搜索
			searchRecycData() {
				this.date === null ? this.date = '' : '';
	      		this.loading = true;
	      		apis.recycleList({start_time: this.date[0], end_time: this.date[1], keyword: this.keyword, token: this.$root.token}, function(data) {
					data ? this.loading = false : '';
					
					if(data.code == 0) {
						this.$alert(data.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				            	location.reload();
				          	}
				        });
					}
	            	else {
	            		this.recycleList = data.data;
	            		this.total = data.total;
						this.pageSize = data.per_page;
			    		if(this.date !== null || this.keyword != '') {
							this.search = true;
						}
						else {
							this.search = false;
						}
						this.currentPage = 1;
	            	}
				}.bind(this))
			},
			// 消息提示框，公共部分
	      	alertMsg: function(_this, info, code) {
		    	_this.$alert(info, {
	         	 	confirmButtonText: '确定',
		          	callback: action => {
		            	if(info == '您还没有登录！！') {
		            		location.reload();
		            	}
		            	else if(code == 1) {
		            		_this.reload();
		            	}
		          	}
		        });
		    }
		},
		created() {
			apis.recycleList({token: this.$root.token}, function(data) {
				data ? this.loading = false : '';
				
				if(data.code == 0) {
					this.$alert(data.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	location.reload();
			          	}
			        });
				}
            	else {
            		this.recycleList = data.data;
            		this.total = data.total;
					this.pageSize = data.per_page;
            	}
			}.bind(this))
		}
	}
</script>

<style lang="less">
	.recycle {
		.deletes {
			margin-bottom: 15px;
		}
		.search {
			font-size: 15px;
			margin-bottom: 15px;
			.el-input {
				width: auto;
			}
			.search-btn {
				margin-left: 15px;
			}
		}
		.page {
			text-align: center;
			margin-top: 15px;
		}
	}
</style>


