<!-- 邮箱配置 -->
<template>
	<div class="right-content mailer">
		<div class="content">
			<div class="mailer-content">
				<div class="from-name">
					<span class="mailer-title">发件人</span>
					<el-input v-model="fromName"></el-input>
					<span class="required">*</span>
				</div>
				<div class="e-mail-address">
					<span class="mailer-title">邮箱地址</span>
					<el-input v-model="eMailAddress"></el-input>
					<span class="required">*</span>
				</div>
				<div class="from-host">
					<span class="mailer-title">SMTP服务器</span>
					<el-input v-model="fromHost"></el-input>
					<span class="required">*</span>
				</div>
				<div class="smtp-secure">
					<span class="mailer-title">连接方式(ssl,tls)</span>
					<el-input v-model="smtpSecure"></el-input>
					<span class="required">*</span>
				</div>
				<div class="from-port">
					<span class="mailer-title">SMTP服务器端口</span>
					<el-input v-model="fromPort"></el-input>
					<span class="required">*</span>
				</div>
				<div class="user-name">
					<span class="mailer-title">发件箱帐号</span>
					<el-input v-model="userName"></el-input>
					<span class="required">*</span>
				</div>
				<div class="password">
					<span class="mailer-title">授权码</span>
					<el-input v-model="password"></el-input>
					<span class="required">*</span>
				</div>
			</div>
  			<div class="submit-btn"><el-button type="primary" @click="saveMailer()">保存</el-button></div>
		</div>
	</div>
</template>

<script>
	import apis from '../../apis'

	export default {
		data() {
			return {
				fromName : '',
				eMailAddress : '',
				fromHost : '',
				smtpSecure : '',
				fromPort : '',
				userName : '',
				password : '',
			}
		},
		methods: {
			saveMailer() {
				apis.mailerPost({from_name: this.fromName, from: this.eMailAddress, host: this.fromHost, smtp_secure: this.smtpSecure, port: this.fromPort, username: this.userName, password: this.password, token: this.$root.token}, function(bck) {
					if(bck.code == 1) {
						this.$alert('保存成功', {confirmButtonText: '确定'});
					}
					else {
						if(bck.info == '您还没有登录！！') {
							this.$alert(bck.info, {
				         	 	confirmButtonText: '确定',
					          	callback: action => {
					            	location.reload();
					          	}
					        });
					    }
					    else {
					    	this.$alert('保存成功', {confirmButtonText: '确定'});
					    }
					}
				}.bind(this))
			}
		},
		created() {
			apis.getMailer({token: this.$root.token}, function(bck) {
				if(bck.code == 0) {
					this.$alert(bck.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	location.reload();
			          	}
			        });
				}
				else {
					this.fromName = bck.from_name;
					this.eMailAddress = bck.from;
					this.fromHost = bck.host;
					this.smtpSecure = bck.smtp_secure;
					this.fromPort = bck.port;
					this.userName = bck.username;
					this.password = bck.password;
				}
			}.bind(this))
		}
	}
</script>

<style lang="less">
	.mailer {
		.mailer-content {
			font-size: 15px;
			padding-left: 4%;
			.mailer-title {
				width: 120px;
				margin-right: 15px;
				text-align: right;
				display: inline-block;
			}
			.el-input {
				width: auto;
			}
			.from-name, .e-mail-address, .from-host, .smtp-secure, .from-port, .user-name, .password {
				margin-top: 15px;
			}
		}
		.submit-btn {
			padding: 15px 0 0 7%;
		}
	}
</style>


