<!-- 幻灯片管理 -->
<template>
	<div class="right-content">
  		<el-tabs v-model="activeName" @tab-click="changeTab" class="content slide-content">

		    <el-tab-pane label="幻灯片管理" name="first">

				<el-select v-model="slideClassId" @change="slideClassSelect" class="slide-class-select">
				    <el-option v-for="item in slideClassList" :key="item.id" :label="item.name" :value="item.id"></el-option>
				</el-select>

				<!-- 修改按钮 -->
				<el-button @click="changeListOrder()" type="primary" plain class="change-list-order">修改显示顺序</el-button>

				<el-table :data="slideList" style="width: 100%" :border='true' v-loading="loading">
					<el-table-column label="显示顺序" width="100">
	    				<template slot-scope="scope">
	    					<el-input v-model="listOrder[scope.$index]"  type="number"></el-input>
	    				</template>
	    			</el-table-column>
	    			<el-table-column label="ID" prop="id" width="50"></el-table-column>
      				<el-table-column label="标题" prop="title"></el-table-column>
      				<el-table-column label="链接" prop="url"></el-table-column>
      				<el-table-column label="显示状态" width="80">
	    				<template slot-scope="scope">
	    					<div>{{slideList[scope.$index].status == 1 ? '显示' : '隐藏'}}</div>
	    				</template>
	    			</el-table-column>
      				<el-table-column label="图片" width="80">
      					<template slot-scope="scope">
	        				<el-button type="text" size="small" @click="checkImg(scope.row)">查看</el-button>
	      				</template>
      				</el-table-column>
	    			<el-table-column label="操作" width="180">
	      				<template slot-scope="scope">
	        				<el-button type="text" size="small" @click="changeStatus(scope.row)">{{scope.row.status == 0 ? '显示' : '隐藏'}}</el-button>
	        				<el-button type="text" size="small" @click="openSlideEdit(scope.row)">编辑</el-button>
	    					<el-button type="text" size="small" @click="deleteSlide(scope.row)">删除</el-button>
	      				</template>
	    			</el-table-column>

    			</el-table>
		    </el-tab-pane>

		    <el-tab-pane label="添加幻灯片" name="second" class="flex">
		    	<div class="slide-add">
		    		<div class="slide-class-select">
						<span class="title">分类</span>
						<el-select v-model="slideClassId2" @change="slideClassSelect">
						    <el-option v-for="item in slideClassList" :key="item.id" :label="item.name" :value="item.id"></el-option>
						</el-select>
						<span class="required">*</span>
					</div>
					<div class="slide-add-title">
						<span class="title">标题</span>
						<el-input v-model="addSlideName" placeholder="请输入幻灯片标题"></el-input>
						<span class="required">*</span>
					</div>
					<div class="slide-add-title">
						<span class="title">链接</span>
						<el-input v-model="addSlideHref" placeholder="请输入幻灯片链接"></el-input>
					</div>
					<div class="slide-add-title">
						<span class="title">描述</span>
						<el-input v-model="addSlideRemark"></el-input>
					</div>
					<div class="slide-add-title">
  						<el-button type="primary" @click="addSubmit()">添加</el-button>
						<el-button @click="goBack()">返回</el-button>
					</div>
		    	</div>
		    	<div class="img-upload flex">
		    		<div><span class="title">幻灯片</span></div>
		    		<div class="preview-img" v-if="base64 != ''">
						<img :src="base64" class="slide-add-title">
						<el-button @click="destoryFile()" v-if="base64 != ''">取消图片</el-button>
					</div>
					<div>
						<div class="el-upload el-upload--picture-card" v-if="base64 == ''">
							<i class="el-icon-plus"></i>
							<input type="file" @change="fileImage" accept="image/jpeg,image/x-png,image/gif">
						</div>
					</div>
		    	</div>
		    </el-tab-pane>

		    <el-tab-pane label="幻灯片分类管理" name="third">
		    	<el-button type="primary" class="add-slide-class" @click="addSlideClass()">添加分类</el-button>
		    	<el-table :data="slideClassList" style="width: 100%" :border='true' v-loading="loading">
	    			<el-table-column label="ID" prop="id" width="50"></el-table-column>
      				<el-table-column label="分类名称" prop="name"></el-table-column>
      				<el-table-column label="描述" prop="remark" width="270"></el-table-column>
	    			<el-table-column label="操作" width="180">
	      				<template slot-scope="scope">
	        				<el-button type="text" size="small" @click="openSlideClassEdit(scope.row)">编辑</el-button>
	    					<el-button type="text" size="small" @click="slideClassDelete(scope.row)">删除</el-button>
	      				</template>
	    			</el-table-column>

    			</el-table>
		    </el-tab-pane>
			
			<!-- 弹窗图片查看 -->
			<el-dialog :visible.sync="dialogImgVisible" width="40%">
			  	<img :src="imgUrl" width="100%">
			</el-dialog>

			<!-- 幻灯片编辑 -->
			<el-dialog title="编辑幻灯片" :visible.sync="dialogEditVisible" width="40%" @close="dialogEditClose">
				<div class="flex">
					<div class="slide-edit">
						<div class="slide-class-select slide-edit-title">
							<span class="title">分类</span>
							<el-select v-model="slideClassId3" @change="slideClassSelect">
							    <el-option v-for="item in slideClassList" :key="item.id" :label="item.name" :value="item.id"></el-option>
							</el-select>
						</div>
					  	<div class="slide-edit-title">
							<span class="title">标题</span>
							<el-input v-model="editSlideName"></el-input>
		    				<span class="required">*</span>
						</div>
						<div class="slide-edit-title">
							<span class="title">链接</span>
							<el-input v-model="editSlideHref"></el-input>
						</div>
						<div class="slide-edit-title">
							<span class="title">描述</span>
							<el-input v-model="editSlideRemark"></el-input>
						</div>
						<div class="slide-edit-title">
							<span class="title">图片</span>
							<el-button style="position:relative;">更换图片
								<input class="upload-btn" type="file" @change="fileImage" accept="image/jpeg,image/x-png,image/gif" name="edit">
							</el-button>
						</div>
					</div>
					<div style="width: 50%;">
						<div class="title">图片预览</div>
						<div class="text-center">
							<img :src="base64 != '' ? base64 : imgUrl" width="80%" style="margin-top: 15px;">
						</div>
					</div>
				</div>
			  	<span slot="footer" class="dialog-footer">
			    	<el-button @click="dialogEditVisible = false">取 消</el-button>
			    	<el-button type="primary" @click="editSubmit()">确 定</el-button>
			  	</span>
			</el-dialog>

			<!-- 分类添加 & 编辑 -->
			<el-dialog title="幻灯片分类" :visible.sync="slideClassDialog" width="30%">
				<div class="slide-class-edit">
				  	<div>
						<span class="title">标题</span>
						<el-input v-model="slideClassName" placeholder="请输入分类标题"></el-input>
		    			<span class="required">*</span>
					</div>
					<div>
						<span class="title">描述</span>
						<el-input v-model="slideClassRemark"></el-input>
					</div>
				</div>
			  	<span slot="footer" class="dialog-footer">
			    	<el-button @click="slideClassDialog = false">取 消</el-button>
			    	<el-button type="primary" @click="slideClassSubmit()">确 定</el-button>
			  	</span>
			</el-dialog>
  		</el-tabs>
  	</div>
</template>

<script>
	import apis from '../../apis'

	export default {
		inject: ['reload', 'reloadTab'],
		data() {
			return {
				activeName: 'first',
				loading: true,
				dialogImgVisible: false,
				dialogEditVisible: false,
				slideClassDialog: false,
				slideClassId: '',
				slideClassId2: '',
				slideClassId3: '',
				slideClassList: [],
				slideList: [],
				listOrder: [],
				cloneListOrder: [],
				listOrderId: [],
				slideListOrderArr: [],
				originalListOrderArr: [],
				imgUrl: '',
				slideListStatus: 0,
				addSlideName: '',
				addSlideHref: '',
				addSlideRemark: '',
				slideId: 0,
				editSlideName: '',
				editSlideHref: '',
				editSlideRemark: '',
				slideClassName: '',
				slideClassRemark: '',
				slideClassTargetId: 0,
				slideClassAdd: false,
				url: 'http://admin.dedaokq.com/',
				base64: '',
			}
		},
		methods: {
			//改变头部选项卡
			changeTab(tab, event) {
				if(tab.index == 0) {
					this.addSlideName = '';
					this.addSlideHref = '';
					this.addSlideRemark = '';
					this.base64 = '';
				}
			},
			// 幻灯片选择
			slideClassSelect() {
				apis.slideList({id: this.slideClassId, token: this.$root.token}, function(data) {
					if(data.code==0) {
            			this.$alert(data.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				            	location.reload();
				          	}
				        });
            		}
            		else {
            			this.slideList = data;
            			this.listOrder=[];
            			this.listOrderId=[];
            			for(var i = 0; i < data.length; i++) {
            				this.listOrderId.push(data[i].id);
							this.listOrder.push(data[i].list_order);
						}
            		}
				}.bind(this))
			},
			// 查看图片
			checkImg(row) {
				this.imgUrl = '';
				this.dialogImgVisible = true;
				this.imgUrl = row.image;
			},
			// 显示 & 隐藏
			changeStatus(row) {
				row.status == 1 ? row.status = 0 : row.status = 1;
				apis.slideHide({id: row.id, status: row.status, token: this.$root.token}, function(bck) {
					this.$alert(bck.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	if(bck.code != 0) {
			            apis.slideList({id: row.slide_id, token: this.$root.token}, function(data) {
						this.slideList = data;
					}.bind(this))
			            		
			            	}
			            	else {
			            		location.reload();
			            	}
			          	}
			        });
				}.bind(this))
			},
			// 上传图片
		    fileImage:function(e) {
		    	var self = this;
                var file = e.target.files[0];
                var reader = new FileReader();
                reader.readAsDataURL(file); // base64
                reader.onloadend = function () {
                    // 图片的 base64 格式
                    self.base64 = reader.result;
                };
            },
            // 取消图片
            destoryFile() {
            	this.base64 = '';
            },
            // 删除幻灯片
            deleteSlide(row) {
            	apis.slideDelete({id: row.id, token: this.$root.token}, function(bck) {
            		this.$alert(bck.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			          		if(bck.code == 0) {
			          			location.reload();
			          		}
			          		else {
			          	apis.slideList({id: row.slide_id, token: this.$root.token}, function(data) {
						this.slideList = data;
						}.bind(this))
			          		}
			          	}
			        });
            	}.bind(this))
            },
            // 添加
            addSubmit() {
            	apis.slideAddPost({slide_id: this.slideClassId2, title: this.addSlideName, image: this.base64, url: this.addSlideHref, description: this.addSlideRemark, token: this.$root.token}, function(bck) {
            		this.$alert(bck.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			          		if(bck.code == 0) {
			          			if(bck.info == '您还没有登录！！') {
			          				location.reload();
			          			}
			          		}
			          		else {
			          			this.reload();
			          		}
			          	}
			        });
            	}.bind(this))
            },
            // 返回
            goBack() {
            	this.addSlideName = '';
				this.addSlideHref = '';
				this.addSlideRemark = '';
				this.activeName = 'first';
				this.base64 = '';
            },
            // 打开编辑
            openSlideEdit(row) {
            	this.dialogEditVisible = true;
            	this.slideId = row.id;
            	this.slideClassId3 = row.slide_id;
            	this.editSlideName = row.title;
            	this.editSlideHref = row.url;
            	this.editSlideRemark = row.description;
            	this.imgUrl = row.image;
            },
            // 关闭编辑
            dialogEditClose(done) {
            	this.base64 = '';
            },
            // 提交编辑
            editSubmit() {
            	var img = '';
            	this.base64 != '' ? img = this.base64 : img = this.imgUrl;
            	apis.slideEditPost({id: this.slideId, slide_id: this.slideClassId3, title: this.editSlideName, url: this.editSlideHref, description: this.editSlideRemark, image: img, token: this.$root.token}, function(bck) {
            		bck.info == '保存失败' ? bck.info = '保存成功' : '';
            		this.$alert(bck.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			          		if(bck.code == 0) {
			          			if(bck.info == '您还没有登录！！') {
			          				location.reload();
			          			}
			          			else {
			          				this.dialogEditVisible = false;
			          				apis.slideList({id: this.slideClassId3, token: this.$root.token}, function(data) {
									this.slideList = data;
								}.bind(this))
			          			}
			          		}
			          		else {
			          			this.dialogEditVisible = false;
			          			apis.slideList({id: this.slideClassId3, token: this.$root.token}, function(data) {
									this.slideList = data;
								}.bind(this))
			          		}
			          	}
			        });
            	}.bind(this))
            },
            // 排序幻灯片
            changeListOrder() {
            	var l_order = this.listOrder;
		    	var id = this.listOrderId;
		    	// 将更改前和更改后发给后台做数据对比修改显示顺序
				for(var i = 0; i < id.length; i++) {
					
					this.slideListOrderArr.push({id: JSON.parse(JSON.stringify(id))[i], list_order: JSON.parse(JSON.stringify(l_order))[i]});
					this.originalListOrderArr.push({id: JSON.parse(JSON.stringify(id))[i], list_order: JSON.parse(JSON.stringify(this.cloneListOrder))[i]});
				}

            	apis.slideListOrders({list_order: this.slideListOrderArr, original_list_order: this.originalListOrderArr, token: this.$root.token}, function(bck) {
            		bck.info == '排序失败' ? bck.info = '排序成功' : '';
					this.$alert(bck.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			          		if(bck.info == '您还没有登录！！') {
			            		location.reload();
							}
							else if(bck.code == 1) {
								this.reload();
								this.reloadTab();
							}
			          	}
			        });
            	}.bind(this))
            },
            // 打开添加导航分类
            addSlideClass() {
            	this.slideClassAdd = true;
            	this.slideClassDialog = true;
				this.slideClassRemark = '';
            	this.slideClassName = '';
            },
            // 打开幻灯片分类编辑
            openSlideClassEdit(row) {
            	this.slideClassAdd = false;
            	this.slideClassDialog = true;
            	this.slideClassRemark = row.remark;
            	this.slideClassName = row.name;
            	this.slideClassTargetId = row.id;
            },
            // 幻灯片分类编辑 & 添加，提交
            slideClassSubmit() {
            	var self = this;
            	// 添加
            	if(this.slideClassAdd) {
            		apis.slideClassAdd({name: this.slideClassName, remark: this.slideClassRemark, token: this.$root.token}, function(bck) {
            			alertMsg(bck.code, bck.info)
            		}.bind(this))
            	}
            	// 编辑
            	else {
            		apis.slideClassEdit({id: this.slideClassTargetId, name: this.slideClassName, remark: this.slideClassRemark, token: this.$root.token}, function(bck) {
            			alertMsg(bck.code, bck.info);
            		}.bind(this))
            	}
            	function alertMsg(code, info){
            		if(code == 0) {
						self.$alert(info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				          		if(info == '您还没有登录！！') {
				          			location.reload();
				          		}
				          	}
				       });
					}
					else {
						self.slideClassDialog = false;
						apis.slideClass({token: self.$root.token}, function(data) {
							self.slideClassList = data;
							self.slideClassId = data[0].id;
							self.slideClassId2 = data[0].id;
						})
						self.$alert(info);
					}
            	}
            },
            // 删除幻灯片分类
            slideClassDelete(row) {
            	apis.slideClassDelete({id: row.id, token: this.$root.token}, function(bck) {
					this.$alert(bck.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			          		if(bck.info == '您还没有登录！！') {
			          			location.reload();
			          		}
			          		else {
			          			this.reload();
			          		}
			          	}
			        });
            	}.bind(this))
            }
		},
		created() {
			apis.slideClass({token: this.$root.token}, function(data) {
				data ? this.loading = false : '';
				if(data.code == 0) {
					this.$alert(data.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	location.reload();
			          	}
			        });
				}
				else {
					this.slideClassList = data;
					this.slideClassId = data[0].id;
					this.slideClassId2 = data[0].id;
					apis.slideList({id: this.slideClassId, token: this.$root.token}, function(data) {
						this.slideList = data;

						// 把id和list-order添加进数组里，用作排序修改
						for(var i = 0; i < data.length; i++) {
							this.listOrderId.push(data[i].id);
							this.listOrder.push(data[i].list_order);
						}
						Object.assign(this.cloneListOrder, this.listOrder);
					}.bind(this))
				}
			}.bind(this))
		}
	}
</script>

<style lang="less">
	.slide-content {
		.change-list-order {
			margin-left: 15px;
		}
		.slide-class-select, .slide-add-title, .slide-edit-title, .add-slide-class, .slide-class-edit>div {
		    margin-bottom: 15px;
		}
		.slide-edit-title, .slide-class-edit {
			margin-left: 15px;
			margin-right: 15px;
		}
		.slide-add {
			font-size: 15px;
	        padding-left: 5%;
	        margin-top: 20px;
	        .el-input {
	            width: auto;
	        }
	        .el-select {
	        	.el-input {
	        		width: 202px;
	        	}
	        }
		}
		.slide-edit {
			width: 45%;
			.el-input {
	            width: auto;
	        }
	        .el-select {
	        	.el-input {
	        		width: 202px;
	        	}
	        }
		}
		.img-upload {
			margin-top: 20px;
			padding-left: 5%;
			width: 70%;
			.preview-img {
	        	width: 50%;
	        	img {
	        		width: 100%;
	        	}
	        }
			.el-upload {
			    position: relative;
			    line-height: 148px;
			}
			.el-upload input {
			    position: absolute;
			    right: 0;
			    top: 0;
			    opacity: 0;
			    cursor: pointer;
	            width: 100%;
    			height: 100%;
			}
		}
		.upload-btn {
			position: absolute;
		    right: 0;
		    top: 0;
		    opacity: 0;
		    cursor: pointer;
            width: 100%;
			height: 100%;
		}
		.slide-class-edit {
			.el-input {
				width: auto;
			}
		}
		.title {
			margin-right: 15px;
		}
	}
</style>


