<!-- 表单数据 -->
<template>
	<div class="right-content form-data">
		<div class="content">
			<div class="flex" style="flex-wrap: wrap; justify-content: space-between;">
				<el-button class="export-data" @click="exportExcel()">导出数据</el-button>
				<div class="search">
					<span style="margin-left: 15px;">创建日期：</span>
					<el-date-picker v-model="date" type="daterange" align="center" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
					<el-button @click="searchFormData()" type="primary" size="medium" icon="el-icon-search" class="search-btn">搜索</el-button>
				</div>
			</div>
			<el-table :data="formDataList" style="width: 100%" :border='true' v-loading="loading" id="out-table">
				<el-table-column label="ID" prop="id" width="80"></el-table-column>
				<el-table-column label="姓名" width="80">
					<template slot-scope="scope">
	    				<div>{{formDataList[scope.$index].data.name}}</div>
	  				</template>
				</el-table-column>
				<el-table-column label="电话" width="130">
					<template slot-scope="scope">
	    				<div>{{formDataList[scope.$index].data.phone}}</div>
	  				</template>
				</el-table-column>
				<el-table-column label="年龄" width="80">
					<template slot-scope="scope">
	    				<div>{{formDataList[scope.$index].data.age}}</div>
	  				</template>
				</el-table-column>
				<el-table-column label="相关信息" width="130">
					<template slot-scope="scope">
	    				<div>{{formDataList[scope.$index].data.etc}}</div>
	  				</template>
				</el-table-column>
				<el-table-column label="症状">
					<template slot-scope="scope">
	    				<div>{{formDataList[scope.$index].data.symptom}}</div>
	  				</template>
				</el-table-column>
				<el-table-column label="备注" width="140">
					<template slot-scope="scope">
	    				<div>{{formDataList[scope.$index].form_name}}</div>
	  				</template>
				</el-table-column>
				<el-table-column label="页面" show-overflow-tooltip prop="page"></el-table-column>
				<el-table-column label="IP地址" prop="ip" width="130"></el-table-column>
				<el-table-column label="提交时间" prop="create_time" width="160"></el-table-column>
				<el-table-column v-if="show" label="操作" width="50">
      				<template slot-scope="scope">
    					<el-button type="text" size="small" @click="deleteFormData(scope.row)">删除</el-button>
      				</template>
    			</el-table-column>
			</el-table>
			
			<el-pagination layout="prev, pager, next" :total="total" class="page" :page-size="pageSize" @current-change="currentChange" :current-page.sync="currentPage" v-if="total != 0"></el-pagination>
		</div>
	</div>
</template>

<script>
	import apis from '../../apis'
   	import FileSaver from 'file-saver'
    import XLSX from 'xlsx'
	export default {
		inject: ['reload', 'reloadTab'],
		data() {
			return {
				loading: true,
				formDataList: [],
				page: 1,
				total: 0,
				pageSize: 0,
				date: '',
				search: false,
				currentPage: 1,
				username:'',
				show:false,
			}
		},
		methods: {
		    // 导出
		    exportExcel() {
	         	if(this.formDataList.length == 0) {
	         		this.$alert('没有数据');
	         		return;
	         	}		
	         	var wb = XLSX.utils.table_to_book(document.querySelector('#out-table'));
	         	var wbout = XLSX.write(wb, {bookType: 'xlsx', bookSST: true, type: 'array',display:true })
	         	try {
	             	FileSaver.saveAs(new Blob([wbout], { type: 'application/octet-stream' }), 'data.xlsx')
	         	} 
	         	catch(e) { if (typeof console !== 'undefined') console.log(e, wbout) }
	         	return wbout
        
		   },
            // page改变
            currentChange(page) {
            	this.loading = true;
            	var startTime, endTime;
            	if(this.search) {
            		this.date != '' ? (startTime = this.date[0]) | (endTime = this.date[1]) : '';
            	}
            	this.page = page;
            	apis.formDataList({start_time: startTime, end_time: endTime, page: page, token: this.$root.token}, function(data) {
					data ? this.loading = false : '';
					if(data.code == 0) {
						this.$alert(data.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				            	location.reload();
				          	}
				        });
					}
					else {
						this.formDataList = data.data;
						this.total = data.total;
						this.pageSize = data.per_page;
					}
				}.bind(this))
            },
	      	// 搜索
	      	searchFormData() {
	      		this.date === null ? this.date = '' : '';
	      		this.loading = true;
	      		
	      		apis.formDataList({start_time: this.date[0], end_time: this.date[1], token: this.$root.token}, function(data) {
					data ? this.loading = false : '';
					
					if(data.code == 0) {
						this.$alert(data.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				            	location.reload();
				          	}
				        });
					}
					else {
						this.formDataList = data;
						this.total = data.total;
						this.pageSize = data.per_page;
						if(this.date != '') {
							this.search = true;
						}
						else {
							this.search = false;
						}
						this.currentPage = 1;
					}
				}.bind(this))
	      	},
	      	// 删除
	      	deleteFormData(row) {
	      		this.$confirm('确认要删除数据吗？', '确认信息', {
		          	distinguishCancelAndClose: true,
		          	confirmButtonText: '确定',
		          	cancelButtonText: '取消'
		        }).then(() => {
		      		apis.formDataDelete({id: row.id, token: this.$root.token}, function(bck) {
		      			if(bck.code == 0) {
							this.$alert(bck.info, {
				         	 	confirmButtonText: '确定',
					          	callback: action => {
					            	location.reload();
					          	}
					        });
						}
						else {
							this.$alert(bck.info);
							apis.formDataList({page: this.page, token: this.$root.token}, function(data) {
								data ? this.loading = false : '';
								this.formDataList = data.data;
								this.total = data.total;
								this.pageSize = data.per_page;
							}.bind(this))
						}
		      		}.bind(this))
		      	})
	      	}
		},
		created() {
			apis.formDataList({token: this.$root.token}, function(data) {
				data ? this.loading = false : '';

				if(data.code == 0) {
					this.$alert(data.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	location.reload();
			          	}
			        });
				}
				else {
					this.formDataList = data.data;
					this.total = data.total;
					this.pageSize = data.per_page;
				}
			}.bind(this))
			var admin='admin'; var admin2='admin2';
			var userinfo = JSON.parse(localStorage.getItem('userInfo'));
			this.username=userinfo.name;
			if(this.username==admin ||this.username==admin2){
				this.show=true;
			}
		}
	}
</script>

<style lang="less">
	.form-data {
		.export-data, .search {
			margin: 15px 0;
		}
		.page {
			text-align: center;
			margin-top: 15px;
		}
		.search {
			font-size: 15px;
			.el-input {
				width: auto;
			}
			.search-btn {
				margin-left: 15px;
			}
		}
	}
</style>


