<!-- 文章管理 -->
<template>
	<div class="right-content article">
		<el-tabs v-model="activeName" @tab-click="changeTab" class="content">
			<el-tab-pane label="文章管理" name="first">

				<div class="flex" style="flex-wrap: wrap; justify-content: space-between;">
					<div>
						<!-- 修改排序 -->
						<el-button @click="changeListOrder()" type="primary" size="medium" plain class="change-list-order">修改排序</el-button>
						<!-- 批量删除 -->
						<el-button @click="deleteArticles()" type="primary" size="medium" plain class="deletes">批量删除</el-button>
						<!-- 置顶、审核、推荐 -->
						<el-dropdown trigger="click" @command="changeStatus">
						  	<el-button type="primary" size="medium" class="change-status">
						    	更改状态<i class="el-icon-arrow-down el-icon--right"></i>
						  	</el-button>
						  	<el-dropdown-menu slot="dropdown">
						    	<el-dropdown-item command="status, 1" type="primary" size="medium" plain class="status">审核</el-dropdown-item>
								<el-dropdown-item command="top, 1" type="primary" size="medium" plain class="status">置顶</el-dropdown-item>
								<el-dropdown-item command="recommended, 1" type="primary" size="medium" plain class="status">推荐</el-dropdown-item>

								<el-dropdown-item command="status, 0" type="primary" size="medium" plain class="status">取消审核</el-dropdown-item>
								<el-dropdown-item command="top, 0" type="primary" size="medium" plain class="status">取消置顶</el-dropdown-item>
								<el-dropdown-item command="recommended, 0" type="primary" size="medium" plain class="status">取消推荐</el-dropdown-item>
						  </el-dropdown-menu>
						</el-dropdown>
					</div>
					<div class="search">
						<span style="margin-left: 15px;">分类：</span>
						<el-cascader :options="articleList2" v-model="selectedOptions" change-on-select clearable></el-cascader>
						<span style="margin-left: 15px;">日期：</span>
						<el-date-picker v-model="date" type="daterange" align="center" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
						<span style="margin-left: 15px;">关键词：</span>
						<el-input v-model="keyword" placeholder="请输入内容" clearable></el-input>
						<el-button @click="searchArticle()" type="primary" size="medium" icon="el-icon-search" class="search-btn">搜索</el-button>
					</div>
				</div>

				<el-table :data="articleList" style="width: 100%" :border='true' v-loading="loading" @selection-change="selectionChange">
					<el-table-column type="selection" width="40"></el-table-column>
					<el-table-column label="显示顺序" width="100">
	    				<template slot-scope="scope">
	    					<el-input v-model="articleListOrderArr[scope.$index]" type="number"></el-input>
	    				</template>
	    			</el-table-column>
	    			<el-table-column label="ID" prop="id" width="80"></el-table-column>
	    			<el-table-column label="标题">
	    				<template slot-scope="scope">
	    					<a :href="url + 'article/' + articleList[scope.$index].id" target="_blank" :title="articleList[scope.$index].post_title" class="articleLink">{{articleList[scope.$index].post_title}}</a>
	    				</template>
	    			</el-table-column>
				 	<el-table-column label="分类" prop="category_name" width="120"></el-table-column>
				 	<el-table-column label="点击量" prop="post_hits" width="80"></el-table-column>
				 	<el-table-column label="关键词" prop="post_tag"></el-table-column>
				 	<el-table-column label="作者" prop="author" width="100"></el-table-column>
				 	<el-table-column label="发布时间" width="160">
				 		<template slot-scope="scope">
				 			<div>{{articleList[scope.$index].published_time}}</div>
				 		</template>
				 	</el-table-column>
				 	<el-table-column label="状态" prop="post_status" width="200">
				 		<template slot-scope="scope">
				 			<span>{{articleList[scope.$index].post_status == 1 ? '已审核' : '未审核'}}</span>
				 			<span style="margin: 0 15px;">{{articleList[scope.$index].is_top == 1 ? '已置顶' : '未置顶'}}</span>
				 			<span>{{articleList[scope.$index].recommended == 1 ? '已推荐' : '未推荐'}}</span>
				 		</template>
				 	</el-table-column>
				 	<el-table-column label="操作" width="100">
	      				<template slot-scope="scope">
	        				<el-button type="text" size="small" @click="editArticle(scope.row)">编辑</el-button>
	    					<el-button type="text" size="small" @click="deleteArticle(scope.row)">删除</el-button>
	      				</template>
	    			</el-table-column>
	  			</el-table>
	  			<el-pagination layout="prev, pager, next" :total="total" class="page" :page-size="pageSize" @current-change="currentChange" :current-page.sync="currentPage" v-if="total != 0"></el-pagination>
		    </el-tab-pane>

		    <el-tab-pane label="添加文章" name="second">
		    	<articleEditAndAdd v-on:change="activeChange($event)" v-if="add"></articleEditAndAdd>
		    </el-tab-pane>

		    <el-tab-pane label="编辑文章" name="third" v-if="edit">
		    	<articleEditAndAdd v-on:change="activeChange($event)"></articleEditAndAdd>
		    </el-tab-pane>
		</el-tabs>
	</div>
</template>

<script>
	import apis from '../../apis'
	import articleEditAndAdd from '@/views/article/article-EditAndAdd'

	export default {
		inject: ['reload', 'reloadTab'],
		components: {
			articleEditAndAdd
		},
		data() {
			return {
				loading: true,
				activeName: 'first',
				url: 'http://www.cdddkqyy.com/',
				articleList: [],
				articleList2: [],
				total: 0,
				pageSize: 0,
				add: false,
				edit: false,
				updateListOrderArr: [],
				idListArr: [],
				articleListOrderArr: [],
				originalListOrderArr: [],
				cloneListOrder: [],
				articleArr: [],
				page: 1,
				selectedOptions: [],
				date: '',
				keyword: '',
				currentPage: 1,
				search: false,
			}
		},
		methods: {
			//改变头部选项卡
			changeTab(tab, event) {
				tab.index == 0 ? (this.add = false) | (this.edit = false) : '';
				tab.index == 1 ? (this.edit = false) | (this.$root.propList = this.articleList2) | (this.add = true) | (this.$root.propType = 'add') : '';
			},
            // page改变
            currentChange(page) {
            	this.loading = true;
            	var startTime, endTime, keyword, select;
            	if(this.search) {
            		this.date != '' ? (startTime = this.date[0]) | (endTime = this.date[1]) : '';
	            	this.keyword != '' ? keyword = this.keyword : '';
		      		this.selectedOptions != '' ? select = this.selectedOptions[this.selectedOptions.length - 1] : '';
            	}
            	this.page = page;

            	apis.articleList({page: page, category: select, start_time: startTime, end_time: endTime, keyword: keyword, token: this.$root.token}, function(data) {
					data ? this.loading = false : '';
					if(data.code == 0) {
						this.$alert(data.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				            	location.reload();
				          	}
				        });
					}
					else {
						this.articleList = data.data;
						this.idListArr = [];
						this.articleListOrderArr = [];
						this.cloneListOrder = [];
						this.originalListOrderArr = [];
						this.updateListOrderArr = [];
						// 把导航列表的id和list-order添加进数组里，用作修改排序
						for(var i = 0; i < data.data.length; i ++) {
			    			this.idListArr.push(data.data[i].rid);
			    			this.articleListOrderArr.push(data.data[i].list_order);
			    		}
			    		Object.assign(this.cloneListOrder, this.articleListOrderArr);
					}
				}.bind(this))
            },
		    // 接收子组件 传回来的值 改变头部选项卡
		    activeChange(name) {
		    	this.activeName = name;
		    	this.add = false;
		    	this.edit = false;
		    },
		    // 编辑
		    editArticle(row) {
		    	this.edit = true;
		    	this.activeName = 'third';
		    	this.$root.propType = 'edit';
				this.$root.propList = this.articleList2;
				this.$root.editArticle = row;
		    },
		    // 修改菜单显示顺序
		    changeListOrder() {
		    	// 将更改前和更改后发给后台做数据对比修改显示顺序
				for(var i = 0; i < this.idListArr.length; i++) {
					this.updateListOrderArr.push({id: JSON.parse(JSON.stringify(this.idListArr))[i], list_order: JSON.parse(JSON.stringify(this.articleListOrderArr))[i]});

					this.originalListOrderArr.push({id: JSON.parse(JSON.stringify(this.idListArr))[i], list_order: JSON.parse(JSON.stringify(this.cloneListOrder))[i]});
				}
		    	apis.articleListOrder({list_order: this.updateListOrderArr, original_list_order: this.originalListOrderArr, token: this.$root.token}, function(bck) {
		    		bck.info == '排序失败' ? bck.info = '排序成功' : '';
					this.$options.methods.alertMsg(this, bck.info, bck.code, this.$root.token);
		    	}.bind(this))
		    },
		    // 多选操作
	      	selectionChange(val) {
		        this.articleArr = val;
	      	},
            // 删除
            deleteArticle(row) {
            	apis.articleDelete({id: row.id, token: this.$root.token}, function(bck) {
            		this.$options.methods.alertMsg(this, bck.info, bck.code, this.$root.token);
            	}.bind(this))
            },
	      	// 批量删除
	      	deleteArticles() {
	      		if(this.articleArr.length != 0) {
	      			var ids = [];
		      		for(var i = 0; i < this.articleArr.length; i ++) {
		      			ids.push(this.articleArr[i].id);
		      		}
		      		apis.articleDelete({ids: ids, token: this.$root.token}, function(bck) {
		      			this.$options.methods.alertMsg(this, bck.info, bck.code, this.$root.token);
		      		}.bind(this))
	      		}
	      		else {
	      			this.$alert('请勾选要操作的文章');
	      		}
	      	},
	      	// 操作审核，置顶，推荐
	      	changeStatus(status) {
	      		var text = status.split(',');
	      		var statusType = '', topType = '', recommendedType = '', ids = [], self = this;

	      		text[0] == 'status' ? statusType = text[1] : text[0] == 'top' ? topType = text[1] : text[0] == 'recommended' ? recommendedType = text[1] : '';
	      		
	      		if(this.articleArr.length != 0) {
		      		for(var i = 0; i < this.articleArr.length; i ++) {
		      			ids.push(this.articleArr[i].id);
		      		}
	      			apis.articleStatus({ids: ids, status: statusType, top: topType, recommended: recommendedType, token: self.$root.token}, function(bck) {
	      				self.$options.methods.alertMsg(self, bck.info, bck.code, self.$root.token);
	      			})
	      		}
	      		else {
	      			this.$alert('请勾选要操作的文章');
	      		}
	      	},
	      	// 搜索
	      	searchArticle() {
	      		var select = '';
	      		this.selectedOptions != '' ? select = this.selectedOptions[this.selectedOptions.length - 1] : '';
	      		this.date === null ? this.date = '' : '';
	      		this.loading = true;
	      		apis.articleList({category: select, start_time: this.date[0], end_time: this.date[1], keyword: this.keyword, token: this.$root.token}, function(data) {
	      			data ? this.loading = false : '';
	      			if(data.code == 0) {
						this.$alert(data.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				            	location.reload();
				          	}
				        });
					}
					else {
						this.articleList = data.data;
						this.total = data.total;
						this.pageSize = data.per_page;
						// 把导航列表的id和list-order添加进数组里，用作修改排序
						this.idListArr = [];
						this.articleListOrderArr = [];
						this.cloneListOrder = [];
						this.originalListOrderArr = [];
						this.updateListOrderArr = [];
						for(var i = 0; i < data.data.length; i ++) {
			    			this.idListArr.push(data.data[i].rid);
			    			this.articleListOrderArr.push(data.data[i].list_order);
			    		}
			    		Object.assign(this.cloneListOrder, this.articleListOrderArr);
			    		if(this.date !== null || this.keyword != '' || this.selectedOptions != '') {
							this.search = true;
						}
						else {
							this.search = false;
						}
						this.currentPage = 1;
					}
	      		}.bind(this))
	      	},
	      	// 消息提示框，公共部分
	      	alertMsg: function(_this, info, code, _token) {
		    	_this.$alert(info, {
	         	 	confirmButtonText: '确定',
		          	callback: action => {
		            	if(code != 0) {
		            		var startTime, endTime, keyword, select;
			            	if(_this.search) {
			            		_this.date != '' ? (startTime = _this.date[0]) | (endTime = _this.date[1]) : '';
				            	_this.keyword != '' ? keyword = _this.keyword : '';
					      		_this.selectedOptions != '' ? select = _this.selectedOptions[_this.selectedOptions.length - 1] : '';
			            	}
		            		// 操作成功后重新请求列表
		            		apis.articleList({category: select, start_time: startTime, end_time: endTime, keyword: keyword, page: _this.page, token: _token}, function(data) {
								_this.articleList = data.data;
								_this.total = data.total;
								_this.pageSize = data.per_page;
								// 把导航列表的id和list-order添加进数组里，用作修改排序
								_this.idListArr = [];
								_this.articleListOrderArr = [];
								_this.cloneListOrder = [];
								_this.originalListOrderArr = [];
								_this.updateListOrderArr = [];
								for(var i = 0; i < data.data.length; i ++) {
					    			_this.idListArr.push(data.data[i].rid);
					    			_this.articleListOrderArr.push(data.data[i].list_order);
					    		}
					    		Object.assign(_this.cloneListOrder, _this.articleListOrderArr);
							}.bind(_this))
		            	}
		            	else if(info == '您还没有登录！！') {
		            		location.reload();
		            	}
		          	}
		        });
		    }
		},
		created() {
			apis.articleList({token: this.$root.token}, function(data) {
				data ? this.loading = false : '';
				if(data.code == 0) {
					this.$alert(data.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	location.reload();
			          	}
			        });
				}
				else {
					this.articleList = data.data;
					this.total = data.total;
					this.pageSize = data.per_page;
					// 把导航列表的id和list-order添加进数组里，用作修改排序
					for(var i = 0; i < data.data.length; i ++) {
		    			this.idListArr.push(data.data[i].rid);
		    			this.articleListOrderArr.push(data.data[i].list_order);
		    		}
		    		Object.assign(this.cloneListOrder, this.articleListOrderArr);
				}
			}.bind(this))

			apis.articleCategoryListTree({token: this.$root.token}, function(data) {
				this.articleList2 = data;
			}.bind(this))
		}
	}
</script>

<style lang="less">
	.article {
		.change-list-order {
			margin-bottom: 15px;
		}
		.change-status {
			margin-left: 10px;
		}
		.search {
			margin-bottom: 15px;
			font-size: 15px;
			.el-input {
				width: auto;
			}
			.search-btn {
				margin-left: 15px;
			}
		}
		.page {
			text-align: center;
			margin-top: 15px;
		}
		.articleLink {
			white-space: nowrap;
		    color: #409EFF;
		    text-decoration: none;
		}
	}
</style>


