<!-- 抽奖管理 -->
<template>
	<div class="right-content draw">
		<el-tabs v-model="activeName" @tab-click="changeTab" class="content">
			<el-tab-pane label="抽奖管理" name="first">
				<el-table :data="drawList" style="width: 100%" :border='true' v-loading="loading">
					<el-table-column label="奖品名称" prop="award_name"></el-table-column>
					<el-table-column label="抽奖人数" prop="award_total"></el-table-column>
					<el-table-column label="创建时间" prop="create_time"></el-table-column>
				 	<el-table-column label="操作" width="130">
	      				<template slot-scope="scope">
	        				<el-button type="text" size="small" @click="editDraw(scope.row)">编辑</el-button>
	    					<el-button type="text" size="small" @click="deleteDraw(scope.row)">删除</el-button>
	      				</template>
	    			</el-table-column>
				</el-table>
			</el-tab-pane>

			<el-tab-pane label="新增抽奖" name="second">
				<drawEditAndAdd v-if="add" v-on:change="activeChange($event)"></drawEditAndAdd>
			</el-tab-pane>

			<el-tab-pane label="编辑抽奖" name="third" v-if="edit">
				<drawEditAndAdd v-on:change="activeChange($event)"></drawEditAndAdd>
			</el-tab-pane>
		</el-tabs>
	</div>
</template>

<script>
	import apis from '../../apis'
	import drawEditAndAdd from '@/views/draw/draw-EditAndAdd'

	export default {
		inject: ['reload', 'reloadTab'],
		components: {
			drawEditAndAdd
		},
		data() {
			return {
				loading: true,
				activeName: 'first',
				drawList: [],
				add: false,
				edit: false,
			}
		},
		methods: {
			// 切换头部栏
			changeTab(tab, event) {
				tab.index == 1 ? (this.add = true) | (this.$root.propType = 'add') : (this.add = false) | (this.edit = false);
			},
		    // 接收子组件 传回来的值 改变头部选项卡
		    activeChange(name) {
		    	this.activeName = name;
		    	this.add = false;
		    	this.edit = false;
		    },
		    // 编辑
			editDraw(row) {
				this.$root.drawList = row;
				this.$root.propType = 'edit';
				this.edit = true;
				this.activeName = 'third';
			},
			// 删除
			deleteDraw(row) {
				apis.drawDelete({id: row.id, token: this.$root.token}, function(bck) {
					this.$alert(bck.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			          		if(bck.info == '您还没有登录！！') {
				            	location.reload();
			            	}
			            	else if(bck.code == 1) {
			            		this.reload();
			            	}
			          	}
			        });
				}.bind(this))
			}
		},
		created() {
			apis.drawList({token: this.$root.token}, function(data) {
				data ? this.loading = false : '';

				if(data.code == 0) {
					this.$alert(data.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	location.reload();
			          	}
			        });
				}
				else {
					this.drawList = data;
				}
			}.bind(this))
		}
	}
</script>

<style lang="less">
	.draw {
		
	}
</style>


