<!-- 上传设置 -->
<template>
	<div class="right-content upload">
		<div class="content">
		<!-- 上传设置 -->
			<div class="list">
				<div>
					<span class="name">图片文件</span>
					<el-input v-model="imageMaxFilesize"></el-input>
					<el-input v-model="imageExtensions"></el-input>
					<span class="required">*</span>
				</div>
				<p><span class="name"></span>允许上传大小默认为10240KB,1M=1024KB，允许上传格式默认为jpg,jpeg,png,gif,bmp</p>
			</div>
			<div class="list">
				<div>
					<span class="name">视频文件</span>
					<el-input v-model="videoMaxFilesize"></el-input>
					<el-input v-model="videoExtensions"></el-input>
					<span class="required">*</span>
				</div>
				<p><span class="name"></span>允许上传大小默认为102400KB,1M=1024KB，允许上传格式默认为mp4,avi,wmv,rm,rmvb,mkv</p>
			</div>
			<div class="list">
				<div>
					<span class="name">音频文件</span>
					<el-input v-model="audioMaxFilesize"></el-input>
					<el-input v-model="audioExtensions"></el-input>
					<span class="required">*</span>
				</div>
				<p><span class="name"></span>允许上传大小默认为10240KB,1M=1024KB，允许上传格式默认为mp3,wma,wav</p>
			</div>
			<div class="list">
				<div>
					<span class="name">附件</span>
					<el-input v-model="fileMaxFilesize"></el-input>
					<el-input v-model="fileExtensions"></el-input>
					<span class="required">*</span>
				</div>
				<p><span class="name"></span>允许上传大小默认为10240KB,1M=1024KB，允许上传格式默认为除以上文档类型以外的其它常用文件,如：txt,pdf,doc,docx,xls,xlsx,ppt,pptx,zip,rar</p>
			</div>
			<div class="submit-btn"><span class="name"></span><el-button type="primary" @click="saveUploadInfo()">保存</el-button></div>
		</div>
	</div>
</template>

<script>
	import apis from '../../apis'

	export default {
		data() {
			return {
				imageMaxFilesize: '',
				imageExtensions: '',
				videoMaxFilesize: '',
				videoExtensions: '',
				audioMaxFilesize: '',
				audioExtensions: '',
				fileMaxFilesize: '',
				fileExtensions: ''
			}
		},
		methods: {
			saveUploadInfo() {
				apis.uploadPost({image_max_filesize: this.imageMaxFilesize, image_extensions: this.imageExtensions, video_max_filesize: this.videoMaxFilesize, video_extensions: this.videoExtensions, audio_max_filesize: this.audioMaxFilesize, audio_extensions: this.audioExtensions, file_max_filesize: this.fileMaxFilesize, file_extensions: this.fileExtensions, token: this.$root.token}, function(bck) {
					if(bck.code == 1) {
						this.$alert('保存成功', {confirmButtonText: '确定'});
					}
					else {
						if(bck.info == '您还没有登录！！') {
							this.$alert(bck.info, {
				         	 	confirmButtonText: '确定',
					          	callback: action => {
					            	location.reload();
					          	}
					        });
						}
						else {
							this.$alert('保存成功', {confirmButtonText: '确定'});
						}
				    }
				}.bind(this))
			}
		},
		created() {
			apis.site({name: 'upload_info', token: this.$root.token}, function(bck) {
				if(bck.code == 0) {
					this.$alert(bck.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	location.reload();
			          	}
			        });
				}
				else {
					this.imageMaxFilesize = bck.image_max_filesize;
					this.imageExtensions = bck.image_extensions;

					this.videoMaxFilesize = bck.video_max_filesize;
					this.videoExtensions = bck.video_extensions;

					this.audioMaxFilesize = bck.audio_max_filesize;
					this.audioExtensions = bck.audio_extensions;

					this.fileMaxFilesize = bck.file_max_filesize;
					this.fileExtensions = bck.file_extensions;
				}
			}.bind(this))
		}
	}
</script>

<style lang="less">
	.upload {
	    .content {
	    	padding: 30px 50px;
	    }
		.el-input {
			width: auto;
		}
		.name {
			width: 85px;
			text-align: right;
			margin-right: 15px;
		}
		span {
			display: inline-block;
		}
		p {
			margin-top: 0;
			font-size: 14px;
		}
		.list {
			margin-bottom: 25px;
			P {
				color: #9C9C9C;
			}
		}
	}
</style>


