<!-- 百科添加与编辑 -->
<template>
	<div class="encyclopedia-edit">
		<div class="flex">
			<div style="padding-left: 5%;width: 440px;">
				<div class="encyclopedia">
					<span class="title">发布时间</span>
		    		<el-date-picker v-model="date" type="datetime" placeholder="选择日期时间" align="center"></el-date-picker>
					<span class="required">*</span>
				</div>
		    	<div class="encyclopedia">
					<span class="title">百科分类</span>
					<el-select v-model="selectedOptions" placeholder="请选择">
						<el-option v-for="item in encyclopediaCategoryList" :key="item.id" :label="item.name" :value="item.id"></el-option>
					</el-select>
					<span class="required">*</span>
		    	</div>
		    	<div class="encyclopedia">
		    		<span class="title">百科标题</span>
		    		<el-input placeholder="请输入百科标题" v-model="encyclopediaName" clearable></el-input>
		    		<span class="required">*</span>
		    	</div>
		    	<div class="encyclopedia">
		    		<span class="title" style="vertical-align: top;">百科摘要</span>
		    		<el-input placeholder="请输入百科摘要" v-model="encyclopediaExcerpt" type='textarea' :autosize="{ minRows: 2}"></el-input>
		    	</div>
			</div>
			<div style="width: 60%; flex-wrap: wrap;" class="flex">
				<div class="img-upload flex" style="width:280px;margin-right:15px;">
		    		<div style="margin-right: 15px;width:112px;text-align:right;"><span class="title">缩略图</span></div>
		    		<div class="preview-img" v-if="imgBase64 != ''">
						<img :src="imgBase64" class="slide-add-title">
						<el-button @click="destoryImgBase64" v-if="imgBase64 != ''" style="margin-top: 15px;">取消图片</el-button>
					</div>
					<div>
						<div class="el-upload el-upload--picture-card" v-if="imgBase64 == ''">
							<i class="el-icon-plus"></i>
							<input type="file" @change="fileImage" accept="image/jpeg,image/x-png,image/gif" accessKey="pc">
						</div>
					</div>
		    	</div>
			</div>
		</div>
		<div class="encyclopedia" style="padding-left: 5%;">
			<span class="title"><b>内容信息</b></span>
			<el-button @click="addInfoLine()">增加一行</el-button>
		</div>
		<div style="padding-left: 5%;">
			<div class="encyclopedia" v-for="(item, inx) in info">
				<span class="title"></span>
				<el-input placeholder="请输入内容标题" v-model="infoTitle[inx]"></el-input>
				<el-input placeholder="请输入内容" v-model="infoContent[inx]" style="margin: 0 15px;"></el-input>
				<el-button type="danger" icon="el-icon-delete" size="medium" @click="destoryInfoLine(inx)" circle></el-button>
			</div>
		</div>
		<!-- <div class="encyclopedia" style="padding-left: 5%;">
			<span class="title" style="vertical-align: top;">内容概述</span>
			<div id="editorElem" class="editorElem" style="text-align:left"></div>
		</div> -->
		<div class="encyclopedia" style="padding-left: 5%;">
			<el-button type="primary" @click="addOrEdit()">{{type == 'add' ? '添加' : '保存'}}</el-button>
			<el-button @click="goBack()">返回</el-button>
		</div>
	</div>
</template>

<script>
	import apis from '../../apis'
	import E from 'wangeditor'

	export default {
		inject: ['reload', 'reloadTab'],
		data() {
			return {
				encyclopediaCategoryList: [],
				selectedOptions: '',
				imgBase64: '',
				encyclopediaName: '',
				encyclopediaExcerpt: '',
				editorContent: '',
				type: '',
				date: '',
				info: [0, 1, 2],
				infoTitle: ['可选方法', '适应人群', '推荐专家'],
				infoContent: [],
				encyclopediaId: Number,
			}
		},
		methods: {
			// 上传图片
		    fileImage:function(e) {
		    	var self = this;
                var file = e.target.files[0];
                var reader = new FileReader();
                reader.readAsDataURL(file); // base64
                reader.onloadend = function () {
                    // 图片的 base64 格式
                    self.imgBase64 = reader.result;
                };
            },
            // 取消图片
            destoryImgBase64(type) {
            	this.imgBase64 = '';
            },
            // 返回
            goBack() {
            	this.$emit("change", "first");
            },
            // 增加一行
            addInfoLine() {
            	var inx = this.info.length;
            	this.info.push(inx);
            },
            // 删除一行
            destoryInfoLine(inx) {
            	this.info.splice(inx, 1);
            	this.infoTitle.splice(inx, 1);
				this.infoContent.splice(inx, 1);
            },
            // 添加或修改
            addOrEdit() {
            	var infoArr = new Array();
            	for(var i = 0; i < this.info.length; i++) {
            		this.infoContent[i] ? '' : this.infoContent[i] = '';
            		infoArr.push({'title': this.infoTitle[i], 'content': this.infoContent[i]});
            	}
            	
            	if(this.type == 'add') {
            		apis.encyclopediaAdd({en_title: this.encyclopediaName, category_id: this.selectedOptions, en_excerpt: this.encyclopediaExcerpt, en_info: infoArr, published_time: this.date, thumb: this.imgBase64, token: this.$root.token}, function(bck) {
            			alertMsg(this, bck.info, bck.code);
            		}.bind(this))
            	}
            	else if(this.type == 'edit') {
            		apis.encyclopediaEdit({id: this.encyclopediaId, en_title: this.encyclopediaName, category_id: this.selectedOptions, en_excerpt: this.encyclopediaExcerpt, en_info: infoArr, published_time: this.date, thumb: this.imgBase64, token: this.$root.token}, function(bck) {
            			alertMsg(this, bck.info, bck.code);
            		}.bind(this))
            	}

            	function alertMsg(_this, info, code) {
            		_this.$alert(info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			          		if(info == '您还没有登录！！') {
				            	location.reload();
			            	}
			            	else if(code == 1) {
			            		_this.reload();
			            	}
			          	}
			        });
            	}
            }
		},
		created() {
			this.encyclopediaCategoryList = this.$root.propList;
			this.type = this.$root.propType;
			
			if(this.type == 'add') {
				this.date = new Date();
			}
			else if(this.type == 'edit') {
				var editEncyclopedia = this.$root.editEncyclopedia;
				var info = JSON.parse(editEncyclopedia.en_info);

				this.date = editEncyclopedia.published_time;
				this.encyclopediaName = editEncyclopedia.en_title;
				this.encyclopediaExcerpt = editEncyclopedia.en_excerpt;
				this.selectedOptions = editEncyclopedia.category_id;
				this.encyclopediaId = editEncyclopedia.id;

				this.info = [];
				for(var i = 0; i < info.length; i ++) {
					this.info.push(i);
	            	this.infoTitle[i] = info[i].title;
					this.infoContent[i] = info[i].content;
				}

				if(editEncyclopedia.thumb) {
					this.imgBase64 = editEncyclopedia.thumb;
				}
			}
		},

		mounted() {
   //   		// 初始化富文本编辑器
	  //       var editor = new E('#editorElem');
			// editor.customConfig.uploadImgShowBase64 = true;   // 使用 base64 保存图片
   //        	// 自定义配置颜色（字体颜色、背景色）
		 //    editor.customConfig.colors = [
		 //        '#000000', '#eeece0', '#1c487f', '#4d80bf', '#ff0000', '#8baa4a', '#7b5ba1', '#46acc8', '#f9963b', '#ffffff'
		 //    ]
		 //    // 自定义菜单配置
		 //    editor.customConfig.menus = ['head', 'bold', 'fontSize', 'fontName', 'italic', 'underline', 'strikeThrough', 'foreColor', 'backColor', 'link', 'list', 'justify', 'quote', 'table', 'image', 'undo']

	  //       editor.customConfig.onchange = (html) => {
	  //         this.editorContent = html;
	  //       }
	  //       editor.customConfig.debug = true;
	  //       editor.create();
	  //       editor.txt.html(this.editorContent);
	  //       this.editor = editor;
      	}
	}
</script>
<style lang="less">
	.encyclopedia-edit {
		.encyclopedia {
			margin-top: 20px;
			font-size: 15px;
			.title {
				margin-right: 15px;
				width: 75px;
				text-align: right;
				display: inline-block;
			}
			.el-input, .el-textarea {
				width: 217px;
			}
			.editorElem {
			    width: 75%;
    			display: inline-block;
			}
		}
		.img-upload {
			margin-top: 20px;
			.preview-img {
	        	width: 80%;
	        	img {
	        		width: 100%;
	        	}
	        }
			.el-upload {
			    position: relative;
			    line-height: 148px;
			}
			.el-upload input {
			    position: absolute;
			    right: 0;
			    top: 0;
			    opacity: 0;
			    cursor: pointer;
	            width: 100%;
    			height: 100%;
			}
		}
		.w-e-text-container {
			height: 350px !important;
			z-index: 999 !important;
		}
		.w-e-toolbar .w-e-menu {
			z-index: 1900 !important;
		}
	}
</style>


