<!-- 左侧导航栏 -->
<template>
	<div class="left-tabs">
	    <el-menu :default-active="path" background-color="#fafafa" :unique-opened='true' active-text-color="#c11920" :router='true'>

	   		 <!-- 第一层遍历 -->
			<div v-for="(item, inx) in tabList" :key="inx">
				<el-menu-item index="" v-if="inx == 0">
					<template slot="title">
				        <a :href="homeUrl" target="_blank">
				        	<i :class="item.icon" class="fa" aria-hidden="true" style="color: #999;"></i>
			        		<span slot="title">{{item.title}}</span>
				        </a>
			        </template>
		      	</el-menu-item>

		      	<el-submenu :index="inx + ''" v-else>
		        	<template slot="title">
		          		<i :class="item.icon" class="fa" aria-hidden="true"></i>
		          		<span>{{item.title}}</span>
		        	</template>

		        	<!-- 第二层遍历 -->
			        <div v-for="(item2, SecondInx) in item.secondTitle" :key="SecondInx">
				        <el-menu-item :index="item2.url" v-if="item2.secondTitle == 0">
				          	<template slot="title">
				          		<i class="el-icon-caret-right"></i>
				          		<span>{{item2.title}}</span>
				          	</template>
				        </el-menu-item>

				        <el-submenu :index="inx + '-' + SecondInx" v-else="">
				          	<template slot="title">
				          		<i class="el-icon-caret-right"></i>
				          		<span>{{item2.title}}</span>
				          	</template>
				          	<!-- 第三层遍历 -->

				          	<div v-for="(item3, ThirdInx) in item2.secondTitle" :key="ThirdInx">
				          		<el-menu-item :index="item3.url">
									<template>
										<i class="el-icon-d-arrow-right"></i>
										<span>{{item3.title}}</span>
									</template>
					          	</el-menu-item>
				          	</div>
				        </el-submenu>
			        </div>
		      	</el-submenu>
			</div>
	    </el-menu>
	</div>
</template>

<script>
	import apis from '../../apis'

  	export default {
	  	data() {
	  		return {
	  			tabList: {},
	  			path: '',
	  			homeUrl: '',
	  		}
	  	},
	    methods: {
	      	
	    },
	    created() {
	    	apis.getLeftTap({token: this.$root.token}, function(data) {
	    		this.tabList = data;
	    		data.code == 0 ? this.$alert(data.info) : '';
	    	}.bind(this))

	    	// 每次渲染获取path，如果刷新页面可以定位到相对应的tab
	    	var string = this.$route.path;
	    	this.path = string.split('/');
	    	this.path = this.path[1];

	    	this.homeUrl = 'http://' + JSON.parse(localStorage.getItem('userInfo')).homeUrl;
	    }
  	}
</script>

<style lang="less">
	@import '../../assets/icon/iconfont.css';
	.left-tabs {
		position: fixed;
		top: 65px;
		bottom: 0;
	    width: 210px;
  		background: #F2F2F2;
  		user-select: none;
  		.fa {
  			margin-right: 5px;
  		}
  		.el-menu {
		    height: 100%;
  		}
  		a {
  			text-decoration: none;
		    color: #303133;    
		    position: absolute;
		    width: 81%;
		    left: 0;
		    padding: 0 20px;
  		}
	}
</style>


