<!-- 问题解答 -->
<template>
	<div class="answer">
		<div class="answer-question">
			<span class="title" style="vertical-align: top;">问题描述</span>
			<el-input v-model="questionExcerpt" placeholder="请输入问题描述" type="textarea" :autosize="{ minRows: 2}"></el-input>
		</div>
		<div class="answer-question">
			<span class="title" style="vertical-align: top;">问题求助</span>
			<el-input v-model="questionHelp" type="textarea" :autosize="{ minRows: 2}"></el-input>
		</div>
		<div class="answer-question">
			<span class="title">选择医生</span>
			<el-select v-model="doctorSelect" placeholder="请选择">
			    <el-option v-for="item in doctorList" :key="item.id" :label="item.doctor_name" :value="item.id"></el-option>
		  	</el-select>
		  	<span class="required">*</span>
		</div>
		<div class="answer-question">
			<div>
				<span class="title">回答内容</span>
				<el-input v-model="answerTitle1"></el-input>
			</div>
			<div id="editorElem" class="editorElem" style="text-align:left"></div>
		</div>
		<div class="answer-question">
			<div>
				<span class="title">回答内容</span>
				<el-input v-model="answerTitle2"></el-input>
			</div>
			<div id="editorElem2" class="editorElem" style="text-align:left"></div>
		</div>
		<div class="answer-article" style="padding-left: 5%; margin-top: 15px;">
			<el-button type="primary" @click="submitData()">保存</el-button>
			<el-button @click="goBack()">返回</el-button>
		</div>
	</div>
</template>

<script>
	import apis from '../../apis'
	import E from 'wangeditor'

	export default {
		inject: ['reload', 'reloadTab'],
		data() {
			return {
				doctorSelect: '',
				doctorList: [],
				questionExcerpt: '',
				questionHelp: '',
				editor: '',
				editor2: '',
				answerTitle1: '病情分析：',
				answerTitle2: '指导意见：',
				editorContent: '',
				editorContent2: '',
				questionId: Number,
				answerId: Number,
			}
		},
		methods: {
			// 保存
			submitData() {
				if(this.doctorSelect == '') {
					this.$alert('请选择医生');
					return;
				}
				var content = [{'title': this.answerTitle1, 'content': this.editorContent}, {'title': this.answerTitle2, 'content': this.editorContent2}];
				apis.answerPost({id: this.answerId, question_id: this.questionId, doctor_id: this.doctorSelect, content: content, token: this.$root.token}, function(bck) {
					this.$alert(bck.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	if(bck.code != 0) {
			            		this.$emit("change", "first");
			            	}
			            	else if(bck.info == '您还没有登录！！') {
			            		location.reload();
			            	}
			          	}
			        });
				}.bind(this))
			},
			// 返回
			goBack() {
				this.$emit("change", "first");
			}
		},
		created() {
			this.questionId = this.$root.question.id;
        	this.questionExcerpt = this.$root.question.question_excerpt;
        	this.questionHelp = this.$root.question.question_help;

			// 医生列表
        	apis.answerQuestionDoctorList({token: this.$root.token}, function(data) {
        		if(data.code == 0) {
					this.$alert(data.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	location.reload();
			          	}
			        });
				}
				else {
					this.doctorList = data;
				}
        	}.bind(this))

        	// 问题对应的解答列
        	apis.answerList({question_id: this.questionId, token: this.$root.token}, function(data) {
        		if(data.code != 0) {
        			this.answerId = data.id;
        			this.doctorSelect = data.doctor_id;
	        		this.answerTitle1 = data.content[0].title;
	        		this.editor.txt.html(data.content[0].content);
	        		if(data.content[1]) {
	        			this.answerTitle2 = data.content[1].title;
	        			this.editor2.txt.html(data.content[1].content);
	        		}
        		}
        		else {
        			this.answerId = 0;
        		}
        	}.bind(this))
		},
		mounted() {
     		// 初始化富文本编辑器
	        var editor = new E('#editorElem');
	        var editor2 = new E('#editorElem2');
          	// 自定义配置颜色（字体颜色、背景色）
		    editor.customConfig.colors = [
		        '#000000', '#eeece0', '#1c487f', '#4d80bf', '#ff0000', '#8baa4a', '#7b5ba1', '#46acc8', '#f9963b', '#ffffff'
		    ]
		    editor2.customConfig.colors = [
		        '#000000', '#eeece0', '#1c487f', '#4d80bf', '#ff0000', '#8baa4a', '#7b5ba1', '#46acc8', '#f9963b', '#ffffff'
		    ]
		    // 自定义菜单配置
		    editor.customConfig.menus = ['head', 'bold', 'fontSize', 'fontName', 'italic', 'underline', 'strikeThrough', 'foreColor', 'backColor', 'link', 'list', 'justify', 'quote', 'table', 'undo']
		    editor2.customConfig.menus = ['head', 'bold', 'fontSize', 'fontName', 'italic', 'underline', 'strikeThrough', 'foreColor', 'backColor', 'link', 'list', 'justify', 'quote', 'table', 'undo']

	        editor.customConfig.onchange = (html) => {this.editorContent = html;}
	        editor2.customConfig.onchange = (html) => {this.editorContent2 = html;}
	        editor.customConfig.debug = true;
	        editor2.customConfig.debug = true;
	        editor.create();
	        editor2.create();
	        editor.txt.html(this.editorContent);
	        editor2.txt.html(this.editorContent2);
	        this.editor = editor;
	        this.editor2 = editor2;
      	}
	}
</script>

<style lang="less">
	.answer {
		.answer-question {
		    font-size: 15px;
		    padding-left: 5%;
		    margin-top: 20px;
		    .title {
		    	margin-right: 15px;
		    }
		    .el-input, .el-textarea {
		    	width: 217px;
		    }
		}
		.editorElem {
		    width: 60%;
			display: inline-block;
			margin-left: 80px;
			margin-top: 15px;
		}
	}
	.w-e-text-container {
		z-index: 0 !important;
	}
	.el-date-picker, .el-select-dropdown {
		z-index: 20000 !important;
	}
	.el-message-box__wrapper {
		z-index: 20009 !important;
	}
</style>


