<!-- 医生管理 -->
<template>
	<div class="right-content doctor">
		<el-tabs v-model="activeName" @tab-click="changeTab" class="content">

			<el-tab-pane label="医生管理" name="first">
				<div class="flex" style="flex-wrap: wrap; justify-content: space-between;">
					<div>
						<!-- 修改排序 -->
						<el-button @click="changeListOrder()" type="primary" size="medium" plain class="change-list-order">修改排序</el-button>
						<!-- 批量删除 -->
						<el-button @click="deleteDoctors()" type="primary" size="medium" plain class="deletes">批量删除</el-button>
						<!-- 置顶、审核、推荐 -->
						<el-dropdown trigger="click" @command="changeStatus">
						  	<el-button type="primary" size="medium" class="change-status">
						    	更改状态<i class="el-icon-arrow-down el-icon--right"></i>
						  	</el-button>
						  	<el-dropdown-menu slot="dropdown">
						    	<el-dropdown-item command="status, 1" type="primary" size="medium" plain class="status">审核</el-dropdown-item>
								<el-dropdown-item command="top, 1" type="primary" size="medium" plain class="status">置顶</el-dropdown-item>
								<el-dropdown-item command="recommended, 1" type="primary" size="medium" plain class="status">推荐</el-dropdown-item>

								<el-dropdown-item command="status, 0" type="primary" size="medium" plain class="status">取消审核</el-dropdown-item>
								<el-dropdown-item command="top, 0" type="primary" size="medium" plain class="status">取消置顶</el-dropdown-item>
								<el-dropdown-item command="recommended, 0" type="primary" size="medium" plain class="status">取消推荐</el-dropdown-item>
						  </el-dropdown-menu>
						</el-dropdown>
					</div>
					<div class="search">
						<span style="margin-left: 15px;">科室：</span>
						<el-select v-model="departmentSelect" placeholder="请选择" clearable>
						    <el-option v-for="item in departmentList" :key="item.id" :label="item.name" :value="item.id">
						    </el-option>
					  	</el-select>
						<span style="margin-left: 15px;">关键词：</span>
						<el-input v-model="keyword" placeholder="请输入内容" clearable></el-input>
						<el-button @click="searchDoctor()" type="primary" size="medium" icon="el-icon-search" class="search-btn">搜索</el-button>
					</div>
				</div>

				<el-table :data="doctorList" style="width: 100%" :border='true' v-loading="loading" @selection-change="selectionChange">
					<el-table-column type="selection" width="40"></el-table-column>
					<el-table-column label="显示顺序" width="100">
	    				<template slot-scope="scope">
	    					<el-input v-model="doctorListOrderArr[scope.$index]" type="number"></el-input>
	    				</template>
	    			</el-table-column>
					<el-table-column label="医生名称">
						<template slot-scope="scope">
      						<a :href="'http://www.cdddkqyy.com/doctor/'+ doctorList[scope.$index].id + '.html'" target="_blank" class="doctorLink">{{doctorList[scope.$index].doctor_name}}</a>
      					</template>
					</el-table-column>
					<el-table-column label="所属科室" prop="department" width="100"></el-table-column>
					<el-table-column label="修改时间" prop="update_time" width="160"></el-table-column>
					<el-table-column label="状态" prop="post_status" width="200">
				 		<template slot-scope="scope">
				 			<span>{{doctorList[scope.$index].status == 1 ? '已审核' : '未审核'}}</span>
				 			<span style="margin: 0 15px;">{{doctorList[scope.$index].is_top == 1 ? '已置顶' : '未置顶'}}</span>
				 			<span>{{doctorList[scope.$index].recommended == 1 ? '已推荐' : '未推荐'}}</span>
				 		</template>
				 	</el-table-column>
				 	<el-table-column label="操作" width="150">
	      				<template slot-scope="scope">
	        				<el-button type="text" size="small" @click="editDoctor(scope.row)">编辑</el-button>
	        				<el-button type="text" size="small" @click="setWorkingDoctor(scope.row)">排班</el-button>
	    					<el-button type="text" size="small" @click="deleteDoctor(scope.row)">删除</el-button>
	      				</template>
	    			</el-table-column>
				</el-table>
				<el-pagination layout="prev, pager, next" :total="total" class="page" :page-size="pageSize" @current-change="currentChange" :current-page.sync="currentPage" v-if="total != 0"></el-pagination>
			</el-tab-pane>

			<el-tab-pane label="添加医生" name="second">
				<doctorEditAndAdd v-if="add" v-on:change="activeChange($event)"></doctorEditAndAdd>
			</el-tab-pane>

			<el-tab-pane label="编辑医生" name="third" v-if="edit">
				<doctorEditAndAdd v-on:change="activeChange($event)"></doctorEditAndAdd>
			</el-tab-pane>
		</el-tabs>

		<el-dialog title="医生排班" :visible.sync="setWorking" width="35%">
  			<div class="set-working">
				<span class="title">医生名字：</span><b>{{doctorName}}</b>
			</div>
			<div class="set-working">
				<span class="title">坐诊时间：</span>
			</div>
			<div class="set-working" style="padding-right: 5%;">
				<!-- 排班时间表 -->
				<table class="set-working-table">
					<thead>
						<tr>
							<td></td>
							<td v-for="(item, index) in doctorWorkTime">{{week[index]}}</td>
						</tr>
					</thead>
					<tbody>
						<tr>
							<td>上午</td>
							<td v-for="(item, index) in doctorWorkTime">
								<el-checkbox v-model="doctorWorkTime[index].am"></el-checkbox>
							</td>
						</tr>
						<tr>
							<td>下午</td>
							<td v-for="(item, index) in doctorWorkTime">
								<el-checkbox v-model="doctorWorkTime[index].pm"></el-checkbox>
							</td>
						</tr>
					</tbody>
				</table>
			</div>
			<div class="set-working">
				<div><span class="title">挂号费用：</span>
				 <el-input v-model="setCost" clearable></el-input></div>
				
				<!--<span>{{setCost}}</span>-->
				<div class="mt-3"><span class="title">挂号分类：</span>
				<el-input v-model="classify" clearable></el-input>
				</div>
			</div>
  			<span slot="footer" class="dialog-footer">
    			<el-button @click="setWorking = false">取 消</el-button>
    			<el-button type="primary" @click="submitWorkTime()">保 存</el-button>
  			</span>
		</el-dialog>
	</div>
</template>

<script>
	import apis from '../../apis'
	import doctorEditAndAdd from '@/views/doctor/doctor-EditAndAdd'

	export default {
		inject: ['reload', 'reloadTab'],
		components: {
			doctorEditAndAdd
		},
		data() {
			return {
				activeName: 'first',
				loading: true,
				setWorking: false,
				add: false,
				edit: false,
				doctorList: [],
				departmentList: [],
				doctorListOrderArr: [],
				idListArr: [],
				cloneListOrder: [],
				updateListOrderArr: [],
				originalListOrderArr: [],
				total: 0,
				pageSize: 0,
				page: 1,
				doctorId: Number,
				doctorWorkTime: [],
				doctorName: '',
				departmentSelect: '',
				keyword: '',
				doctorArr: [],
				week: ['星期一', '星期二', '星期三', '星期四', '星期五', '星期六', '星期日'],
				setCost: 0,
				search: false,
				currentPage: 1,
				classify:'',
				more:[],
			}
		},
		methods: {
			// 改变头部栏
			changeTab(tab) {
				tab.index == 1 ? (this.$root.departmentList = this.departmentList) | (this.add = true) | (this.edit = false) | (this.$root.propType = 'add') : '';
				tab.index == 0 ? (this.add = false) | (this.edit = false) : '';
			},
            // 排班
            setWorkingDoctor(row) {
            	this.doctorId = row.id;
            	this.doctorWorkTime = row.working_time;
            	this.setWorking = true;
            	this.doctorName = row.doctor_name;
            	this.setCost = row.more.registration_fee;
            	this.classify = row.more.category;
            	this.more=[];
            	this.more=row.more;
            },
            // 提交
            submitWorkTime() {
            	apis.doctorSetWorking({working: this.doctorWorkTime, id: this.doctorId, more: this.more, token: this.$root.token}, function(bck) {
            		this.$options.methods.alertMsg(this, bck.info, bck.code);
            	}.bind(this))
            },
		    // 接收子组件 传回来的值 改变头部选项卡
		    activeChange(name) {
		    	this.activeName = name;
		    	this.add = false;
		    	this.edit = false;
		    },
		    // 编辑
		    editDoctor(row) {
		    	this.edit = true;
		    	this.activeName = 'third';
		    	this.$root.propType = 'edit';
		    	this.$root.departmentList = this.departmentList;
		    	this.$root.editDoctor = row;
		    },
		    // 多选操作
	      	selectionChange(val) {
		        this.doctorArr = val;
	      	},
	      	// 排序
	      	changeListOrder() {
	      		// 将更改前和更改后发给后台做数据对比修改显示顺序
				for(var i = 0; i < this.idListArr.length; i++) {
					this.updateListOrderArr.push({id: JSON.parse(JSON.stringify(this.idListArr))[i], list_order: JSON.parse(JSON.stringify(this.doctorListOrderArr))[i]});

					this.originalListOrderArr.push({id: JSON.parse(JSON.stringify(this.idListArr))[i], list_order: JSON.parse(JSON.stringify(this.cloneListOrder))[i]});
				}
		    	apis.doctorListOrder({list_order: this.updateListOrderArr, original_list_order: this.originalListOrderArr, token: this.$root.token}, function(bck) {
		    		bck.info == '排序失败' ? bck.info = '排序成功' : '';
					this.$options.methods.alertMsg(this, bck.info, bck.code);
		    	}.bind(this))
	      	},
            // 删除
            deleteDoctor(row) {
	      		apis.doctorDelete({id: row.id, token: this.$root.token}, function(bck) {
            		this.$options.methods.alertMsg(this, bck.info, bck.code);
            	}.bind(this))
            },
	      	// 批量删除
	      	deleteDoctors() {
	      		if(this.doctorArr.length != 0) {
	      			var ids = [];
		      		for(var i = 0; i < this.doctorArr.length; i ++) {
		      			ids.push(this.doctorArr[i].id);
		      		}
		      		apis.doctorDelete({ids: ids, token: this.$root.token}, function(bck) {
	            		this.$options.methods.alertMsg(this, bck.info, bck.code);
	            	}.bind(this))
	      		}
	      		else {
	      			this.$alert('请勾选要操作的文章');
	      		}
	      	},
	      	// 操作审核，置顶，推荐
	      	changeStatus(status) {
	      		var text = status.split(',');
	      		var statusType = '', topType = '', recommendedType = '', ids = [], self = this;

	      		text[0] == 'status' ? statusType = text[1] : text[0] == 'top' ? topType = text[1] : text[0] == 'recommended' ? recommendedType = text[1] : '';
	      		
	      		if(this.doctorArr.length != 0) {
		      		for(var i = 0; i < this.doctorArr.length; i ++) {
		      			ids.push(this.doctorArr[i].id);
		      		}
	      			apis.doctorStatus({ids: ids, status: statusType, top: topType, recommended: recommendedType, token: this.$root.token}, function(bck) {
	      				self.$options.methods.alertMsg(self, bck.info, bck.code);
	      			})
	      		}
	      		else {
	      			this.$alert('请勾选要操作的文章');
	      		}
	      	},
	      	// 搜索
	      	searchDoctor() {
	      		var departmentId = '';
	      		this.selectedOptions != '' ? departmentId = this.departmentSelect : '';
	      		this.loading = true;
	      		apis.doctorList({department: departmentId, keyword: this.keyword, token: this.$root.token}, function(data) {
	      			data ? this.loading = false : '';
	      			if(data.info == '您还没有登录！！') {
						this.$alert(data.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				            	location.reload();
				          	}
				        });
	            	}
	            	else {
	            		this.doctorList = data.data;
						this.total = data.total;
						this.pageSize = data.per_page;
						// 把导航列表的id和list-order添加进数组里，用作修改排序
						this.idListArr = [];
						this.doctorListOrderArr = [];
						this.cloneListOrder = [];
						this.originalListOrderArr = [];
						this.updateListOrderArr = [];
						for(var i = 0; i < data.data.length; i ++) {
			    			this.idListArr.push(data.data[i].rid);
			    			this.doctorListOrderArr.push(data.data[i].list_order);
			    		}
			    		Object.assign(this.cloneListOrder, this.doctorListOrderArr);
			    		if(this.keyword != '' || this.selectedOptions != '') {
							this.search = true;
						}
						else {
							this.search = false;
						}
						this.currentPage = 1;
	            	}
	      		}.bind(this))
	      	},
            // page改变
            currentChange(page) {
            	this.loading = true;
            	var keyword, departmentId;
            	if(this.search) {
            		this.keyword != '' ? keyword = this.keyword : '';
            		this.selectedOptions != '' ? departmentId = this.departmentSelect : '';
            	}
            	this.page = page;
            	apis.doctorList({page: page, department: departmentId, keyword: this.keyword, token: this.$root.token}, function(data) {
					data ? this.loading = false : '';
					if(data.code == 0) {
						this.$alert(data.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				            	location.reload();
				          	}
				        });
					}
					else {
						this.doctorList = data.data;
						this.idListArr = [];
						this.doctorListOrderArr = [];
						this.cloneListOrder = [];
						this.originalListOrderArr = [];
						this.updateListOrderArr = [];
						// 把导航列表的id和list-order添加进数组里，用作修改排序
						for(var i = 0; i < data.data.length; i ++) {
			    			this.idListArr.push(data.data[i].rid);
			    			this.doctorListOrderArr.push(data.data[i].list_order);
			    		}
			    		Object.assign(this.cloneListOrder, this.doctorListOrderArr);
					}
				}.bind(this))
            },
            // 消息提醒，公共部分
            alertMsg: function(_this, info, code) {
            	_this.$alert(info, {
	         	 	confirmButtonText: '确定',
		          	callback: action => {
		          		if(info == '您还没有登录！！') {
			            	location.reload();
		            	}
		            	else if(code == 1) {
		            		_this.reload();
		            	}
		          	}
		        });
            }
		},
		created() {
			apis.doctorList({token: this.$root.token}, function(data) {
				data ? this.loading = false : '';
				if(data.code == 0) {
					this.$alert(data.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	location.reload();
			          	}
			        });
				}
				else {
					this.doctorList = data.data;
					this.total = data.total;
					this.pageSize = data.per_page;
					// 把导航列表的id和list-order添加进数组里，用作修改排序
					for(var i = 0; i < data.data.length; i ++) {
		    			this.idListArr.push(data.data[i].rid);
		    			this.doctorListOrderArr.push(data.data[i].list_order);
		    		}
		    		Object.assign(this.cloneListOrder, this.doctorListOrderArr);
				}
			}.bind(this))

			apis.departmentList({token: this.$root.token}, function(data) {
				this.departmentList = data;
				this.departmentList.shift();
			}.bind(this))
		}
	}
</script>

<style lang="less">
	.doctor {
		.change-list-order {
			margin-bottom: 15px;
		}
		.change-status {
			margin-left: 10px;
		}
		.search {
			margin-bottom: 15px;
			.el-input {
				width: auto;
			}
			.search-btn {
				margin-left: 15px;
			}
		}
		.search {
			font-size: 15px;
		}
		.doctorLink {
			white-space: nowrap;
		    color: #409EFF;
		    text-decoration: none;
		}
		.set-working {
			font-size: 15px;
		    padding-left: 5%;
		    margin-top: 20px;
	     	.title {
		    	margin-right: 15px;
		    	width: 80px;
		    	text-align: right;
		    	display: inline-block;
		    }
			.el-input {
				width: 150px;
			}
			.mt-3{
				margin-top: 10px;
			}
		}
		.flex {
			display: flex;
			.add-doctor {
				padding: 0;
			}
		}
		.page {
			text-align: center;
			margin-top: 15px;
		}
		.set-working-table {
	        border-collapse:collapse;
		    border-top: 1px solid #ebeef5;
	        border-left: 1px solid #ebeef5;
            width: 100%;
    		text-align: center;
    		user-select:none;
			thead {
				color: #909399;
			}
			td {
				font-weight: 500;
				padding: 12px 10px;
			    border-bottom: 1px solid #ebeef5;
		        border-right: 1px solid #ebeef5;
			}
		}
	}
</style>


