<!-- 医生编辑 -->
<template>
	<div class="doctor-edit">
		<div class="flex">
			<div style="padding-left: 5%;width: 470px;">
				<div class="add-doctor">
					<span class="title">科室</span>
				 	<el-select v-model="departmentSelect" placeholder="请选择">
					    <el-option v-for="item in departmentList" :key="item.id" :label="item.name" :value="item.id">
					    </el-option>
				  	</el-select>
		    		<span class="required">*</span>
				</div>
				<div class="add-doctor">
					<span class="title">医生名字</span>
				 	<el-input v-model="doctorName" placeholder="请输入医生名字" clearable></el-input>
		    		<span class="required">*</span>
				</div>
				<div class="add-doctor">
					<span class="title">职务</span>
				 	<el-input v-model="doctorDuties" placeholder="请输入医生职务" clearable></el-input>
		    		<span class="required">*</span>
				</div>
				<div class="add-doctor">
		    		<span class="title">职称</span>
				 	<el-input v-model="doctorJob" placeholder="请输入医生职称" clearable></el-input>
		    		<span class="required">*</span>
				</div>
			</div>
			<div style="width: 60%; flex-wrap: wrap;" class="flex">
				<div class="img-upload flex" style="width:280px;margin-right:15px;">
		    		<div style="margin-right: 15px;width:112px;text-align:right;"><span class="title">医生详情照片</span></div>
		    		<div class="preview-img" v-if="detailImgBase64 != ''">
						<img :src="detailImgBase64" class="slide-add-title">
						<el-button @click="destoryImgBase64('detail')" v-if="detailImgBase64 != ''" style="margin-top: 15px;">取消图片</el-button>
					</div>
					<div>
						<div class="el-upload el-upload--picture-card" v-if="detailImgBase64 == ''">
							<i class="el-icon-plus"></i>
							<input type="file" @change="fileImage" accept="image/jpeg,image/x-png,image/gif" accessKey="detail">
						</div>
					</div>
		    	</div>
		    	<div class="img-upload flex" style="width:280px;">
		    		<div style="margin-right: 15px;"><span class="title">医生列表头像</span></div>
		    		<div class="preview-img" v-if="listImgBase64 != ''">
						<img :src="listImgBase64" class="slide-add-title">
						<el-button @click="destoryImgBase64()" v-if="listImgBase64 != ''" style="margin-top: 15px;">取消图片</el-button>
					</div>
					<div>
						<div class="el-upload el-upload--picture-card" v-if="listImgBase64 == ''">
							<i class="el-icon-plus"></i>
							<input type="file" @change="fileImage" accept="image/jpeg,image/x-png,image/gif">
						</div>
					</div>
		    	</div>
			</div>
		</div>
		<div class="add-doctor">
			<span class="title" style="vertical-align: top;">专业擅长</span>
		 	<div id="editorElem" class="editorElem" style="text-align:left"></div>
		</div>
		<div class="add-doctor">
			<span class="title" style="vertical-align: top;">医生介绍</span>
		 	<div id="editorElem2" class="editorElem" style="text-align:left"></div>
		</div>
		<div class="add-doctor">
			<span class="title" style="vertical-align: top;">医生头衔</span>
		 	<div id="editorElem3" class="editorElem" style="text-align:left"></div>
		</div>
		<div class="add-doctor" style="margin-bottom: 15px;">
			<el-button type="primary" @click="submitBtn()">{{type == 'add' ? '添加' : '保存'}}</el-button>
    		<el-button @click="goBack()">返回</el-button>
		</div>
	</div>
</template>

<script>
	import apis from '../../apis'
	import E from 'wangeditor'

	export default {
		inject: ['reload', 'reloadTab'],
		data() {
			return {
				departmentList: [],
				departmentSelect: '',
				doctorName: '',
				doctorDuties: '',
				doctorJob: '',
				doctorProficient: '',
				doctorIntroduction: '',
				doctorChampion: '',
				detailImgBase64: '',
				listImgBase64: '',
				type: '',
			}
		},
		methods: {
			// 提交
			submitBtn() {
				if(this.type == 'add') {
					apis.doctorAdd({doctor_name: this.doctorName, department_id: this.departmentSelect, doctor_duties: this.doctorDuties, doctor_job: this.doctorJob, doctor_proficient: this.doctorProficient, doctor_introduction: this.doctorIntroduction, doctor_champion: this.doctorChampion, detail_thumb: this.detailImgBase64, list_thumb: this.listImgBase64, token: this.$root.token}, function(bck) {
	            		this.$options.methods.alertMsg(this, bck.info, bck.code);
					}.bind(this))
				}
				else if(this.type == 'edit') {
					apis.doctorEdit({id: this.doctorId, doctor_name: this.doctorName, department_id: this.departmentSelect, doctor_duties: this.doctorDuties, doctor_job: this.doctorJob, doctor_proficient: this.doctorProficient, doctor_introduction: this.doctorIntroduction, doctor_champion: this.doctorChampion, detail_thumb: this.detailImgBase64, list_thumb: this.listImgBase64, token: this.$root.token}, function(bck) {
						this.$options.methods.alertMsg(this, bck.info, bck.code);
					}.bind(this))
				}
			},
            // 取消图片
            destoryImgBase64(type) {
            	type == 'detail' ? this.detailImgBase64 = '' : this.listImgBase64 = '';
            },
			// 上传图片
		    fileImage: function(e) {
		    	var self = this;
                var file = e.target.files[0];
                var reader = new FileReader();
                reader.readAsDataURL(file); // base64
                reader.onloadend = function() {
                    // 图片的 base64 格式
                    e.srcElement.accessKey == 'detail' ? self.detailImgBase64 = reader.result : self.listImgBase64 = reader.result;
                };
            },
			// 返回
			goBack() {
				this.$emit("change", "first");
			},
			// 消息提醒，公共部分
            alertMsg: function(_this, info, code) {
            	_this.$alert(info, {
	         	 	confirmButtonText: '确定',
		          	callback: action => {
		          		if(info == '您还没有登录！！') {
			            	location.reload();
		            	}
		            	else if(code == 1) {
		            		_this.reload();
		            	}
		          	}
		        });
            }
		},
		created() {
			this.departmentList = this.$root.departmentList;
			this.type = this.$root.propType;
			if(this.type == 'edit') {
				this.doctorId = this.$root.editDoctor.id;
				this.departmentSelect = this.$root.editDoctor.department_id;
				this.doctorName = this.$root.editDoctor.doctor_name;
				this.doctorJob = this.$root.editDoctor.doctor_job;
				this.doctorDuties = this.$root.editDoctor.doctor_duties;
				this.detailImgBase64 = this.$root.editDoctor.thumb.detail_thumb;
				this.listImgBase64 = this.$root.editDoctor.thumb.list_thumb;
				this.doctorIntroduction = this.$root.editDoctor.doctor_introduction;
				this.doctorProficient = this.$root.editDoctor.doctor_proficient;
				this.doctorChampion = this.$root.editDoctor.doctor_champion;
			}
		},
		mounted() {
     		// 初始化富文本编辑器
	        var editor = new E('#editorElem');
	        var editor2 = new E('#editorElem2');
	        var editor3 = new E('#editorElem3');
			editor.customConfig.uploadImgShowBase64 = true;   // 使用 base64 保存图片
  			editor2.customConfig.uploadImgShowBase64 = true;
  			editor3.customConfig.uploadImgShowBase64 = true;
          	// 自定义配置颜色（字体颜色、背景色）
		    editor.customConfig.colors = [
		        '#000000', '#eeece0', '#1c487f', '#4d80bf', '#ff0000', '#8baa4a', '#7b5ba1', '#46acc8', '#f9963b', '#ffffff'
		    ]
		    editor2.customConfig.colors = [
		        '#000000', '#eeece0', '#1c487f', '#4d80bf', '#ff0000', '#8baa4a', '#7b5ba1', '#46acc8', '#f9963b', '#ffffff'
		    ]
		    editor3.customConfig.colors = [
		        '#000000', '#eeece0', '#1c487f', '#4d80bf', '#ff0000', '#8baa4a', '#7b5ba1', '#46acc8', '#f9963b', '#ffffff'
		    ]
		    // 自定义菜单配置
		    editor.customConfig.menus = ['head', 'bold', 'fontSize', 'fontName', 'italic', 'underline', 'strikeThrough', 'foreColor', 'backColor', 'link', 'list', 'justify', 'quote', 'table', 'undo']
		    editor2.customConfig.menus = ['head', 'bold', 'fontSize', 'fontName', 'italic', 'underline', 'strikeThrough', 'foreColor', 'backColor', 'link', 'list', 'justify', 'quote', 'table', 'undo']
		    editor3.customConfig.menus = ['head', 'bold', 'fontSize', 'fontName', 'italic', 'underline', 'strikeThrough', 'foreColor', 'backColor', 'link', 'list', 'justify', 'quote', 'table', 'undo']

	        editor.customConfig.onchange = (html) => {
	          this.doctorProficient = html;
	        }
	        editor2.customConfig.onchange = (html) => {
	          this.doctorIntroduction = html;
	        }
	        editor3.customConfig.onchange = (html) => {
	        	this.doctorChampion = html;
	        }

	        editor.customConfig.debug = true;
	        editor2.customConfig.debug = true;
	        editor3.customConfig.debug = true;

	        editor.create();
	        editor2.create();
	        editor3.create();

	        editor.txt.html(this.doctorProficient);
	        editor2.txt.html(this.doctorIntroduction);
	        editor3.txt.html(this.doctorChampion);
      	}
	}
</script>

<style lang="less">
	.doctor-edit {
		.w-e-text-container {
			height: 250px !important;
			z-index: 999 !important;
		}
		.w-e-toolbar .w-e-menu {
			z-index: 1900 !important;
		}
		.img-upload {
			margin-top: 20px;
			.preview-img {
	        	width: 135px;
	        	img {
	        		width: 100%;
	        	}
	        }
			.el-upload {
			    position: relative;
			    line-height: 148px;
			}
			.el-upload input {
			    position: absolute;
			    right: 0;
			    top: 0;
			    opacity: 0;
			    cursor: pointer;
	            width: 100%;
    			height: 100%;
			}
			.title {
				font-size: 15px;
			}
		}
		.editorElem {
		    width: 75%;
			display: inline-block;
		}
		.add-doctor {
			font-size: 15px;
		    padding-left: 5%;
		    margin-top: 20px;
	     	.title {
		    	margin-right: 15px;
		    	width: 80px;
		    	text-align: right;
		    	display: inline-block;
		    }
			.el-input, .el-textarea {
				width: 217px;
			}
		}
	}
</style>


