<!-- 友情链接 -->
<template>
	<div class="right-content link">
		<el-tabs v-model="activeName" class="content" @tab-click="changeTab">

		    <el-tab-pane label="友情链接" name="first">

		    	<!-- 修改按钮 -->
				<el-button @click="changeListOrder()" type="primary" plain class="change-list-order">修改显示顺序</el-button>

				<el-table :data="linkList" style="width: 100%" :border='true' v-loading="loading">
					<el-table-column label="显示顺序" width="100">
	    				<template slot-scope="scope">
	    					<el-input v-model="listOrder[scope.$index]" :placeholder="linkList[scope.$index].list_order" type="number"></el-input>
	    				</template>
	    			</el-table-column>
	    			<el-table-column label="ID" prop="id" width="80"></el-table-column>
	    			<el-table-column label="链接名称" prop="name"></el-table-column>
				 	<el-table-column label="链接地址" prop="url"></el-table-column>
	    			<el-table-column label="状态" width="80">
	    				<template slot-scope="scope">
	    					<div>{{linkList[scope.$index].status == 1 ? '已启用' : '已禁用'}}</div>
	    				</template>
	    			</el-table-column>
				 	<el-table-column label="操作" width="130">
	      				<template slot-scope="scope">
	    					<el-button type="text" size="small" @click="changeStatus(scope.row)">{{linkList[scope.$index].status == 1 ? '禁用' : '启用'}}</el-button>
	        				<el-button type="text" size="small" @click="edit(scope.row)">修改</el-button>
	    					<el-button type="text" size="small" @click="deleteLink(scope.row)">删除</el-button>
	      				</template>
	    			</el-table-column>
	  			</el-table>
		    </el-tab-pane>
			
		    <el-tab-pane label="添加友情链接" name="second">
				<div class="add-link">
					<span class="title">链接名称</span>
					<el-input v-model="addLinkName" placeholder="请输入友情链接名称"></el-input>
		    		<span class="required">*</span>
				</div>
				<div class="add-link">
					<span class="title">链接地址</span>
					<el-input v-model="addUrl" placeholder="请输入友情链接地址"></el-input>
		    		<span class="required">*</span>
				</div>
				<div class="add-link">
					<span class="title" style="vertical-align: top;">链接描述</span>
					<el-input v-model="addLinkRemark" type="textarea" :autosize="{ minRows: 4, maxRows: 4}"></el-input>
				</div>
				<div class="add-link">
					<span class="title" style="vertical-align: top;">图标链接</span>
					<transition name="el-fade-in-linear">
						<img :src="base64" alt="" width="300px" v-show="base64">
					</transition>
					<div class="el-upload el-upload--picture-card img-upload" v-if="base64 == ''">
						<i class="el-icon-plus"></i>
						<input type="file" @change="fileImage" accept="image/jpeg,image/x-png,image/gif">
					</div>
					<el-button v-else style="margin-left: 15px;" @click="cancelImg()">取消图片</el-button>
				</div>
				<div class="add-link">
		    		<el-button type="primary" @click="addLink()">添加</el-button>
		    		<el-button @click="goBack()">返回</el-button>
		    	</div>
		    </el-tab-pane>
  		</el-tabs>
		
		<!-- 修改友情链接 -->
  		<el-dialog title="修改友情链接" :visible.sync="linkEditDialog" width="40%">
  			<div class="flex">
  			<div class="edit-link-box">
		  	<div class="edit-link">
		  		<span>链接名称</span>
		  		<el-input v-model="editLinkName" placeholder="请输入友情链接名称"></el-input>
	    		<span class="required">*</span>
		  	</div>
			<div class="edit-link">
				<span>链接地址</span>
				<el-input v-model="editUrl"></el-input>
	    		<span class="required">*</span>
			</div>
			<div class="edit-link">
				<span style="vertical-align: top;">链接描述</span>
				<el-input v-model="editLinkRemark" type="textarea" :autosize="{ minRows: 4, maxRows: 4}"></el-input>
			</div>
			<div class="edit-link">
				<span class="title">链接图片</span>
				<el-button style="position:relative;">更换图片
					<input class="upload-btn" type="file" @change="fileImage" accept="image/jpeg,image/x-png,image/gif" name="edit">
				</el-button>
			</div>
			</div>
			<div style="width: 50%;margin-left: 20px;">
						<div class="title">图片预览</div>
						<div class="text-center">
							<img :src="base64 != '' ? base64 : editLinkImg" width="80%" style="margin-top: 15px;">
						</div>
					</div>
			</div>
		  	<span slot="footer" class="dialog-footer">
		    	<el-button @click="linkEditDialog = false">取 消</el-button>
		    	<el-button type="primary" @click="editSubmit()">确 定</el-button>
		  	</span>
		</el-dialog>
	</div>
</template>

<script>
	import apis from '../../apis'

	export default {
		inject: ['reload', 'reloadTab'],
		data() {
			return {
				activeName: 'first',
				loading: true,
				linkEditDialog: false,
				linkList: [],
				listOrder: [],
				cloneListOrder: [],
				linkListOrderArr: [],
				originalListOrderArr: [],
				linkId: [],
				addLinkName: '',
				addUrl: '',
				base64: '',
				addLinkRemark: '',
				editLinkName: '',
				editUrl: '',
				editLinkImg: '',
				editLinkRemark: '',
				id: 0,
			}
		},
		methods: {
			//改变头部选项卡
			changeTab(tab, event) {
				if(tab.index == 0) {
					this.addLinkName = '';
					this.addUrl = '';
					this.addLinkRemark = '';
					this.base64 = '';
				}
			},
			// 显示 & 隐藏
			changeStatus(row) {
				row.status == 1 ? row.status = 0 : row.status = 1;
				apis.linkListStatus({id: row.id, show: row.status, token: this.$root.token}, function(bck) {
					this.$alert(bck.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	if(bck.code != 0) {
			            		this.reload();
			            	}
			            	else {
			            		location.reload();
			            	}
			          	}
			        });
				}.bind(this))
			},
			// 修改显示顺序
			changeListOrder() {
				var l_order = this.listOrder;
		    	var id = this.linkId;

		    	// 将更改前和更改后发给后台做数据对比修改显示顺序
				for(var i = 0; i < id.length; i++) {
					this.linkListOrderArr.push({id: JSON.parse(JSON.stringify(id))[i], list_order: JSON.parse(JSON.stringify(l_order))[i]});
					this.originalListOrderArr.push({id: JSON.parse(JSON.stringify(id))[i], list_order: JSON.parse(JSON.stringify(this.cloneListOrder))[i]});
				}

            	apis.linkListListOrder({list_order: this.linkListOrderArr, original_list_order: this.originalListOrderArr, token: this.$root.token}, function(bck) {
            		bck.info == '排序失败' ? bck.info = '排序成功' : '';
					this.$alert(bck.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			          		if(bck.info == '您还没有登录！！') {
			            		location.reload();
							}
							else if(bck.code == 1) {
								this.reload();
								this.reloadTab();
							}
			          	}
			        });
            	}.bind(this))
			},
			// 编辑
			edit(row) {
				this.linkEditDialog = true;
				this.editLinkName = row.name;
				this.editUrl = row.url;
				this.editLinkImg = row.image;
				this.editLinkRemark = row.description;
				this.id = row.id;
			},
            // 返回
            goBack() {
            	this.base64 = '';
        		this.activeName = 'first';
        		this.addLinkName = '';
				this.addUrl = '';
				this.addLinkRemark = '';
            },
            // 上传图片
		    fileImage:function(e) {
		    	var self = this;
                var file = e.target.files[0];
                var reader = new FileReader();
                reader.readAsDataURL(file); // base64
                reader.onloadend = function () {
                    // 图片的 base64 格式
                    self.base64 = reader.result;
                };
            },
            // 添加
            addLink() {
            	apis.linkAdd({name: this.addLinkName, url: this.addUrl, image: this.base64, description: this.addLinkRemark, token: this.$root.token}, function(bck) {
					this.$options.methods.alertMsg(this, bck.info, bck.code);
            	}.bind(this))
            },
            // 删除
            deleteLink(row) {
            	apis.linkDelete({id: row.id, token: this.$root.token}, function(bck) {
					this.$options.methods.alertMsg(this, bck.info, bck.code);
            	}.bind(this))
            },
            // 保存修改
            editSubmit() {
            	var img = '';
            	this.base64 != '' ? img = this.base64 : img = this.editLinkImg;
            	apis.linkEdit({id: this.id, name: this.editLinkName, url: this.editUrl, image: img, description: this.editLinkRemark, token: this.$root.token}, function(bck) {
            		bck.info == '保存失败' ? bck.info = '保存成功' : '';
					this.$options.methods.alertMsg(this, bck.info, bck.code);
				}.bind(this))
            },
            // 取消图片
            cancelImg() {
            	this.base64 = '';
            },
            alertMsg: function(_this, info, code) {
            	_this.$alert(info, {
	         	 	confirmButtonText: '确定',
		          	callback: action => {
		            	if(code == 1) {
		            		_this.reload();
		            	}
		            	else if(info == '您还没有登录！！') {
		            		location.reload();
		            	}
		          	}
		        });
            }
		},
		created() {
			apis.linkList({token: this.$root.token}, function(data) {
				data ? this.loading = false : '';
				if(data.code == 0) {
					this.$alert(data.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	location.reload();
			          	}
			        });
				}
				else {
					this.linkList = data;
					// 把导航列表的id和list-order添加进数组里，用作修改排序
					for(var i = 0; i < data.length; i ++) {
		    			this.linkId.push(data[i].id);
		    			this.listOrder.push(data[i].list_order);
		    		}
		    		Object.assign(this.cloneListOrder, this.listOrder);
				}
			}.bind(this))
		}
	}
</script>

<style lang="less">
	.link {
		.change-list-order {
			margin-bottom: 15px;
		}
		.edit-link-box{
			width: 45%;
		.edit-link {
			margin: 0 0 15px 15px;
			.el-input {
				width: auto;
			}
			.el-textarea {
				width: 202px;
			}
			span {
				margin-right: 15px;
			}
		}
		}
		.add-link {
			font-size: 15px;
		    padding-left: 5%;
		    margin-top: 20px;
	     	.title {
		    	margin-right: 15px;
		    }
			.el-input, .el-textarea {
				width: 217px;
			}
		}
		.upload-btn {
			position: absolute;
		    right: 0;
		    top: 0;
		    opacity: 0;
		    cursor: pointer;
            width: 100%;
			height: 100%;
		}
		.img-upload {
		    position: relative;
		    line-height: 148px;
			input {
			    position: absolute;
			    right: 0;
			    top: 0;
			    opacity: 0;
			    cursor: pointer;
	            width: 100%;
    			height: 100%;
			}
		}
	}
</style>


