<!-- 后台公告 -->
<template>
	<div class="right-content announcement">
		<div class="content">
			<div class="text">
				<span class="title" style="vertical-align: top;">公告内容</span>
				<el-input v-model="announcement" type="textarea" placeholder="请输入内容" :autosize="{ minRows: 3, maxRows: 4}"></el-input>
			</div>
			<div class="text">
				<span class="title">更新时间</span>
				<b>{{updateTime}}</b>
			</div>
			<div class="text" style="padding-left: 94px;">
				<el-button type="primary" @click="submitSave()">保存</el-button>
			</div>
		</div>
	</div>
</template>

<script>
	import apis from '../../apis'

	export default {
		inject: ['reload', 'reloadTab'],
		data() {
			return {
				announcement: '',
				announcementId: '',
				updateTime: '',
			}
		},
		methods: {
			submitSave() {
				apis.announcementEdit({id: this.announcementId, content: this.announcement, token: this.$root.token}, function(bck) {
					this.$alert(bck.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	if(bck.info == '您还没有登录！！') {
			            		location.reload();
			            	}
			            	else if(bck.code != 0) {
			            		this.reload();
			            	}
			          	}
			        });
				}.bind(this))
			}
		},
		created() {
			apis.announcement({token: this.$root.token}, function(data) {
				if(data.code == 0) {
					this.$alert(data.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	location.reload();
			          	}
			        });
				}
				else {
					this.announcement = data.content;
					this.updateTime = data.update_time;
					this.announcementId = data.id;
				}
			}.bind(this))
		}
	}
</script>

<style lang="less">
	.announcement {
		.text {
			margin-top: 20px;
			font-size: 15px;
			.title {
				margin-right: 15px;
				width: 75px;
				text-align: right;
				display: inline-block;
			}
			 .el-textarea, .el-input {
				width: 300px;
			}
		}
	}
</style>


