<!-- 角色管理 -->
<template>
	<div class="right-content role-management">
		<el-tabs v-model="activeName" class="content" @tab-click="changeTab">

		    <el-tab-pane label="角色管理" name="first">
				<el-table :data="roleList" style="width: 100%" :border='true' v-loading="loading">
	    			<el-table-column label="ID" prop="id" width="80"></el-table-column>
	    			<el-table-column label="角色名称" prop="name"></el-table-column>
				 	<el-table-column label="角色描述" prop="remark"></el-table-column>
	    			<el-table-column label="状态" width="50">
	    				<template slot-scope="scope">
	    					<div>{{roleList[scope.$index].status == 1 ? '√' : '×'}}</div>
	    				</template>
	    			</el-table-column>
				 	<el-table-column label="操作" width="160">
	      				<template slot-scope="scope">
	      					<el-button type="text" size="small" @click="setPermissions(scope.row)">权限设置</el-button>
	        				<el-button type="text" size="small" @click="openEdit(scope.row)">修改</el-button>
	    					<el-button type="text" size="small" @click="deleteRole(scope.row)">删除</el-button>
	      				</template>
	    			</el-table-column>
	  			</el-table>
		    </el-tab-pane>
			
		    <el-tab-pane label="添加角色" name="second">
				<div class="add-role">
					<span class="title">角色名称</span>
					<el-input v-model="addRoleName" placeholder="请输入角色名称"></el-input>
		    		<span class="required">*</span>
				</div>
				<div class="add-role">
					<span class="title" style="vertical-align: top;">角色描述</span>
					<el-input v-model="addRoleRemark" type="textarea" :autosize="{ minRows: 4, maxRows: 4}"></el-input>
				</div>
				<div class="add-role">
		    		<span class="title">角色状态</span>
		    		<el-select v-model="addRoleStatu" placeholder="如不选默认为开启">
		    			<el-option v-for="item in status" :key="item.value" :label="item.label" :value="item.value"></el-option>
		  			</el-select>
		    	</div>
				<div class="add-role">
		    		<el-button type="primary" @click="addRole()">添加</el-button>
		    		<el-button @click="goBack()">返回</el-button>
		    	</div>
		    </el-tab-pane>

		    <el-tab-pane label="权限设置" name="third" v-if="permissionsList != ''">
		    	<el-tree :data="permissionsList" show-checkbox check-strictly node-key="id" :default-checked-keys="permissionsCheck" :props="defaultProps" ref="tree"></el-tree>
		    	<div class="permissions-bottom-btn">
		    		<el-button type="primary" @click="savePermissions()">保存</el-button>
		    		<el-button @click="goBack('permissions')">返回</el-button>
		    	</div>
		    </el-tab-pane>
  		</el-tabs>

  		<el-dialog title="修改角色" :visible.sync="roleEditVisible" width="30%">
  			<div class="edit-role">
				<span class="title">角色名称</span>
				<el-input v-model="editRoleName"></el-input>
				<span class="required">*</span>
			</div>
			<div class="edit-role">
				<span class="title" style="vertical-align: top;">角色描述</span>
				<el-input v-model="editRoleRemark" type="textarea" :autosize="{ minRows: 2 }"></el-input>
			</div>
  			<span slot="footer" class="dialog-footer">
    			<el-button @click="roleEditVisible = false">取 消</el-button>
    			<el-button type="primary" @click="submitEdit()">确 定</el-button>
  			</span>
		</el-dialog>
	</div>
</template>

<script>
	import apis from '../../apis'

	export default {
		inject: ['reload', 'reloadTab'],
		data() {
			return {
				activeName: 'first',
				loading: true,
				roleEditVisible: false,
                status: [{value: 1, label: '显示'},{value: 0, label: '隐藏'}],
				defaultProps: {children: 'children', label: 'label'},
				roleList: [],
				addRoleName: '',
				addRoleRemark: '',
				addRoleStatu: 1,
				editRoleName: '',
				editRoleRemark: '',
				roleId: 0,
				permissionsCheck: [],
				permissionsList: [],
			}
		},
		methods: {
			//改变头部选项卡
			changeTab(tab, event) {
				if(tab.index == 1) {
					this.addRoleName = '';
					this.addRoleRemark = '';
					this.addRoleStatu = 1;
				}
				else if(tab.index != 3) {
					this.permissionsList = [];
				}
			},
			// 添加角色
			addRole() {
				if(this.addRoleName == '') {
					this.$alert('请填写角色名称');
					return;
				}
				else {
					apis.roleAdd({name: this.addRoleName, remark: this.addRoleRemark, status: this.addRoleStatu, token: this.$root.token}, function(bck) {
						this.$options.methods.alertMsg(this, bck.info);
					}.bind(this))
				}
			},
			// 返回
			goBack(type) {
				this.addRoleName = '';
				this.addRoleRemark = '';
				this.addRoleStatu = 1;
				this.activeName = 'first';
				if(type == 'permissions') {
					this.permissionsList = [];
					this.permissionsCheck = [];
				}
			},
			// 删除
			deleteRole(row) {
				apis.roleDelete({id: row.id, token: this.$root.token}, function(bck) {
					this.$options.methods.alertMsg(this, bck.info);
				}.bind(this))
			},
			// 打开修改
			openEdit(row) {
				this.roleEditVisible = true;
				this.editRoleName = row.name;
				this.editRoleRemark = row.remark;
				this.roleId = row.id;
			},
			// 修改提交
			submitEdit() {
				apis.roleEdit({id: this.roleId, name: this.editRoleName, remark: this.editRoleRemark, token: this.$root.token}, function(bck) {
					bck.info == '修改失败' ? bck.info = '修改成功' : '';
					this.$options.methods.alertMsg(this, bck.info);
				}.bind(this))
			},
			// 权限设置
			setPermissions(row) {
				apis.permissionsCheck({role_id: row.id, token: this.$root.token}, function(bck) {
					if(bck.code == 0) {
						this.$alert(info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
			            		location.reload();
				          	}
				        });
					}
					else {
						this.permissionsCheck = bck;
						this.roleId = row.id;
					}
				}.bind(this))

				apis.permissionsList({token: this.$root.token}, function(data) {
					this.permissionsList = data;
					this.activeName = 'third';
				}.bind(this))
			},
			// 保存权限设置
			savePermissions() {
				apis.permissionsEdit({role_id: this.roleId, menuIds: this.$refs.tree.getCheckedKeys(), token: this.$root.token}, function(bck) {
					this.$options.methods.alertMsg(this, bck.info);
				}.bind(this))
			},
			alertMsg: function(_this, info) {
				_this.$alert(info, {
	         	 	confirmButtonText: '确定',
		          	callback: action => {
		            	if(info == '您还没有登录！！') {
		            		location.reload();
		            	}
		            	else {
		            		_this.reload();
		            	}
		          	}
		        });
			}
		},
		created() {
			apis.roleList({token: this.$root.token}, function(data) {
				data ? this.loading = false : '';

				if(data.code == 0) {
					this.$alert(data.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	location.reload();
			          	}
			        });
				}
				else {
					this.roleList = data;
				}
			}.bind(this))
		}
	}
</script>

<style lang="less">
	.role-management {
		.add-role {
		    font-size: 15px;
			padding-left: 5%;
			margin-top: 20px;
			.el-input, .el-textarea {
				width: 217px;
			}
		}
		.edit-role {
			margin: 0 15px 15px;
			.el-input {
	            width: auto;
	        }
	        .el-textarea {
	        	width: 202px;
	        }
		}
		.el-tree {
			padding-left: 15px;
			width: 340px;
		}
		.permissions-bottom-btn {
			margin-top: 15px;
		}
		.title {
			margin-right: 15px;
		}
	}
</style>


