<!-- 本站用户 -->
<template>
	<div class="right-content user-management">
		<div class="content">
			<el-table :data="userList" style="width: 100%" :border='true' v-loading="loading">
				<el-table-column label="ID" prop="id" width="80"></el-table-column>
				<el-table-column label="用户名" prop="user_login"></el-table-column>
			 	<el-table-column label="昵称" prop="user_nickname"></el-table-column>
			 	<el-table-column label="头像" width="80">
					<template slot-scope="scope">
						<img :src="url + userList[scope.$index].avatar" alt="">
					</template>
				</el-table-column>
			 	<el-table-column label="最后登录时间" prop="last_login_time"></el-table-column>
			 	<el-table-column label="最后登录IP" prop="last_login_ip"></el-table-column>
				<el-table-column label="状态" width="80">
					<template slot-scope="scope">
						<div>{{userList[scope.$index].user_status == 1 ? '已启用' : '已禁用'}}</div>
					</template>
				</el-table-column>
			 	<el-table-column label="操作" width="80">
	  				<template slot-scope="scope">
						<el-button type="text" size="small" @click="changeStatus(scope.row)">{{userList[scope.$index].user_status == 1 ? '禁用' : '启用'}}</el-button>
	  				</template>
				</el-table-column>
			</el-table>
		</div>
	</div>
</template>

<script>
	import apis from '../../apis'

	export default {
		inject: ['reload', 'reloadTab'],
		data() {
			return {
				loading: true,
				userList: [],
				url: 'http://admin.dedaokq.com/',
			}
		},
		methods: {
			// 改变状态
			changeStatus(row) {
				row.user_status == 1 ? row.user_status = 0 : row.user_status = 1;
				apis.userBan({id: row.id, open: row.user_status, token: this.$root.token}, function(bck) {
					this.$alert(bck.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
							if(bck.code == 0) {
				            	location.reload();
							}
			          	}
			        });
				}.bind(this))
			}
		},
		created() {
			apis.userList({token: this.$root.token}, function(data) {
				data ? this.loading = false : '';

				if(data.code == 0) {
					this.$alert(data.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	location.reload();
			          	}
			        });
				}
				else {
					this.userList = data;
				}
			}.bind(this))
		}
	}
</script>

<style lang="less">
	.user-management {
		padding-top: 15px;
		img {
		    width: 55%;
		}
	}
</style>


