<!-- 百科分类 -->
<template>
	<div class="right-content encyclopedia-category">
		<el-tabs v-model="activeName" class="content"  @tab-click="changeTab">
			<el-tab-pane label="分类管理" name="first">
				<!-- 修改排序 -->
				<el-button @click="changeListOrder()" type="primary" size="medium" plain class="change-list-order">修改排序</el-button>

				<el-table :data="encyclopediaCategoryList" style="width: 100%" :border='true' v-loading="loading">
					<el-table-column label="显示顺序" width="100">
	    				<template slot-scope="scope">
	    					<el-input v-model="encyclopediaCategoryListOrderArr[scope.$index]" type="number"></el-input>
	    				</template>
	    			</el-table-column>
					<el-table-column label="ID" prop="id" width="80"></el-table-column>
					<el-table-column label="分类名称">
						<template slot-scope="scope">
							<a :href="url + encyclopediaCategoryList[scope.$index].id" target="_blank">{{encyclopediaCategoryList[scope.$index].name}}</a>
		  				</template>
					</el-table-column>
				 	<el-table-column label="状态" width="80">
				 		<template slot-scope="scope">
				 			<el-button type="text" size="small" @click="changeStatus(scope.row)">{{encyclopediaCategoryList[scope.$index].status == 1 ? '显示' : '隐藏'}}</el-button>
				 		</template>
				 	</el-table-column>
					<el-table-column label="操作" width="100">
		  				<template slot-scope="scope">
							<el-button type="text" size="small" @click="editEncyclopediaCategory(scope.row)">编辑</el-button>
	    					<el-button type="text" size="small" @click="deleteEncyclopediaCategory(scope.row)">删除</el-button>
		  				</template>
					</el-table-column>
				</el-table>
			</el-tab-pane>

			<el-tab-pane label="添加分类" name="second">
		    	<div class="add-encyclopedia-category">
					<span class="title">分类名称</span>
					<el-input placeholder="请输入分类名称" v-model="addEncyclopediaCategoryName" clearable></el-input>
					<span class="required">*</span>
		    	</div>
		    	<div class="add-encyclopedia-category">
		    		<span class="title" style="vertical-align: top;">分类描述</span>
		    		<el-input placeholder="请输入分类描述" v-model="addEncyclopediaDescription" type='textarea' :autosize="{ minRows: 2, maxRows: 4}"></el-input>
		    	</div>
		    	<div class="add-encyclopedia-category">
		    		<span class="title">SEO标题</span>
		    		<el-input placeholder="请输入SEO标题" v-model="addSeoTitle" clearable></el-input>
		    	</div>
		    	<div class="add-encyclopedia-category">
		    		<span class="title">SEO关键词</span>
		    		<el-input placeholder="请输入关键词" v-model="addSeoKeyword" clearable></el-input>
		    	</div>
		    	<div class="add-encyclopedia-category">
		    		<span class="title" style="vertical-align: top;">SEO描述</span>
		    		<el-input v-model="addSeoDescription" clearable type="textarea" :autosize="{ minRows: 2, maxRows: 4}"></el-input>
		    	</div>
		    	<div class="add-encyclopedia-category">
		    		<span class="title">列表模板</span>
					<el-select v-model="listTemplate" placeholder="请选择">
	    				<el-option v-for="item in listTPL" :key="item.id" :label="item.name" :value="item.id"></el-option>
	  				</el-select>
		    	</div>
		    	<div class="add-encyclopedia-category">
		    		<span class="title">文章模板</span>
					<el-select v-model="articleTemplate" placeholder="请选择">
	    				<el-option v-for="item in listTPL" :key="item.id" :label="item.name" :value="item.id"></el-option>
	  				</el-select>
		    	</div>
		    	<div class="add-encyclopedia-category">
		    		<el-button type="primary" @click="addEncyclopediaCategory()">添加</el-button>
		    		<el-button @click="goBack()">返回</el-button>
		    	</div>
			</el-tab-pane>
		</el-tabs>

		<!-- 修改 -->
		<el-dialog title="编辑分类" :visible.sync="dialogVisible" class="edit-dialog" width="40%">
			<div class="edit-encyclopedia-category">
				<span class="title">分类名称</span>
				<el-input placeholder="请输入分类名称" v-model="editEncyclopediaCategoryName" clearable></el-input>
				<span class="required">*</span>
	    	</div>
	    	<div class="edit-encyclopedia-category">
	    		<span class="title" style="vertical-align: top;">分类描述</span>
	    		<el-input placeholder="请输入分类描述" v-model="editEncyclopediaDescription" type='textarea' :autosize="{ minRows: 2, maxRows: 4}"></el-input>
	    	</div>
	    	<div class="edit-encyclopedia-category">
	    		<span class="title">SEO标题</span>
	    		<el-input placeholder="请输入SEO标题" v-model="editSeoTitle" clearable></el-input>
	    	</div>
	    	<div class="edit-encyclopedia-category">
	    		<span class="title">SEO关键词</span>
	    		<el-input placeholder="请输入关键词" v-model="editSeoKeyword" clearable></el-input>
	    	</div>
	    	<div class="edit-encyclopedia-category">
	    		<span class="title" style="vertical-align: top;">SEO描述</span>
	    		<el-input v-model="editSeoDescription" clearable type="textarea" :autosize="{ minRows: 2, maxRows: 4}"></el-input>
	    	</div>
	    	<div class="edit-encyclopedia-category">
	    		<span class="title">列表模板</span>
				<el-select v-model="editListTemplate" placeholder="请选择">
    				<el-option v-for="item in listTPL" :key="item.id" :label="item.name" :value="item.id"></el-option>
  				</el-select>
	    	</div>
	    	<div class="edit-encyclopedia-category">
	    		<span class="title">文章模板</span>
				<el-select v-model="editArticleTemplate" placeholder="请选择">
    				<el-option v-for="item in listTPL" :key="item.id" :label="item.name" :value="item.id"></el-option>
  				</el-select>
	    	</div>
	  		<span slot="footer" class="dialog-footer">
		    	<el-button @click="dialogVisible = false;">取消</el-button>
			    <el-button type="primary" @click="submitData()">确定</el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	import apis from '../../apis'

	export default {
		inject: ['reload', 'reloadTab'],
		data() {
			return {
				url: 'http://www.cdddkqyy.com/encyclopedia/list/',
				activeName: 'first',
				loading: true,
				encyclopediaCategoryList: [],
				listTPL: [],
				idListArr: [],
				encyclopediaCategoryListOrderArr: [],
				cloneListOrder: [],
				originalListOrderArr: [],
				listOrderArr: [],
				listTemplate: 0,
				articleTemplate: 0,
				addEncyclopediaCategoryName: '',
				addEncyclopediaDescription: '',
				addSeoTitle: '',
				addSeoKeyword: '',
				addSeoDescription: '',
				dialogVisible: false,
				editEncyclopediaCategoryName: '',
				editEncyclopediaDescription: '',
				editSeoTitle: '',
				editSeoKeyword: '',
				editSeoDescription: '',
				editListTemplate: '',
				editArticleTemplate: '',
				encyclopediaCategoryId: Number,
			}
		},
		methods: {
			//改变头部选项卡
			changeTab(tab, event) {
				if(tab.index != 1) {
					this.addEncyclopediaCategoryName = '';
					this.addEncyclopediaDescription = '';
					this.addSeoTitle = '';
					this.addSeoKeyword = '';
					this.addSeoDescription = '';
				}
			},
			// 添加
			addEncyclopediaCategory() {
				apis.encyclopediaCategoryAdd({parent_id: 4, name: this.addEncyclopediaCategoryName, description: this.addEncyclopediaDescription, seo_title: this.addSeoTitle, seo_keywords: this.addSeoKeyword, seo_description: this.addSeoDescription, list_tpl: this.listTemplate, one_tpl: this.articleTemplate, token: this.$root.token}, function(bck) {
					this.$options.methods.alertMsg(this, bck.info, bck.code);
				}.bind(this))
			},
			// 返回
			goBack() {
				this.activeName = 'first';
				this.addEncyclopediaCategoryName = '';
				this.addEncyclopediaDescription = '';
				this.addSeoTitle = '';
				this.addSeoKeyword = '';
				this.addSeoDescription = '';
			},
			// 删除
			deleteEncyclopediaCategory(row) {
				apis.encyclopediaCategoryDelete({id: row.id, token: this.$root.token}, function(bck) {
					this.$options.methods.alertMsg(this, bck.info, bck.code);
				}.bind(this))
			},
			// 编辑
			editEncyclopediaCategory(row) {
				this.dialogVisible = true;
				this.editEncyclopediaCategoryName = row.name;
				this.editEncyclopediaDescription = row.description;
				this.editSeoTitle = row.seo_title;
				this.editSeoKeyword = row.seo_keywords;
				this.editSeoDescription = row.seo_description;
				this.editListTemplate = row.list_tpl;
				this.editArticleTemplate = row.one_tpl;
				this.encyclopediaCategoryId = row.id;
			},
			// 提交
			submitData() {
				apis.encyclopediaCategoryEdit({parent_id: 4, id: this.encyclopediaCategoryId, name: this.editEncyclopediaCategoryName, description: this.editEncyclopediaDescription, seo_title: this.editSeoTitle, seo_keywords: this.editSeoKeyword, seo_description: this.editSeoDescription, list_tpl: this.editListTemplate, one_tpl: this.editArticleTemplate, token: this.$root.token}, function(bck) {
					this.$options.methods.alertMsg(this, bck.info, bck.code);
				}.bind(this))
			},
			// 排序
			changeListOrder() {
	      		var l_order = this.encyclopediaCategoryListOrderArr;
	    		var id = this.idListArr;
	    		
	    		for(var i = 0; i < id.length; i++) {
					this.listOrderArr.push({id: JSON.parse(JSON.stringify(id))[i], list_order: JSON.parse(JSON.stringify(l_order))[i]})
					this.originalListOrderArr.push({id: JSON.parse(JSON.stringify(id))[i], list_order: JSON.parse(JSON.stringify(this.cloneListOrder))[i]})
				}
				apis.encyclopediaCategoryListOrder({list_order: this.listOrderArr, original_list_order: this.originalListOrderArr, token: this.$root.token}, function(bck) {
					this.$options.methods.alertMsg(this, bck.info, bck.code);
				}.bind(this))
			},
	      	// 消息提示框，公共部分
	      	alertMsg: function(_this, info, code) {
		    	_this.$alert(info, {
	         	 	confirmButtonText: '确定',
		          	callback: action => {
		            	if(code != 0) {
		            		_this.reload();
		            	}
		            	else if(info == '您还没有登录！！') {
		            		location.reload();
		            	}
		          	}
		        });
		    },
	      	// 显示 & 隐藏
	      	changeStatus(row) {
	      		var statu;
	      		row.status == 1 ? statu = 0 : statu = 1;
	      		apis.encyclopediaCategoryStatus({id: row.id, status: statu, token: this.$root.token}, function(bck) {
	      			bck.code == 1 ? row.status = statu : '';
	      			if(bck.info == '您还没有登录！！') {
	      				this.$alert(bck.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				          		location.reload();
				          	}
				        });
	      			}
	      		}.bind(this))
	      	}
		},
		created() {
			apis.encyclopediaCategoryList({token: this.$root.token}, function(data) {
				data ? this.loading = false : '';

				if(data.code == 0) {
					this.$alert(data.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	location.reload();
			          	}
			        });
				}
				else {
					this.encyclopediaCategoryList = data;
					// 把导航列表的id和list-order添加进数组里，用作修改排序
					for(var i = 0; i < data.length; i ++) {
		    			this.idListArr.push(data[i].id);
		    			this.encyclopediaCategoryListOrderArr.push(data[i].list_order);
		    		}
		    		Object.assign(this.cloneListOrder, this.encyclopediaCategoryListOrderArr);
				}
			}.bind(this))

			apis.themeList({token: this.$root.token}, function(data) {
				this.listTPL = data;
				this.listTPL.unshift({'id': 0, 'name': '默认'});
			}.bind(this))
		}
	}
</script>

<style lang="less">
	.encyclopedia-category {
		.change-list-order {
			margin-bottom: 15px;
		}
		.add-encyclopedia-category, .edit-encyclopedia-category {
			margin-top: 20px;
			font-size: 15px;
			padding-left: 5%;
			.title {
				margin-right: 15px;
				width: 75px;
				text-align: right;
				display: inline-block;
			}
			.el-input, .el-textarea {
				width: 217px;
			}
			.editorElem {
			    width: 75%;
    			display: inline-block;
			}
		}
		a {
			white-space: nowrap;
		    color: #409EFF;
		    text-decoration: none;
		}
	}
</style>


