<!-- 首页 -->
<template>
	<div class="right-content index">
		<div class="announcement">
			<i class="fa fa-bullhorn"></i>
			<span>公告：{{announcementContent}}</span>
			<span class="time">— &nbsp; {{updateTime}}</span>
		</div>
		<div class="welcome">欢迎使用{{dbName}}德道网站管理后台</div>
	</div>
</template>

<script>
	import apis from '../apis'

	export default {
		data() {
			return {
				announcementContent: '',
				updateTime: '',
				dbName: '',
			}
		},
		created() {
			apis.announcement({token: this.$root.token}, function(data) {
				this.announcementContent = data.content;
				this.updateTime = data.update_time;
			}.bind(this))

			this.dbName = JSON.parse(localStorage.getItem('userInfo')).db_name;
		}
	}
</script>

<style lang="less">
	.index {
		.welcome {
			line-height: 30;
			text-align: center;
			font-weight: 600;
			font-size: 20px;
		}
		.announcement {
			margin: 0 25px;
			padding: 10px 0;
			border-bottom: 1px solid rgb(181, 181, 181);
			span {
				margin-left: 5px;
			}
			.fa {
				vertical-align: middle;
			}
		}
		.time {
			font-size: 14px;
		}
	}
</style>


