<!-- 清除缓存 -->
<template>
	<div class="right-content clear-cache">
		<div class="content">
			<el-button v-loading.fullscreen.lock="fullscreenLoading"></el-button>
		</div>
	</div>
</template>

<script>
	import apis from '../../apis'

	export default {
		data() {
			return {
				fullscreenLoading: true
			}
		},
		method: {

		},
		created() {
			apis.clearCache({token: this.$root.token}, function(bck) {
				var self = this;
				if(bck.code == 1) {
					setTimeout(function() {
						self.fullscreenLoading = false;
						self.$alert(bck.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				            	self.$router.push({path:'/'});
				          	}
				        });
					}, 300)
				}
				else {
					self.$alert('您还没有登录！！', {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	location.reload();
			          	}
			        });
				}
			}.bind(this))
		}
	}
</script>

<style lang="less">
	.clear-cache {
		.el-button {
			opacity: 0;
		}
	}
</style>


