<!-- 问答分类 -->
<template>
	<div class="right-content question-category">
		<el-tabs v-model="activeName" @tab-click="changeTab" class="content">
			<el-tab-pane label="分类管理" name="first">
				<!-- 修改按钮 -->
				<el-button @click="changeListOrder()" type="primary" plain class="change-list-order">修改排序</el-button>
				
				<el-table :data="questionCategoryList" style="width: 100%" :border='true' v-loading="loading">
					<el-table-column label="显示顺序" width="100">
	    				<template slot-scope="scope">
	    					<el-input v-model="questionCategoryListOrderArr[scope.$index]" type="number"></el-input>
	    				</template>
	    			</el-table-column>
	    			<el-table-column label="ID" prop="id" width="80"></el-table-column>
      				<el-table-column label="菜单名称">
      					<template slot-scope="scope">
      						<div v-if="questionCategoryList[scope.$index]._level == 1">
      							<a :href="url + questionCategoryList[scope.$index].id + '.html'" target="_blank">{{questionCategoryList[scope.$index].name}}</a>
      						</div>
      						<div v-else-if="questionCategoryList[scope.$index]._level == 2">
      							<a :href="url + questionCategoryList[scope.$index].id + '.html'" target="_blank"><span style="margin: 0 15px 0 30px;">├─</span>{{questionCategoryList[scope.$index].name}}</a>
      						</div>
      						<div v-else-if="questionCategoryList[scope.$index]._level == 3">
      							<a :href="url + questionCategoryList[scope.$index].id + '.html'" target="_blank"><span style="margin: 0 15px 0 60px;">├─</span>{{questionCategoryList[scope.$index].name}}</a>
      						</div>
      						<div v-else-if="questionCategoryList[scope.$index]._level == 4">
      							<a :href="url + questionCategoryList[scope.$index].id + '.html'" target="_blank"><span style="margin: 0 15px 0 90px;">├─</span>{{questionCategoryList[scope.$index].name}}</a>
      						</div>
      					</template>
      				</el-table-column>
				 	<el-table-column label="状态" width="80">
				 		<template slot-scope="scope">
				 			<el-button type="text" size="small" @click="changeStatus(scope.row)">{{questionCategoryList[scope.$index].status == 1 ? '显示' : '隐藏'}}</el-button>
				 		</template>
				 	</el-table-column>
				 	<el-table-column label="操作" width="170">
	      				<template slot-scope="scope">
	        				<el-button type="text" size="small" @click="addCategoryChildren(scope.row)">添加子类</el-button>
	        				<el-button type="text" size="small" @click="editCategory(scope.row)">编辑</el-button>
	    					<el-button type="text" size="small" @click="deleteCategory(scope.row)">删除</el-button>
	      				</template>
	    			</el-table-column>
				</el-table>
			</el-tab-pane>

			<el-tab-pane label="添加分类" name="second">
				<div class="add-question-category">
					<span class="title">选择上级</span>
					<el-cascader expand-trigger="click" :options="questionCategoryListTree" v-model="selectedOptions" change-on-select></el-cascader>
				  	<span class="required">*</span>
				</div>
				<div class="add-question-category">
					<span class="title">分类名称</span>
					<el-input v-model="addQuestionCategoryTitle" placeholder="请输入分类标题"></el-input>
	    			<span class="required">*</span>
				</div>
				<div class="add-question-category">
					<span class="title" style="vertical-align: top;">分类描述</span>
					<el-input v-model="addQuestionCategoryDescription" type="textarea" placeholder="请输入分类描述"></el-input>
				</div>
				<div class="add-question-category">
					<span class="title">SEO标题</span>
					<el-input v-model="addSeoTitle"></el-input>
				</div>
				<div class="add-question-category">
					<span class="title">SEO关键词</span>
					<el-input v-model="addSeoKeyword"></el-input>
				</div>
				<div class="add-question-category">
					<span class="title" style="vertical-align: top;">SEO描述</span>
					<el-input v-model="addSeoDescription" type="textarea"></el-input>
				</div>
				<div class="add-question-category">
		    		<span class="title">列表模板</span>
					<el-select v-model="listTemplate" placeholder="请选择">
	    				<el-option v-for="item in listTPL" :key="item.id" :label="item.name" :value="item.id"></el-option>
	  				</el-select>
		    	</div>
		    	<div class="add-question-category">
		    		<span class="title">文章模板</span>
					<el-select v-model="articleTemplate" placeholder="请选择">
	    				<el-option v-for="item in listTPL" :key="item.id" :label="item.name" :value="item.id"></el-option>
	  				</el-select>
		    	</div>
				<div class="add-question-category" style="margin-top: 15px;">
					<el-button type="primary" @click="addQuestionCategory()">添加</el-button>
					<el-button @click="goBack()">返回</el-button>
				</div>
			</el-tab-pane>
		</el-tabs>

		<!-- 编辑与添加子类 -->
	  	<el-dialog :title="type == 'addChildren' ? '添加子类' : '编辑分类'" :visible.sync="dialogVisible" width="40%">
	  		<p class="edit-question-category" v-if="type == 'addChildren'">
  				<span class="title">上级名称</span><b>{{parentName}}</b>
  			</p>
  			<p class="edit-question-category" v-else>
  				<span class="title">选择上级</span>
				<el-cascader expand-trigger="click" :options="questionCategoryListTree" v-model="selectedOptions2" change-on-select></el-cascader>
			  	<span class="required">*</span>
  			</p>
			<p class="edit-question-category">
				<span class="title">分类名称</span>
				<el-input v-model="questionCategoryTitle" placeholder="请输入分类标题"></el-input>
    			<span class="required">*</span>
			</p>
			<p class="edit-question-category">
				<span class="title" style="vertical-align: top;">分类描述</span>
				<el-input v-model="questionCategoryDescription" type="textarea" placeholder="请输入分类描述"></el-input>
			</p>
			<p class="edit-question-category">
				<span class="title">SEO标题</span>
				<el-input v-model="seoTitle"></el-input>
			</p>
			<p class="edit-question-category">
				<span class="title">SEO关键词</span>
				<el-input v-model="seoKeyword"></el-input>
			</p>
			<p class="edit-question-category">
				<span class="title" style="vertical-align: top;">SEO描述</span>
				<el-input v-model="seoDescription" type="textarea"></el-input>
			</p>
			<p class="edit-question-category">
	    		<span class="title">列表模板</span>
				<el-select v-model="listTemplate" placeholder="请选择">
    				<el-option v-for="item in listTPL" :key="item.id" :label="item.name" :value="item.id"></el-option>
  				</el-select>
	    	</p>
	    	<p class="edit-question-category">
	    		<span class="title">文章模板</span>
				<el-select v-model="articleTemplate" placeholder="请选择">
    				<el-option v-for="item in listTPL" :key="item.id" :label="item.name" :value="item.id"></el-option>
  				</el-select>
	    	</p>
			<span slot="footer" class="dialog-footer">
		    	<el-button @click="dialogVisible = false">取 消</el-button>
			    <el-button type="primary" @click="submitData()">{{type == 'eidt' ? '保存' : '添加'}}</el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	import apis from '../../apis'

	export default {
		inject: ['reload', 'reloadTab'],
		data() {
			return {
				activeName: 'first',
				url: 'http://www.cdddkqyy.com/qa/list/',
				loading: true,
				dialogVisible: false,
				questionCategoryList: [],
				questionCategoryListTree: [],
        		questionCategoryListOrderArr: [],
        		listOrder: [],
        		listOrderId: [],
				cloneListOrder: [],
				originalListOrderArr: [],
				selectedOptions: [],
				selectedOptions2: [],
				addQuestionCategoryTitle: '',
				addQuestionCategoryDescription: '',
				addSeoTitle: '',
				addSeoKeyword: '',
				addSeoDescription: '',
				listTemplate: 0,
				articleTemplate: 0,
				listTPL: [],
				type: '',
				questionCategoryTitle: '',
				questionCategoryDescription: '',
				seoTitle: '',
				seoKeyword: '',
				seoDescription: '',
				categoryId: Number,
				parentId: Number,
				parentName: '',
			}
		},
		methods: {
			// 切换头部导航
			changeTab(tab, event) {
				if(tab.index == 1) {
					this.selectedOptions = [];
					this.addQuestionCategoryTitle = '';
					this.addQuestionCategoryDescription = '';
					this.addSeoTitle = '';
					this.addSeoKeyword = '';
					this.addSeoDescription = '';
					this.listTemplate = 0;
					this.articleTemplate = 0;
				}
			},
			// 添加
			addQuestionCategory() {
				apis.questionCategoryAdd({name: this.addQuestionCategoryTitle, parent_id: this.selectedOptions[this.selectedOptions.length - 1], description: this.addQuestionCategoryDescription, seo_title: this.addSeoTitle, seo_keywords: this.addSeoKeyword, seo_description: this.addSeoDescription, list_tpl: this.listTemplate, one_tpl: this.articleTemplate, token: this.$root.token}, function(bck) {
					// 调用公共部分
					this.$options.methods.alertMsg(this, bck.info, bck.code);
				}.bind(this))
			},
			// 返回
			goBack() {
				this.activeName = 'first';
				this.selectedOptions = [];
				this.addQuestionCategoryTitle = '';
				this.addQuestionCategoryDescription = '';
				this.addSeoTitle = '';
				this.addSeoKeyword = '';
				this.addSeoDescription = '';
				this.listTemplate = 0;
				this.articleTemplate = 0;
			},
			// 添加子类
			addCategoryChildren(row) {
				this.type = 'addChildren';
				this.dialogVisible = true;
				this.parentId = row.id;
				this.parentName = row.name;
			},
			// 编辑
			editCategory(row) {
				this.type = 'edit';
				this.dialogVisible = true;
				var shift = row.path.split(',');
				if(shift.length==1){
                    	this.selectedOptions2=[0];
                    	
                   }else{
                    	this.selectedOptions2=[];
                    	shift.pop();
                    	for(var s=0; s<shift.length;s++){ 			
                	 	this.selectedOptions2.push(Number(shift[s]));
                	}
                    }
//				this.questionCategoryTitle = row.name;
				this.questionCategoryDescription = row.description;
				this.seoTitle = row.seo_title;
				this.seoKeyword = row.seo_keywords;
				this.seoDescription = row.seo_description;
				this.listTemplate = row.list_tpl;
				this.articleTemplate = row.one_tpl;
				this.categoryId = row.id;
			},
			// 提交
			submitData() {
				if(this.type == 'edit') {
					apis.questionCategoryEdit({id: this.categoryId, name: this.questionCategoryTitle, parent_id: this.selectedOptions2[this.selectedOptions2.length - 1], description: this.questionCategoryDescription, seo_title: this.seoTitle, seo_keywords: this.seoKeyword, seo_description: this.seoDescription, list_tpl: this.listTemplate, one_tpl: this.articleTemplate, token: this.$root.token}, function(bck) {
						this.$options.methods.alertMsg(this, bck.info, bck.code);
					}.bind(this))
				}
				else if(this.type == 'addChildren') {
					apis.questionCategoryAdd({name: this.questionCategoryTitle, parent_id: this.parentId, description: this.questionCategoryDescription, seo_title: this.seoTitle, seo_keywords: this.seoKeyword, seo_description: this.seoDescription, list_tpl: this.listTemplate, one_tpl: this.articleTemplate, token: this.$root.token}, function(bck) {
						this.$options.methods.alertMsg(this, bck.info, bck.code);
					}.bind(this))
				}
			},
			// 删除
			deleteCategory(row) {
				apis.questionCategoryDelete({id: row.id, token: this.$root.token}, function(bck) {
					this.$options.methods.alertMsg(this, bck.info, bck.code);
				}.bind(this))
			},
			// 排序
			changeListOrder() {
				for(var i = 0; i < this.listOrderId.length; i++) {
					this.listOrder.push({id: JSON.parse(JSON.stringify(this.listOrderId))[i], list_order: JSON.parse(JSON.stringify(this.questionCategoryListOrderArr))[i]})
					this.originalListOrderArr.push({id: JSON.parse(JSON.stringify(this.listOrderId))[i], list_order: JSON.parse(JSON.stringify(this.cloneListOrder))[i]})
				}
				
				apis.questionCategoryListOrder({list_order: this.listOrder, original_list_order: this.originalListOrderArr, token: this.$root.token}, function(bck) {
					this.$options.methods.alertMsg(this, bck.info, bck.code);
				}.bind(this))
			},
			// 公共消息提示部分
			alertMsg: function(_this, info, code) {
            	_this.$alert(info, {
	         	 	confirmButtonText: '确定',
		          	callback: action => {
		            	if(code == 1) {
		            		_this.reload();
		            	}
		            	else if(info == '您还没有登录！！') {
		            		location.reload();
		            	}
		          	}
		        });
            },
		    // 显示 & 隐藏
	      	changeStatus(row) {
	      		var statu;
	      		row.status == 1 ? statu = 0 : statu = 1;
	      		apis.questionCategoryStatus({id: row.id, status: statu, token: this.$root.token}, function(bck) {
	      			bck.code == 1 ? row.status = statu : '';
	      			if(bck.info == '您还没有登录！！') {
	      				this.$alert(bck.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				          		location.reload();
				          	}
				        });
	      			}
	      		}.bind(this))
	      	}
		},
		created() {
			apis.questionCategoryList({token: this.$root.token}, function(data) {
				data ? this.loading = false : '';

				if(data.code == 0) {
					this.$alert(data.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	location.reload();
			          	}
			        });
				}
				else {
					this.questionCategoryList = data;

					// 把id和list-order添加进数组里，用作排序修改
		    		for(var i = 0; i < data.length; i ++) {
		    			this.listOrderId.push(data[i].id);
		    			this.questionCategoryListOrderArr.push(data[i].list_order);
		    		}
		    		Object.assign(this.cloneListOrder, this.questionCategoryListOrderArr);
				}
			}.bind(this))

			apis.questionCategoryListTree({token: this.$root.token}, function(data) {
				this.questionCategoryListTree = data;
				this.questionCategoryListTree.unshift({'value': 0, 'label': '作为一级菜单'});
				this.selectedOptions = [0];
			}.bind(this))

			apis.themeList({token: this.$root.token}, function(data) {
				this.listTPL = data;
				this.listTPL.unshift({'id': 0, 'name': '默认'});
			}.bind(this))
		}
	}
</script>

<style lang="less">
	.question-category {
		a {
			white-space: nowrap;
		    color: #409EFF;
		    text-decoration: none;
		}
	  	.change-list-order {
	  		margin-bottom: 15px;
	  	}
		.add-question-category, .edit-question-category {
		    font-size: 15px;
		    padding-left: 5%;
		    margin-top: 20px;
		    .title {
		    	margin-right: 15px;
		    	width: 75px;
		    	text-align: right;
		    	display: inline-block;
		    }
		    .el-input, .el-textarea {
		    	width: 217px;
		    }
		}
	}
</style>


