<!-- 专题列表 -->
<template>
	<div class="right-content topic">
		<el-tabs v-model="activeName" @tab-click="changeTab" class="content">
			<el-tab-pane label="专题列表" name="first">
				<div class="search">
					<span style="margin-left: 15px;">日期：</span>
					<el-date-picker v-model="date" type="daterange" align="center" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
					<span style="margin-left: 15px;">关键词：</span>
					<el-input v-model="keyword" placeholder="请输入内容" clearable></el-input>
					<el-button @click="searchArticle()" type="primary" size="medium" icon="el-icon-search" class="search-btn">搜索</el-button>
				</div>
				<el-table :data="topicList" style="width: 100%" :border='true' v-loading="loading">
					<el-table-column label="ID" prop="id" width="60"></el-table-column>
					<el-table-column label="专题名称">
						<template slot-scope="scope">
							<a :href="url + topicList[scope.$index].topic_url" target="_blank">{{topicList[scope.$index].topic_title}}</a>
						</template>
					</el-table-column>
					<el-table-column label="专题路径" prop="topic_url" width=""></el-table-column>
					<el-table-column label="创建时间" prop="create_time" width="180"></el-table-column>
					<el-table-column label="操作" width="150">
	      				<template slot-scope="scope">
	        				<el-button type="text" size="small" @click="changeStatus(scope.row)">{{topicList[scope.$index].status == 0 ? '显示' : '隐藏'}}</el-button>
	        				<el-button type="text" size="small" @click="editTopic(scope.row)">编辑</el-button>
	    					<el-button type="text" size="small" @click="deleteTopic(scope.row)">删除</el-button>
	      				</template>
	    			</el-table-column>
				</el-table>

				<el-pagination layout="prev, pager, next" :total="total" class="page" :page-size="pageSize" @current-change="currentChange" :current-page.sync="currentPage" v-if="total != 0"></el-pagination>
			</el-tab-pane>

			<el-tab-pane label="添加专题" name="second">
				<div class="add-topic">
					<span class="title">专题名称</span>
					<el-input placeholder="请输入名称" v-model="addTopicName"></el-input>
				  	<span class="required">*</span>
				</div>
				<div class="add-topic">
					<span class="title">专题路径</span>
					<el-input placeholder="请输入路径" v-model="addTopicUrl"></el-input>
				  	<span class="required">*</span>
				</div>
				<div class="add-topic">
					<el-button type="primary" @click="addTopic()">添加</el-button>
					<el-button @click="goBack()">返回</el-button>
				</div>
			</el-tab-pane>

			<el-dialog title="编辑专题" :visible.sync="dialogVisible" class="topic-edit" width="40%">
				<div class="edit-topic">
					<span class="title">专题名称</span>
					<el-input placeholder="请输入名称" v-model="editTopicName"></el-input>
				  	<span class="required">*</span>
				</div>
				<div class="edit-topic">
					<span class="title">专题路径</span>
					<el-input placeholder="请输入路径" v-model="editTopicUrl"></el-input>
				  	<span class="required">*</span>
				</div>
				<span slot="footer" class="dialog-footer">
			    	<el-button @click="dialogVisible = false">取消</el-button>
				    <el-button type="primary" @click="submitEdit()">保存</el-button>
				</span>
			</el-dialog>
		</el-tabs>
	</div>
</template>

<script>
	import apis from '../../apis'

	export default {
		inject: ['reload', 'reloadTab'],
		data() {
			return {
				loading: true,
				dialogVisible: false,
				activeName: 'first',
				url: 'http://www.cdddkqyy.com',
				topicList: [],
				date: '',
				keyword: '',
				total: 0,
				pageSize: 0,
				page: 1,
				addTopicName: '',
				addTopicUrl: '',
				editTopicName: '',
				editTopicUrl: '',
				topicId: '',
				search: false,
				currentPage: 1,
			}
		},
		methods: {
			// 切换头部栏
			changeTab(tab, event) {
				tab.index == 1 ? (this.topicName = '') | (this.topicUrl = '') : '';
			},
            // page改变
            currentChange(page) {
            	this.loading = true;
            	var startTime, endTime, keyword;
            	if(this.search) {
            		this.date != '' ? (startTime = this.date[0]) | (endTime = this.date[1]) : '';
            		this.keyword != '' ? keyword = this.keyword : '';
            	}
            	this.page = page;
            	apis.topicList({start_time: startTime, end_time: endTime, keyword: keyword, page: page, token: this.$root.token}, function(data) {
					data ? this.loading = false : '';
					if(data.code == 0) {
						this.$alert(data.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				            	location.reload();
				          	}
				        });
					}
					else {
						this.topicList = data.data;
						this.total = data.total;
						this.pageSize = data.per_page;
					}
				}.bind(this))
            },
	      	// 搜索
	      	searchArticle() {
	      		this.date === null ? this.date = '' : '';
	      		this.loading = true;
	      		apis.topicList({start_time: this.date[0], end_time: this.date[1], keyword: this.keyword, token: this.$root.token}, function(data) {
	      			data ? this.loading = false : '';
	      			if(data.code == 0) {
						this.$alert(data.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				            	location.reload();
				          	}
				        });
					}
					else {
						this.topicList = data.data;
						this.total = data.total;
						this.pageSize = data.per_page;
			    		if(this.date !== null || this.keyword != '') {
							this.search = true;
						}
						else {
							this.search = false;
						}
						this.currentPage = 1;
					}
	      		}.bind(this))
	      	},
			// 添加专题
			addTopic() {
				apis.topicAdd({topic_title: this.addTopicName, topic_url: this.addTopicUrl, token: this.$root.token}, function(bck) {
					this.$options.methods.alertMsg(this, bck.info, bck.code);
				}.bind(this))
			},
			// 返回
			goBack() {
				this.activeName = 'first';
			},
			// 编辑
			editTopic(row) {
				this.dialogVisible = true;
				this.editTopicName = row.topic_title;
				this.editTopicUrl = row.topic_url;
				this.topicId = row.id;
			},
			// 提交修改
			submitEdit() {
				apis.topicEdit({id: this.topicId, topic_title: this.editTopicName, topic_url: this.editTopicUrl, token: this.$root.token}, function(bck) {
					this.$options.methods.alertMsg(this, bck.info, bck.code);
				}.bind(this))
			},
			// 删除
			deleteTopic(row) {
				apis.topicDelete({id: row.id, token: this.$root.token}, function(bck) {
					this.$options.methods.alertMsg(this, bck.info, bck.code);
				}.bind(this))
			},
			// 显示 & 隐藏
			changeStatus(row) {
				var change = 0;
				row.status == 1 ? change = 0 : change = 1;
				apis.topicStatus({id: row.id, status: change, token: this.$root.token}, function(bck) {
					this.$options.methods.alertMsg(this, bck.info, bck.code);
				}.bind(this))
			},
	      	// 消息提示框，公共部分
	      	alertMsg: function(_this, info, code) {
		    	_this.$alert(info, {
	         	 	confirmButtonText: '确定',
		          	callback: action => {
		            	if(code != 0) {
		            		_this.reload();
		            	}
		            	else if(info == '您还没有登录！！') {
		            		location.reload();
		            	}
		          	}
		        });
		    }
		},
		created() {
			apis.topicList({token: this.$root.token}, function(data) {
				data ? this.loading = false : '';

				if(data.code == 0) {
					this.$alert(data.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	location.reload();
			          	}
			        });
				}
				else {
					this.topicList = data.data;
					this.total = data.total;
					this.pageSize = data.per_page;
				}
			}.bind(this))
		}
	}
</script>

<style lang="less">
	.topic {
		a {
			white-space: nowrap;
		    color: #409EFF;
		    text-decoration: none;
		}
		.search {
			margin-bottom: 15px;
			font-size: 15px;
			.el-input {
				width: auto;
			}
			.search-btn {
				margin-left: 15px;
			}
		}
		.add-topic, .edit-topic {
			font-size: 15px;
		    padding-left: 5%;
		    margin-top: 20px;
		    .title {
		    	margin-right: 15px;
		    }
		    .el-input {
		    	width: auto;
		    }
		}
		.page {
			text-align: center;
			margin-top: 15px;
		}
	}
</style>


