<!-- 抽奖添加与编辑 -->
<template>
	<div class="draw-edit">
		<div class="set">
			<span class="title">抽奖活动名称</span>
			<el-input placeholder="请输入名称" v-model="drawName"></el-input>
		  	<span class="required">*</span>
		</div>
		<div class="set">
			<span class="title">设置抽奖人数</span>
			<el-input placeholder="请输入数字" v-model="totalParticipant"></el-input>
		  	<span class="required">*</span>
		</div>
		<div class="set">
			<span class="title">设置抽奖奖品</span>
			<el-button @click="addOnceAward()">添加一个奖品</el-button>
		</div>
		<div class="set">
			<div class="set-award">
				<el-table :data="awardList" style="width: 100%" :border='true'>
					<el-table-column label="奖品名称" width="">
						<template slot-scope="scope">
							<el-input placeholder="请输入奖品名称" v-model="awardList[scope.$index].awardName"></el-input>
						</template>
					</el-table-column>
					<el-table-column label="奖品数量" width="">
						<template slot-scope="scope">
							<el-input placeholder="请输入数字" v-model="awardList[scope.$index].awardNumber"></el-input>
						</template>
					</el-table-column>
					<el-table-column label="每轮抽取数量" width="">
						<template slot-scope="scope">
							<el-input placeholder="若不填则一次抽取所有" v-model="awardList[scope.$index].drawRound"></el-input>
						</template>
					</el-table-column>
					<el-table-column label="内定号码" width="">
						<template slot-scope="scope">
							<el-input class="designated" placeholder="请输入数字，用逗号分隔" v-model="awardList[scope.$index].designated"></el-input>
						</template>
					</el-table-column>
					<el-table-column label="操作" width="80">
						<template slot-scope="scope">
							<el-button type="text" size="small" @click="deleteAward(scope.$index)">删除</el-button>
						</template>
					</el-table-column>
				</el-table>
			</div>
		</div>
		<div class="set">
			<el-button type="primary" @click="addOrEdit()">{{type == 'add' ? '添加' : '保存'}}</el-button>
			<el-button @click="goBack()">返回</el-button>
		</div>
	</div>
</template>

<script>
	import apis from '../../apis'

	export default {
		inject: ['reload', 'reloadTab'],
		data() {
			return {
				awardList: [{'awardName': '', 'awardNumber': '', 'designated': '', 'drawRound': ''}],
				drawName: '',
				totalParticipant: '',
				type: '',
				awardId: '',
			}
		},
		methods: {
			// 增加一行奖品
			addOnceAward() {
				this.awardList.push({'awardName': '', 'awardNumber': '', 'designated': '', 'drawRound': ''});
			},
			// 删除奖品
			deleteAward(inx) {
				this.awardList.splice(inx, 1);
			},
			// 添加 & 修改
			addOrEdit() {
				var id = '';
				this.awardId != '' ? id = this.awardId : '';
				if(this.totalParticipant == '') {
					this.$alert('请输入抽奖人数');
					return;
				}
				else if(!Number.isInteger(Number(this.totalParticipant))) {
					this.$alert('请输入数字');
					return;
				}
				for(var i = 0; i < this.awardList.length; i++) {
					if(this.awardList[i].awardName == '') {
						this.$alert('请输入奖品名称');
						return;
					}
					else if(this.awardList[i].awardNumber == ''){
						this.$alert('请输入奖品数量');
						return;
					}
					else if(!Number.isInteger(Number(this.awardList[i].awardNumber))) {
						this.$alert('请输入数字');
						return;
					}
				}
				apis.drawSet({id: id, award_name: this.drawName, award_total: this.totalParticipant, content: this.awardList, token: this.$root.token}, function(bck) {
					if(id == '') {
						if(bck.code == 1) {
							bck.info = '添加成功';
						}
					}
					this.$alert(bck.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			          		if(bck.info == '您还没有登录！！') {
				            	location.reload();
			            	}
			            	else if(bck.code == 1) {
			            		this.reload();
			            	}
			          	}
			        });
				}.bind(this))
			},
			// 返回
			goBack() {
				this.$emit("change", "first");
			}
		},
		created() {
			// 父组件点击编辑后进入组件，把得到的数据渲染
			this.type = this.$root.propType;
			if(this.$root.propType == 'add') {

			}
			else {
				var list = JSON.parse(JSON.stringify(this.$root.drawList));
				this.awardList = [];
				this.awardList = list.content;
				this.drawName = list.award_name;
				this.totalParticipant = list.award_total;
				this.awardId = list.id;
			}
		}
	}
</script>

<style lang="less">
	.draw-edit {
		.set {
		 	font-size: 15px;
		    padding-left: 5%;
		    margin-top: 20px;
		    .title {
		    	margin-right: 15px;
		    }
		    .el-input {
		    	width: auto;
		    }
		    .set-award {
		    	width: 70%;
		    }
		    .designated {
		    	width: 100%;
		    }
		}
	}
</style>


