<!-- 网页导航菜单编辑与添加 -->
<template>
	<div class="edit-nav">
		<div class="nav-class">
			<span class="title">导航分类</span>
			<el-select v-model="navClassification" @change="webNavSelect">
    			<el-option v-for="item in navClassList" :key="item.id" :label="item.name" :value="item.id"></el-option>
  			</el-select>
		</div>
		<div class="nva-parent">
			<span class="title">上级菜单</span>
			<el-cascader expand-trigger="click" :options="webNav" v-model="selectedOptions" change-on-select></el-cascader>
    	</div>
    	<div class="nav-name">
    		<span class="title">{{propType == 'edit' ? '修改名称' : '导航名称'}}</span>
    		<el-input placeholder="请输入导航名字" v-model="navName" clearable></el-input>
    		<span class="required">*</span>
    	</div>
    	<div class="nav-link">
    		<span class="title">导航链接</span>
    		<el-input placeholder="如果是一级菜单不用填" v-model="navHref" clearable></el-input>
    	</div> 
    	<div class="nav-statu">
    		<span class="title">{{propType == 'edit' ? '修改状态' : '导航状态'}}</span>
    		<el-select v-model="navStatu" placeholder="请选择">
    			<el-option v-for="item in status" :key="item.value" :label="item.label" :value="item.value"></el-option>
  			</el-select>
    		<span class="required">*</span>
    	</div>
    	<div class="nav-btn">
    		<el-button type="primary" @click="submitMenu()">{{propType == 'edit' ? '保存' : '添加'}}</el-button>
    		<el-button @click="goBack()">返回</el-button>
    	</div>
	</div>
</template>

<script>
	import apis from '../../apis'

	export default {
        inject: ['reload', 'reloadTab'],
		data() {
			return {
                navClassification: '',
                selectedOptions: [],
                navName: '',
                status: [{value: 1, label: '显示'},{value: 0, label: '隐藏'}],
                navClassList: {},
                webNav: [],
                propType: '',
                navHref: '',
                navStatu: '',
                canSubmit: false,
                editNav: {},
                navListId: ''
			}
		},
		methods: {
            // 保存 & 提交
            submitMenu() {
                var self = this;
                this.selectedOptions == '' ? alertMsg('请选择上级菜单') : this.navName == '' ? alertMsg('请输入导航名称') : this.navStatu === '' ? alertMsg('请选择菜单状态') : this.canSubmit = true;
                this.navHref == '' ? this.navHref = '/' : '';

                if(this.canSubmit) {
                    if(this.propType == 'add') {
                        apis.webNavAdd({cat_id: this.navClassification, parent_id: this.selectedOptions[this.selectedOptions.length-1], name: this.navName, href: this.navHref, target: '', status: this.navStatu, token: this.$root.token}, function(bck) {
                            bck.code == 1 ? alertMsg(bck.info, 'submit-success') : alertMsg(bck.info, 'not-login');
                        }.bind(this))
                    }
                    else if(this.propType == 'edit') {
                        apis.webNavEdit({cat_id: this.navClassification, parent_id: this.selectedOptions[this.selectedOptions.length-1], name: this.navName, href: this.navHref, target: '', status: this.navStatu, id: this.editNav.id, token: this.$root.token}, function(bck) {
                            bck.info == '修改失败' ? bck.info = '修改成功' : '';
                            this.$alert(bck.info, {
                                confirmButtonText: '确定',
                                callback: action => {
                                    if(bck.info == '您还没有登录！！') {
                                        location.reload();
                                    }
                                    else if(bck.info == '修改成功' || bck.code == 1) {
                                        this.reload();
                                    }
                                }
                            })
                        }.bind(this))
                    }
                }

                function alertMsg(msg, type) {
                    self.$alert(msg, {
                        confirmButtonText: '确定',
                        callback: action => {
                            if(type == 'submit-success') {
                                self.$emit("change", ["first", 1]);
                            }
                            else if(type == 'not-login') {
                                location.reload();
                            }
                        }
                    });
                }
            },
            // 返回
            goBack() {
                this.$emit("change", ["first", 0]);
            },
            // 菜单分类选择
            webNavSelect() {
                apis.webNav({id: this.navClassification, token: this.$root.token}, function(bck) {
                    if(bck.code !=='') {
                        this.webNav = bck;
                        this.webNav.unshift({'value': 0, 'label': '作为一级菜单'});
                        this.selectedOptions = [this.webNav[0].value];
                    }
                    else {
                        this.$alert(bck.info, {
                        confirmButtonText: '确定',
                        callback: action => {
                            location.reload();
                        }
                    });
                    }
                }.bind(this))
            }
		},
		created() {
            var navEditValue;
            var pathArr = new Array();
            this.propType = this.$root.propMenuType;
            this.navClassList = this.$root.navClassList;

            if(this.propType == 'add') {
                this.navListId = this.$root.catId;
            }
            else if(this.propType == 'edit') {
                this.editNav = this.$root.navEdit;
                this.navListId = this.editNav.cat_id;
                this.navName = this.editNav.label;
                this.navHref = this.editNav.href;
                this.navStatu = this.editNav.status;
                navEditValue = this.editNav.value;
                var path = this.editNav.path;
                // 编辑菜单里 '上级菜单'默认选中父菜单
                if(this.editNav.path.length > 1) {
                   this.selectedOptions.unshift({'value': 0, 'label': '作为一级菜单'});
                }
                else {
                    pathArr.push(0);
                }

            }
            // 根据主页面所停留的cat—id来匹配对应默认的导航分类选项
            for(var i = 0; i < this.navClassList.length; i++) {
                if(this.propType == 'edit') {
                    this.navClassList[i].id ==  this.editNav.cat_id ? this.navClassification = this.navClassList[i].id : '';
                }
                else if(this.propType == 'add') {
                    this.navClassList[i].id ==  this.navListId ? this.navClassification = this.navClassList[i].id : '';
                }
            }
            apis.webNav({id: this.navListId, token: this.$root.token}, function(bck) {
                if(bck.code == 0) {
                    this.$alert(bck.info, {
                        confirmButtonText: '确定',
                        callback: action => {
                            location.reload();
                        }
                    });
                }
                else {
                    this.webNav = bck;
                    this.webNav.unshift({'value': 0, 'label': '作为一级菜单'});
//                  this.propType == 'edit' ? this.selectedOptions[this.selectedOptions.length-1] : this.selectedOptions = [this.webNav[0].value];
                    this.selectedOptions = [this.webNav[0].value];
                }
            }.bind(this))
		}
	}
</script>

<style lang="less">
    .edit-nav {
        font-size: 15px;
        padding-left: 5%;
        margin-top: 20px;
        .nav-name .el-input, .nav-link .el-input {
            width: auto;
        }
        .nav-class, .nva-parent, .nav-name, .nav-link, .nav-statu, .nav-btn {
            margin-top: 10px;
        }
        .nav-btn {
            margin-top: 20px;
        }
        .title {
            margin-right: 15px;
        }
    }
</style>


