<!-- 问卷调查 -->
<template>
	<div class="right-content investigation">
		<el-tabs v-model="activeName" @tab-click="changeTab" class="content">
			<el-tab-pane label="问卷管理" name="first">
				<el-table :data="investigationList" style="width: 100%" :border='true' v-loading="loading">
	    			<el-table-column label="ID" prop="id" width="60"></el-table-column>
	    			<el-table-column label="问卷名称" prop="questionnaire_title"></el-table-column>
	    			<el-table-column label="创建时间" prop="create_time" width="180"></el-table-column>
				 	<el-table-column label="操作" width="230">
	      				<template slot-scope="scope">
	        				<el-button type="text" size="small" @click="checkData(scope.row)">查看数据</el-button>
	        				<el-button type="text" size="small" @click="openAnalysisData(scope.row)">交叉分析</el-button>
	        				<el-button type="text" size="small" @click="editInvestigation(scope.row)">编辑</el-button>
	    					<el-button type="text" size="small" @click="deleteInvestigation(scope.row)">删除</el-button>
	      				</template>
	    			</el-table-column>
				</el-table>

				<el-pagination layout="prev, pager, next" :total="total" class="page" :page-size="pageSize" @current-change="currentChange"></el-pagination>
			</el-tab-pane>


			<el-tab-pane label="新建问卷" name="second">
				<investigationEditAndAdd v-if="add" v-on:change="activeChange($event)"></investigationEditAndAdd>
			</el-tab-pane>

			<el-tab-pane label="编辑问卷" name="third" v-if="edit">
				<investigationEditAndAdd v-on:change="activeChange($event)"></investigationEditAndAdd>
			</el-tab-pane>

			<el-tab-pane label="查看数据" name="fourth" v-if="check" class="check-data">
				<div class="title"><b>{{investigationTitle}}</b></div>
				
				<table v-for="(item, index) in investigationDataList">
					<thead>
						<tr>
							<th>{{index + 1}}、{{item.name}}（{{item.type == 1 ? '单选' : '多选'}}）</th>
							<th>小计</th>
							<th>比例</th>
						</tr>
					</thead>
					<tbody>
						<tr v-for="(item2, index) in item.children">
							<th>{{item2.name}}</th>
							<th>{{!item2.subtotal ? '0' : item2.subtotal}}</th>
							<th>{{item2.proportion}}</th>
						</tr>
					</tbody>
				</table>
			</el-tab-pane>

			<el-tab-pane label="交叉分析" name="firth" v-if="analysis" class="analysis-data">
				<div style="width: 80%;">
					<div class="title"><b>{{investigationTitle}}</b></div>
					<div class="d-flex">
						<div style="width: 50%">
							<p><b>自变量X</b>（一般为样本属性，例如性别，年龄等。限2题）</p>
							<p v-for="(itemX, inx) in x">
								<el-select v-model="x[inx]" placeholder="添加自变量">
								    <el-option v-for="(item, index) in investigationAnalysisList.content" :key="index" :label="item.name" :value="index"></el-option>
							  	</el-select>
							  	<el-button class="delete-condition" type="danger" icon="el-icon-delete" circle v-if="inx == 1" @click="x.splice(inx, 1)"></el-button>
							</p>
							<div class="add-condition" v-if="x.length < 2" @click="x.push('')">增加条件</div>
						</div>
						<div style="width: 50%">
							<p><b>自变量Y</b>（您要分析的目标题目。限2题）</p>
							<p v-for="(itemY, inx) in y">
								<el-select v-model="y[inx]" placeholder="添加自变量">
								    <el-option v-for="(item, index) in investigationAnalysisList.content" :key="index" :label="item.name" :value="index"></el-option>
							  	</el-select>
							  	<el-button class="delete-condition" type="danger" icon="el-icon-delete" circle v-if="inx == 1" @click="y.splice(inx, 1)"></el-button>
							</p>
							<div class="add-condition" v-if="y.length < 2" @click="y.push('')">增加条件</div>
						</div>
					</div>
					<div style="text-align: center;">
						<el-button type="primary" plain @click="analysisData()">数据分析</el-button>
					</div>
					
					<!-- 分析后的内容 -->
					<div class="el-table--border" style="margin-top: 30px;" v-if="analysisDataList != ''" v-for="item in analysisDataList">
						<table class="el-table" style="border-collapse: collapse;">
							<thead>
								<tr>
									<th class="is-leaf" v-for="title in item.title">
										<div class="cell">{{title}}</div>
									</th>
								</tr>
							</thead>
							<tbody>
								<tr v-for="data in item.data">
									<td v-for="item2 in data"><div class="cell">{{item2}}</div></td>
								</tr>
							</tbody>
						</table>
					</div>
				</div>
			</el-tab-pane>
		</el-tabs>
	</div>
</template>

<script>
	import apis from '../../apis'
	import investigationEditAndAdd from '@/views/investigation/investigation-EditAndAdd'

	export default {
		inject: ['reload', 'reloadTab'],
		components: {
			investigationEditAndAdd
		},
		data() {
			return {
				activeName: 'first',
				loading: true,
				total: 0,
				pageSize: 0,
				add: false,
				edit: false,
				check: false,
				analysis: false,
				investigationTitle: '',
				investigationList: [],
				investigationDataList: [],
				investigationAnalysisList: [],
				x: [''],
				y: [''],
				analysisDataList: [],
			}
		},
		methods: {
			// 切换头部栏
			changeTab(tab, event) {
				tab.index == 1 ? (this.add = true) | (this.$root.propType = 'add') : (this.add = false) | (this.edit = false);
				if(tab.index != 2) {
					this.edit = false;
					this.check = false;
					this.investigationDataList = [];
					this.analysis = false;
					this.x = [''];
					this.y = [''];
					this.analysisDataList = [];
				}
			},
            // page改变
            currentChange(page) {
            	this.loading = true;
            	this.page = page;
            	apis.investigationList({page: page, token: this.$root.token}, function(data) {
					data ? this.loading = false : '';
					if(data.code == 0) {
						this.$alert(data.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				            	location.reload();
				          	}
				        });
					}
					else {
						this.investigationList = data.data;
						this.total = data.total;
						this.pageSize = data.per_page;
					}
				}.bind(this))
            },
		    // 接收子组件 传回来的值 改变头部选项卡
		    activeChange(name) {
		    	this.activeName = name;
		    	this.add = false;
		    	this.edit = false;
		    },
		    // 编辑
		    editInvestigation(row) {
		    	this.$root.editInvestigation = row;
				this.activeName = 'third';
				this.edit = true;
				this.$root.propType = 'edit';
		    },
		    // 删除
		    deleteInvestigation(row) {
		    	apis.investigationDelete({id: row.id, token: this.$root.token}, function(bck) {
		    		this.$alert(bck.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	if(bck.code != 0) {
			            		this.reload();
			            	}
			            	else if(bck.info == '您还没有登录！！') {
			            		location.reload();
			            	}
			          	}
			        });
		    	}.bind(this))
		    },
		    // 查看数据
		    checkData(row) {
		    	this.loading = true;
		    	this.investigationTitle = row.questionnaire_title;
		    	this.activeName = 'fourth';
		    	this.check = true;

		    	apis.investigationDataList({id: row.id, token: this.$root.token}, function(data) {
		    		data ? this.loading = false : '';

		    		if(data.info == '您还没有登录！！') {
	            		this.$alert(data.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
			            		location.reload();
				          	}
				        });
	            	}
		    		this.investigationDataList = data;
		    	}.bind(this))
		    },
		    // 打开分析
		    openAnalysisData(row) {
		    	this.analysis = true;
		    	this.activeName = 'firth';
		    	this.investigationAnalysisList = row;
		    	this.investigationTitle = row.questionnaire_title;
		    },
		    // 分析
		    analysisData() {
		    	var x2 = -1;
		    	var y2 = -1;
		    	this.x.length == 2 ? x2 = this.x[1] : '';
		    	this.y.length == 2 ? y2 = this.y[1] : '';
		    	
		    	apis.analysisData({id: this.investigationAnalysisList.id, x: this.x[0], y: this.y[0], x2: x2, y2: y2, token: this.$root.token}, function(bck) {
		    		if(bck.code == 0) {
	            		this.$alert(bck.info, {
			         	 	confirmButtonText: '确定',
				          	callback: action => {
				          		if(bck.info == '您还没有登录！！') {
				          			location.reload();
				          		}
				          	}
				        });
	            	}
	            	else {
		    			this.analysisDataList = bck;
	            	}
		    	}.bind(this))
		    }
		},
		created() {
			apis.investigationList({token: this.$root.token}, function(data) {
				data ? this.loading = false : '';

				if(data.code == 0) {
					this.$alert(data.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	location.reload();
			          	}
			        });
				}
				else {
					this.investigationList = data.data;
					this.total = data.total;
					this.pageSize = data.per_page;
				}
			}.bind(this))
		}
	}
</script>

<style lang="less">
	.investigation {
		.page {
			text-align: center;
			margin-top: 15px;
		}
		.check-data {
			.title, table {
				margin-bottom: 30px;
			}
			table {
				font-size: 14px;
				width: 65%;
				text-align: left;
				border-collapse: collapse;
			    color: #555;
				thead {
					th:first-child {
						width: 70%;
					}
					th {
						border-bottom: 2px solid #ddd;
					}
				}
				tbody {
					tr {
						border-bottom: 1px solid #ddd;
					}
					th {
						font-weight: 100;
					}
					th:first-child {
						padding-left: 30px;
					}
				}
				th {
					padding: 8px;
				}
			}
		}
		.analysis-data {
			.title {
				margin-bottom: 30px;
				text-align: center;
			}
			.d-flex {
				display: flex;
				text-align: center;
			}
			.add-condition {
				font-size: 14px;
				cursor: pointer;
			    margin: auto;
			    border: 1px dashed #d4d7d9;
			    border-radius: 4px;
			    line-height: 38px;
			    color: #a9a8a8;
		        width: 215px;
			}
			.add-condition:hover {
				border-color: #66B1FF;
				color: #66B1FF;
			}
			.delete-condition {
				position: absolute;
    			margin-left: 15px;
			}
			.el-table::before {
			    display: none;
			}
		}
	}
</style>


