<!-- 模板管理 -->
<template>
	<div class="right-content theme">
		<el-tabs v-model="activeName" @tab-click="changeTab" class="content">
			<el-tab-pane label="模板管理" name="first">
				<el-table :data="themeList" style="width: 100%" :border='true' v-loading="loading">
					<el-table-column label="ID" prop="id" width="80"></el-table-column>
					<el-table-column label="模板名称" prop="name"></el-table-column>
					<el-table-column label="模板文件路径" prop="file"></el-table-column>
				 	<el-table-column label="操作" width="100">
	      				<template slot-scope="scope">
	        				<el-button type="text" size="small" @click="editTheme(scope.row)">编辑</el-button>
	    					<el-button type="text" size="small" @click="deleteTheme(scope.row)">删除</el-button>
	      				</template>
	    			</el-table-column>
				</el-table>
			</el-tab-pane>

			<el-tab-pane label="添加模板" name="second">
				<div class="add-theme">
					<span class="title">模板名称</span>
					<el-input v-model="addThemeName" placeholder="请输入名称"></el-input>
		    		<span class="required">*</span>
				</div>
				<div class="add-theme">
					<span class="title" style="vertical-align: top;">模板路径</span>
					<el-input v-model="addThemeFile" placeholder="请输入路径"></el-input>
		    		<span class="required">*</span>
				</div>
		    	<div class="add-theme">
		    		<span class="title" style="vertical-align: top;">模板描述</span>
		    		<el-input v-model="addThemeDescription" clearable type="textarea" :autosize="{ minRows: 4, maxRows: 4}"></el-input>
		    	</div>
				<div class="add-theme">
		    		<el-button type="primary" @click="addTheme()">添加</el-button>
		    		<el-button @click="goBack()">返回</el-button>
		    	</div>
			</el-tab-pane>
		</el-tabs>

		<!-- 编辑弹窗 -->
	  	<el-dialog title="编辑科室" :visible.sync="dialogThemeVisible" class="navDialog" width="40%">
  			<p class="edit-theme">
  				<span class="title">模板名称</span>
				<el-input v-model="editThemeName" placeholder="请输入名称"></el-input>
	    		<span class="required">*</span>
  			</p>
			<p class="edit-theme">
				<span class="title" style="vertical-align: top;">模板路径</span>
				<el-input v-model="editThemeFile" placeholder="请输入路径"></el-input>
	    		<span class="required">*</span>
			</p>
			<p class="edit-theme">
				<span class="title" style="vertical-align: top;">模板描述</span>
	    		<el-input v-model="editThemeDescription" clearable type="textarea" :autosize="{ minRows: 2}"></el-input>
			</p>
			<span slot="footer" class="dialog-footer">
		    	<el-button @click="dialogThemeVisible = false">取 消</el-button>
			    <el-button type="primary" @click="submitThemeEdit()">确 定</el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	import apis from '../../apis'

	export default {
		inject: ['reload', 'reloadTab'],
		data() {
			return {
				activeName: 'first',
				loading: true,
				dialogThemeVisible: false,
				themeList: [],
				addThemeName: '',
				addThemeFile: '',
				addThemeDescription: '',
				editThemeName: '',
				editThemeFile: '',
				editThemeDescription: '',
				themeId: 0,
			}
		},
		methods: {
			// 切换头部导航栏
			changeTab(tab) {
				if(tab.index != 2) {
					this.addThemeName = '';
					this.addThemeFile = '';
					this.addThemeDescription = '';
				}
			},
			// 添加
			addTheme() {
				if(this.addThemeName == '') {
					this.$alert('请输入名称');
					return;
				}
				else {
					apis.themeAdd({name: this.addThemeName, file: this.addThemeFile, description: this.addThemeDescription, token: this.$root.token}, function(bck) {
						this.$options.methods.alertMsg(this, bck.info, bck.code);
					}.bind(this))
				}
			},
			// 返回
			goBack() {
				this.activeName = 'first';
				this.addThemeName = '';
				this.addThemeFile = '';
				this.addThemeDescription = '';
			},
			// 删除
			deleteTheme(row) {
				apis.themeDelete({id: row.id, token: this.$root.token}, function(bck) {
					this.$options.methods.alertMsg(this, bck.info, bck.code);
				}.bind(this))
			},
			// 编辑
			editTheme(row) {
				this.dialogThemeVisible = true;
				this.editThemeName = row.name;
				this.editThemeFile = row.file;
				this.editThemeDescription = row.description;
				this.themeId = row.id;
			},
			// 提交编辑
			submitThemeEdit() {
				apis.themeEdit({id: this.themeId, name: this.editThemeName, file: this.editThemeFile, description: this.editThemeDescription, token: this.$root.token}, function(bck) {
					bck.info == '修改失败' ? bck.info = '修改成功' : '';
					this.$options.methods.alertMsg(this, bck.info, bck.code);
				}.bind(this))
			},
			alertMsg: function(_this, info, code) {
            	_this.$alert(info, {
	         	 	confirmButtonText: '确定',
		          	callback: action => {
		            	if(code == 1) {
		            		_this.reload();
		            	}
		            	else if(info == '您还没有登录！！') {
		            		location.reload();
		            	}
		          	}
		        });
            }
		},
		created() {
			apis.themeList({token: this.$root.token}, function(data) {
				data ? this.loading = false : '';
				if(data.code == 0) {
					this.$alert(data.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	location.reload();
			          	}
			        });
				}
				else {
					this.themeList = data;
				}
			}.bind(this))
		}
	}
</script>

<style lang="less">
	.theme {
		.add-theme, .edit-theme {
			font-size: 15px;
		    padding-left: 5%;
		    margin-top: 20px;
	     	.title {
		    	margin-right: 15px;
		    }
			.el-input, .el-textarea {
				width: 217px;
			}
		}
	}
</style>


