import Vue from 'vue'
import Router from 'vue-router'
import index from '@/views/index'

import tabs from '@/views/tabs/tabs'

import backstageMenu from '@/views/backstage-menu/backstage-menu'
import backstageMenuEditAndAdd from '@/views/backstage-menu/backstage-menu-EditAndAdd'

import siteInfo from '@/views/site-info/site-info'

import upload from '@/views/upload/upload'

import clearCache from '@/views/clear-cache/clear-cache'

import urlRoute from '@/views/url-route/url-route'

import webNav from '@/views/web-nav/web-nav'
import webNavEditAndAdd from '@/views/web-nav/web-nav-EditAndAdd'

import mailer from '@/views/mailer/mailer'

import slide from '@/views/slide/slide'

import link from '@/views/link/link'

import userManagement from '@/views/user-management/user-management'

import roleManagement from '@/views/role-management/role-management'

import administrators from '@/views/administrators/administrators'

import articleCategory from '@/views/article/article-category'
import article from '@/views/article/article'
import articleEditAndAdd from '@/views/article/article-EditAndAdd'

import department from '@/views/department/department'

import theme from '@/views/theme/theme'

import doctor from '@/views/doctor/doctor'
import doctorEditAndAdd from '@/views/doctor/doctor-EditAndAdd'

import recycle from '@/views/recycle/recycle'

import encyclopedia from '@/views/encyclopedia/encyclopedia'
import encyclopediaEditAndAdd from '@/views/encyclopedia/encyclopedia-EditAndAdd'
import encyclopediaCategory from '@/views/encyclopedia/encyclopedia-category'

import question from '@/views/question/question'
import questionCategory from '@/views/question/question-category'

import answer from '@/views/answer/answer'


import form from '@/views/form/form'
import formData from '@/views/form/form-data'
import formEditAndAdd from '@/views/form/form-EditAndAdd'

import investigation from '@/views/investigation/investigation'
import investigationEditAndAdd from '@/views/investigation/investigation-EditAndAdd'

import draw from '@/views/draw/draw'
import drawEditAndAdd from '@/views/draw/draw-EditAndAdd'

import topic from '@/views/topic/topic'

import announcement from '@/views/announcement/announcement'

import registerLittleProgram from '@/views/register-littleProgram/register-littleProgram'

import notFound from '@/views/not-found'

// 名字带有"edit"的组件 里面包含添加功能

Vue.use(Router)

export default new Router({
    routes: [
        // 首页
        {
            path: '/',
            name: 'index',
            component: index
        },
        // 左边导航栏
        {
        	path: 'tabs',
        	name: 'tabs',
        	component: tabs
        },
        // 后台菜单
        {
            path: '/backstage-menu',
            name: 'backstage-menu',
            component: backstageMenu
        },
        // 后台菜单添加修改
        {
            path: '',
            name: 'backstage-menu-EditAndAdd',
            component: backstageMenuEditAndAdd
        },
        // 网站信息
        {
            path: '/site-info',
            name: 'site-info',
            component: siteInfo
        },
        // 上传设置
        {
            path: '/upload',
            name: 'upload',
            component: upload
        },
        // 清理缓存
        {
            path: '/clear-cache',
            name: 'clear-cache',
            component: clearCache
        },
        // url美化
        {
            path: '/url-route',
            name: 'url-route',
            component: urlRoute
        },
        // 导航菜单
        {
            path: '/web-nav',
            name: 'web-nav',
            component: webNav
        },
        // 导航菜单添加修改
        {
            path: '',
            name: 'web-nav-EditAndAdd',
            component: webNavEditAndAdd
        },
        // 邮箱配置
        {
            path: '/mailer',
            name: 'mailer',
            component: mailer
        },
        // 幻灯片管理
        {
            path: '/slide',
            name: 'slide',
            component: slide
        },
        // 友情链接
        {
            path: '/link',
            name: 'link',
            component: link
        },
        // 本站用户管理
        {
            path: '/user-management',
            name: 'user-management',
            component: userManagement
        },
        // 角色管理
        {
            path: '/role-management',
            name: 'role-management',
            component: roleManagement
        },
        // 管理员
        {
            path: '/administrators',
            name: 'administrators',
            component: administrators
        },
        // 文章分类
        {
            path: '/article-category',
            name: 'article-category',
            component: articleCategory
        },
        // 文章分类
        {
            path: '/article',
            name: 'article',
            component: article
        },
        // 文章分类添加修改
        {
            path: '',
            name: 'article-EditAndAdd',
            component: articleEditAndAdd
        },
        // 科室管理
        {
            path: '/department',
            name: 'department',
            component: department
        },
        // 模板管理
        {
            path: '/theme',
            name: 'theme',
            component: theme
        },
        // 医生管理
        {
            path: '/doctor',
            name: 'doctor',
            component: doctor
        },
        // 医生编辑
        {
            path: '',
            name: 'doctor-EditAndAdd',
            component: doctorEditAndAdd
        },
        // 回收站
        {
            path: '/recycle',
            name: 'recycle',
            component: recycle
        },
        // 百科
        {
            path: '/encyclopedia',
            name: 'encyclopedia',
            component: encyclopedia
        },
        // 百科编辑
        {
            path: '',
            name: 'encyclopedia-EditAndAdd',
            component: encyclopediaEditAndAdd
        },
        // 百科分类
        {
            path: '/encyclopedia-category',
            name: 'encyclopedia-category',
            component: encyclopediaCategory
        },
        // 问答管理
        {
            path: '/question',
            name: 'question',
            component: question
        },
        // 医生解答
        {
            path: '/answer',
            name: 'answer',
            component: answer
        },
        // 问答分类
        {
            path: '/question-category',
            name: 'question-category',
            component: questionCategory
        },
        // 表单管理
        {
            path: '/form',
            name: 'form',
            component: form
        },
        // 数据管理
        {
            path: '/form-data',
            name: 'form-data',
            component: formData
        },
        // 数据编辑
        {
            path: '',
            name: 'form-EditAndAdd',
            component: formEditAndAdd
        },
        // 问卷调查
        {
            path: '/investigation',
            name: 'investigation',
            component: investigation
        },
        // 问卷调查编辑
        {
            path: '',
            name: 'investigation-EditAndAdd',
            component: investigationEditAndAdd
        },
        // 抽奖
        {
            path: '/draw',
            name: 'draw',
            component: draw
        },
        // 抽奖编辑
        {
            path: '',
            name: 'draw-EditAndAdd',
            component: drawEditAndAdd
        },
        // 专题列表
        {
            path: '/topic',
            name: 'topic',
            component: topic
        },
        // 后台公告
        {
            path: '/announcement',
            name: 'announcement',
            component: announcement
        },
        // 预约挂号-小程序
        {
            path: '/register-littleProgram',
            name: 'register-littleProgram',
            component: registerLittleProgram
        },
        //全不匹配的情况下，返回404，路由按顺序从上到下，依次匹配。
        {
            path: '*',
            component: notFound
        }
    ]
})



