function putApi(name, url, method) {
    let form = {};

    if (!method) {
        method = 'post';
    }

    apis[name] = (body, fun) => {

        if (method == 'post') {
            form = body
        } 
        else {
            form.params = body
        }
        vm.$http.post(url, body).then((back) => {
            if (fun) {
                (typeof fun === 'function') && fun.call(this, back.data);
            }
        }, (err) => {
            (typeof fun === 'function') && fun.call(this, err);
        })
    }
}

export default apis;



