<!-- 管理员 -->
<template>
	<div class="right-content administrators">
		<el-tabs v-model="activeName" class="content" @tab-click="changeTab">

			<el-tab-pane label="角色管理" name="first">
				<el-table :data="adminList" style="width: 100%" :border='true' v-loading="loading">
	    			<el-table-column label="ID" prop="id" width="80"></el-table-column>
	    			<el-table-column label="用户名" prop="user_login"></el-table-column>
				 	<el-table-column label="最后登录IP" prop="last_login_ip"></el-table-column>
				 	<el-table-column label="最后登录时间">
				 		<template slot-scope="scope">
				 			<div>{{adminList[scope.$index].last_login_time | formatDate}}</div>
				 		</template>
				 	</el-table-column>
	    			<el-table-column label="状态" width="80">
	    				<template slot-scope="scope">
	    					<div>{{adminList[scope.$index].user_status == 1 ? '正常' : '已拉黑'}}</div>
	    				</template>
	    			</el-table-column>
				 	<el-table-column label="操作" width="160">
	      				<template slot-scope="scope">
	      					<div v-if="adminList[scope.$index].id == 1">
				 				<el-button type="text" size="small" :disabled="true">拉黑</el-button>
		        				<el-button type="text" size="small" :disabled="true">修改</el-button>
		    					<el-button type="text" size="small" :disabled="true">删除</el-button>
				 			</div>
	      					<div v-else="">
	      						<el-button type="text" size="small" @click="changeStatus(scope.row)">
									{{adminList[scope.$index].user_status == 0 ? '启动' : '拉黑'}}
		      					</el-button>
		        				<el-button type="text" size="small" @click="openEdit(scope.row)">修改</el-button>
		    					<el-button type="text" size="small" @click="deleteAdmin(scope.row)">删除</el-button>
	      					</div>
	      				</template>
	    			</el-table-column>
	  			</el-table>
		    </el-tab-pane>

		    <el-tab-pane label="添加管理员" name="second">
				<div class="add-admin">
					<span class="title">用户名</span>
					<el-input v-model="addAdminName" placeholder="请输入管理员名称"></el-input>
		    		<span class="required">*</span>
				</div>
				<div class="add-admin">
					<span class="title">密码</span>
					<el-input v-model="addAdminPassword" type="password"></el-input>
		    		<span class="required">*</span>
				</div>
				<div class="add-admin">
					<span class="title">重复密码</span>
					<el-input v-model="addRepeatAdminPassword" type="password"></el-input>
		    		<span class="required">*</span>
				</div>
				<div class="add-admin">
		    		<span class="title">角色选择</span>
		    		<el-radio-group v-model="roleRadio">
					    <el-radio v-for="(item, index) in roleList" :key="index" :label="item.id" v-if="item.id != 1">{{item.name}}</el-radio>
				  	</el-radio-group>
		    	</div>
				<div class="add-admin">
		    		<el-button type="primary" @click="addAdmin()">添加</el-button>
		    		<el-button @click="goBack()">返回</el-button>
		    	</div>
		    </el-tab-pane>

		</el-tabs>

		<el-dialog title="提示" :visible.sync="adminEditVisible" width="30%">
  			<div class="add-admin" style="margin-top: 0;">
				<span class="title">用户名</span>
				<el-input v-model="editAdminName" placeholder="请输入管理员名称"></el-input>
	    		<span class="required">*</span>
			</div>
			<div class="add-admin">
	    		<span class="title">角色选择</span>
	    		<el-radio-group v-model="roleRadio">
				    <el-radio v-for="(item, index) in roleList" :key="index" :label="item.id" v-if="item.id != 1">{{item.name}}</el-radio>
			  	</el-radio-group>
	    	</div>
  			<span slot="footer" class="dialog-footer">
    			<el-button @click="adminEditVisible = false">取 消</el-button>
    			<el-button type="primary" @click="submitEdit()">确 定</el-button>
  			</span>
		</el-dialog>
	</div>
</template>

<script>
	import apis from '../../apis'

	export default {
		inject: ['reload', 'reloadTab'],
		data() {
			return {
				activeName: 'first',
				loading: true,
				adminEditVisible: false,
				adminList: [],
				roleList: [],
				roleRadio: '',
				addAdminName: '',
				addAdminPassword: '',
				addRepeatAdminPassword: '',
				editAdminName: '',
				adminId: 0,
			}
		},
		methods: {
			// 改变头部栏
			changeTab(tab, event) {
				if(tab.index == 1) {
					this.addAdminName = '';
					this.addAdminPassword = '';
					this.addRepeatAdminPassword = '';
					this.roleRadio = '';
				}
			},
			// 启动 & 禁用
			changeStatus(row) {
				row.user_status == 1 ? row.user_status = 0 : row.user_status = 1;
				apis.adminBan({id: row.id, open: row.user_status, token: this.$root.token}, function(bck) {
					this.$alert(bck.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	if(bck.info == '您还没有登录！！') {
			            		location.reload();
			            	}
			          	}
			        });
				}.bind(this))
			},
			// 添加管理员
			addAdmin() {
				apis.adminAdd({user_login: this.addAdminName, user_pass: this.addAdminPassword, re_pass: this.addRepeatAdminPassword, role_id: this.roleRadio, token: this.$root.token}, function(bck) {
					this.$alert(bck.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	if(bck.info == '您还没有登录！！') {
			            		location.reload();
			            	}
			            	else if(bck.code == 1) {
			            		this.reload();
			            	}
			          	}
			        });
				}.bind(this))
			},
			// 返回
			goBack() {
				this.addAdminName = '';
				this.addAdminPassword = '';
				this.addRepeatAdminPassword = '';
				this.roleRadio = '';
				this.activeName = 'first';
			},
			// 删除
			deleteAdmin(row) {
				apis.adminDelete({id: row.id, token: this.$root.token}, function(bck) {
					this.$alert(bck.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	if(bck.info == '您还没有登录！！') {
			            		location.reload();
			            	}
			            	else if(bck.code == 1) {
			            		this.reload();
			            	}
			          	}
			        });
				}.bind(this))
			},
			// 修改
			openEdit(row) {
				this.adminEditVisible = true;
				this.adminId = row.id;
				this.editAdminName = row.user_login;
				this.roleRadio = row.role_id;
			},
			// 提交修改
			submitEdit() {
				if(this.editAdminName == '') {
					this.$alert('用户名不能为空');
					return;
				}
				apis.adminEdit({id: this.adminId, user_login: this.editAdminName, role_id: this.roleRadio, token: this.$root.token}, function(bck) {
					this.$alert(bck.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	if(bck.info == '您还没有登录！！') {
			            		location.reload();
			            	}
			            	else if(bck.code == 1) {
			            		this.reload();
			            	}
			          	}
			        });
				}.bind(this))
			}
		},
		created() {
			// 获取角色id
			apis.roleList({token: this.$root.token}, function(data) {
				data ? this.loading = false : '';
				if(data.code == 0) {
					this.$alert(data.info, {
		         	 	confirmButtonText: '确定',
			          	callback: action => {
			            	location.reload();
			          	}
			        });
				}
				else {
					this.roleList = data.reverse();
				}
			}.bind(this))

			apis.adminList({token: this.$root.token}, function(data) {
				this.adminList = data;
			}.bind(this))
		},
		// 时间戳转日期
		filters: {
	      	formatDate: function (value) {
	        	if(value != 0) {
	        		let date = new Date(value * 1000);
		        	let y = date.getFullYear();
			        let MM = date.getMonth() + 1;
			        MM = MM < 10 ? ('0' + MM) : MM;
			        let d = date.getDate();
			        d = d < 10 ? ('0' + d) : d;
			        let h = date.getHours();
			        h = h < 10 ? ('0' + h) : h;
			        let m = date.getMinutes();
			        m = m < 10 ? ('0' + m) : m;
			        let s = date.getSeconds();
			        s = s < 10 ? ('0' + s) : s;
			        return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s;
	        	}
	      	}
	    }
	}
</script>

<style lang="less">
	.administrators {
		.add-admin {
		    font-size: 15px;
			padding-left: 5%;
			margin-top: 20px;
			.el-input {
				width: 217px;
			}
			.title {
				margin-right: 15px;
				width: 60px;
				text-align: right;
				display: inline-block;
			}
		}
	}
</style>


